import React, { useEffect, useRef } from 'react';

const BannerAd = () => {
  const ref = useRef();
  useEffect(() => {
    if (!ref.current) return;
    // Script de configuración
    const scriptConfig = document.createElement('script');
    scriptConfig.type = 'text/javascript';
    scriptConfig.innerHTML = `
      atOptions = {
        'key' : '164d5c4aed2d697097f27b54ab70618d',
        'format' : 'iframe',
        'height' : 50,
        'width' : 320,
        'params' : {}
      };
    `;
    // Script de invocación
    const scriptInvoke = document.createElement('script');
    scriptInvoke.type = 'text/javascript';
    scriptInvoke.src = '//felllilyforegoing.com/164d5c4aed2d697097f27b54ab70618d/invoke.js';

    ref.current.appendChild(scriptConfig);
    ref.current.appendChild(scriptInvoke);

    return () => {
      if (ref.current) ref.current.innerHTML = '';
    };
  }, []);
  return <div ref={ref} style={{ width: 320, minHeight: 50 }} />;
};

export default BannerAd;
