
import React, { useEffect, useState, useRef } from 'react';
// Avatar genérico para invitados
const GENERIC_AVATAR = 'https://ui-avatars.com/api/?name=Invitado&background=cccccc&color=555555&rounded=true';

// Genera nombre de invitado incremental
function getGuestName(index) {
  return `invitado ${index + 1}`;
}

const OnlineUsers = ({ API_URL }) => {
  const [users, setUsers] = useState([]);
  // Eliminado showAll/setShowAll porque no se usa
  const [showModal, setShowModal] = useState(false);
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const intervalRef = useRef(null);

  // Obtener usuario logueado una vez (solo para mostrar "Tú" si corresponde)
  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetch(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` },
      })
        .then(res => res.json())
        .then(data => {
          setCurrentUser(data.user || null);
        })
        .catch(() => setCurrentUser(null));
    } else {
      setCurrentUser(null);
    }
  }, [API_URL]);

  // Genera o recupera un guestId único para invitados (localStorage)
  function getOrCreateGuestId() {
    let guestId = localStorage.getItem('guestId');
    if (!guestId) {
      // Genera un guestId simple (uuid-like)
      guestId = 'guest-' + Math.random().toString(36).slice(2) + Date.now().toString(36);
      localStorage.setItem('guestId', guestId);
    }
    return guestId;
  }

  // Cargar usuarios online (enviando JWT o guestId)
  useEffect(() => {
    const fetchOnline = async () => {
      try {
        const jwt = localStorage.getItem('jwt');
        let headers = {};
        if (jwt) {
          headers['Authorization'] = `Bearer ${jwt}`;
        } else {
          headers['x-guest-id'] = getOrCreateGuestId();
        }
        const res = await fetch(`${API_URL}/users/online`, { headers });
        if (!res.ok) {
          setUsers([]);
          setLoading(false);
          return;
        }
        const data = await res.json();
        let online = data.users || [];
        setUsers(online);
      } catch {
        setUsers([]);
      } finally {
        setLoading(false);
      }
    };

    fetchOnline(); // Primer llamada
    intervalRef.current = setInterval(fetchOnline, 5000); // Actualiza cada 5s

    return () => clearInterval(intervalRef.current);
  }, [API_URL]);

  // Notificar al backend que el usuario está activo al entrar (soporta invitados)
  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    const guestId = !jwt ? getOrCreateGuestId() : null;
    // Ping cada 5s para mantener al usuario en línea
    const ping = () => {
      let headers = {};
      if (jwt) {
        headers['Authorization'] = `Bearer ${jwt}`;
      } else if (guestId) {
        headers['x-guest-id'] = guestId;
      }
      if (jwt || guestId) {
        fetch(`${API_URL}/users/ping`, {
          method: 'POST',
          headers,
          credentials: 'include'
        }).catch(() => {});
      }
    };
    ping();
    const pingInterval = setInterval(ping, 5000);

    // Notificar al backend que el usuario sale al cerrar la pestaña o cambiar de visibilidad
    const notifyOffline = () => {
      let headers = {};
      if (jwt) {
        headers['Authorization'] = `Bearer ${jwt}`;
      } else if (guestId) {
        headers['x-guest-id'] = guestId;
      }
      if (jwt || guestId) {
        if (navigator.sendBeacon && !jwt) {
          // sendBeacon solo soporta POST con body, así que solo para invitados
          const blob = new Blob([], { type: 'text/plain' });
          navigator.sendBeacon(`${API_URL}/users/offline`, blob);
        } else {
          fetch(`${API_URL}/users/offline`, {
            method: 'POST',
            headers,
            credentials: 'include'
          }).catch(() => {});
        }
      }
    };

    window.addEventListener('beforeunload', notifyOffline);
    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === 'hidden') notifyOffline();
      if (document.visibilityState === 'visible') ping();
    });

    return () => {
      clearInterval(pingInterval);
      window.removeEventListener('beforeunload', notifyOffline);
      document.removeEventListener('visibilitychange', notifyOffline);
    };
  }, [API_URL]);

  const limit = 3;
  // Separa usuarios registrados e invitados
  const registered = users.filter(u => u.name && u.photo);
  const guests = users.filter(u => !u.name || !u.photo).map((u, idx) => ({ ...u, guestIndex: idx }));
  // Junta ambos, primero registrados, luego invitados
  const allUsers = [...registered, ...guests];
  const displayedUsers = allUsers.slice(0, limit);

  return (
    <div className="bg-gray-100 rounded-lg p-3 shadow flex flex-col items-start max-w-md">
      <div className="font-semibold text-gray-700 mb-2">Usuarios en línea:</div>
      {loading ? (
        <div className="flex items-center gap-2 py-2">
          <div className="animate-spin rounded-full h-6 w-6 border-t-2 border-b-2 border-blue-500"></div>
          <span className="text-blue-500 font-medium">Cargando usuarios...</span>
        </div>
      ) : users.length === 0 ? (
        <span className="text-gray-400 text-sm">Nadie en línea</span>
      ) : (
        <>
          <div className="flex flex-wrap gap-3">
            {displayedUsers.map((u, idx) => {
              const isGuest = !u.name || !u.photo;
              const guestNumber = isGuest ? (u.guestIndex != null ? u.guestIndex : idx) : null;
              const isCurrent = !isGuest && currentUser && String(u.id) === String(currentUser.id);
              return (
                <div
                  key={u.id || `guest-${guestNumber}`}
                  className={`flex items-center gap-2 bg-white px-2 py-1 rounded shadow ${isCurrent ? 'border-2 border-blue-500' : ''}`}
                  style={isCurrent ? { background: '#e0f2fe' } : {}}
                >
                  <img src={isGuest ? GENERIC_AVATAR : u.photo} alt={isGuest ? 'Invitado' : u.name} className="w-7 h-7 rounded-full border" />
                  <span className={`text-sm font-medium ${isCurrent ? 'text-blue-700' : 'text-gray-700'}`}>
                    {isGuest ? getGuestName(guestNumber) : u.name}
                    {isCurrent && <span className="ml-1 text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded">Tú</span>}
                  </span>
                </div>
              );
            })}
          </div>
          {users.length > limit && (
            <button
              className="mt-2 text-blue-600 hover:underline text-sm"
              onClick={() => setShowModal(true)}
              aria-label="Ver más usuarios en línea"
            >
              Ver más usuarios ({users.length - limit} más)
            </button>
          )}
          {/* Modal simple para mostrar todos los usuarios */}
          {showModal && (
            <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50">
              <div className="bg-white rounded-xl shadow-lg p-6 max-w-md w-full relative">
                <button
                  className="absolute top-2 right-2 text-gray-500 hover:text-gray-800 text-lg font-bold"
                  onClick={() => setShowModal(false)}
                  aria-label="Cerrar"
                >
                  ×
                </button>
                <div className="font-semibold text-gray-700 mb-4">Todos los usuarios en línea:</div>
                <div className="flex flex-wrap gap-3">
                  {allUsers.map((u, idx) => {
                    const isGuest = !u.name || !u.photo;
                    const guestNumber = isGuest ? (u.guestIndex != null ? u.guestIndex : idx) : null;
                    const isCurrent = !isGuest && currentUser && String(u.id) === String(currentUser.id);
                    return (
                      <div
                        key={u.id || `guest-${guestNumber}`}
                        className={`flex items-center gap-2 bg-gray-50 px-2 py-1 rounded shadow ${isCurrent ? 'border-2 border-blue-500' : ''}`}
                        style={isCurrent ? { background: '#e0f2fe' } : {}}
                      >
                        <img src={isGuest ? GENERIC_AVATAR : u.photo} alt={isGuest ? 'Invitado' : u.name} className="w-7 h-7 rounded-full border" />
                        <span className={`text-sm font-medium ${isCurrent ? 'text-blue-700' : 'text-gray-700'}`}>
                          {isGuest ? getGuestName(guestNumber) : u.name}
                          {isCurrent && <span className="ml-1 text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded">Tú</span>}
                        </span>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default OnlineUsers;

