import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
// Agrega import para el botón de PayPal
import PayPalButton from './PayPalButton';
import { motion } from 'framer-motion';

const FileCard = ({
  file,
  currentUser,
  onDelete,
  onSessionChange,
  onLikeChange,
  onEdit,
  disableModal = false,
  hasPurchased: propHasPurchased, // Nuevo prop
  ...props
}) => {
  // Usa publicUrl para el botón Ver (GitHub Pages)
  const publicUrl = file.publicUrl || file.file_url || '';
  // Ruta de descarga (Supabase o backend)
  const fileData = file.file_data || '';
  // Cambia la lógica de downloadUrl para usar la ruta RESTful dinámica del backend
  let downloadUrl = '';
  if (typeof fileData === 'string' && fileData.includes('/')) {
    // fileData es tipo "2025/07/uuid.html"
    const [year, month, ...rest] = fileData.split('/');
    const filename = rest.join('/');
    downloadUrl = `${process.env.REACT_APP_API_URL}/files/download/${encodeURIComponent(year)}/${encodeURIComponent(month)}/${encodeURIComponent(filename)}`;
  } else {
    downloadUrl = '';
  }

  const filename = typeof file.filename === 'string' ? file.filename : '';
  const [copied, setCopied] = useState(false);
  const [modalOpen, setModalOpen] = useState(false);
  const [likes, setLikes] = useState(Number(file.likes) || 0);
  const [downloads, setDownloads] = useState(Number(file.downloads) || 0);
  const [liked, setLiked] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);

  // Cambia: solo usa JWT para habilitar el botón de like
  const hasJwt = Boolean(localStorage.getItem('jwt'));

  const [showAd, setShowAd] = useState(false);
  const adTimeoutRef = useRef();

  // Nuevo: usa modalidad para acceso VIP
  const isVip = String(file.epago).toLowerCase() === "vip";
  // Corrige la detección de usuario VIP
  const isFreeVipUser = currentUser && String(currentUser.modalidad).toLowerCase() === 'vip';
  const fileOwnerEmail = file.user_email || file.owner_email || file.email || ""; // asegúrate de que el backend devuelva el email del dueño
  const [showVipModal, setShowVipModal] = useState(false);

  // Nuevo: estado para saber si el usuario ya compró este archivo
  const [hasPurchased, setHasPurchased] = useState(!!propHasPurchased);

  // Consulta al backend si el usuario ya compró el archivo VIP
  useEffect(() => {
    let isMounted = true;
    const checkPurchased = async () => {
      if (!isVip || !currentUser || !file.id) return;
      const jwt = localStorage.getItem('jwt');
      if (!jwt) return;
      try {
        const res = await fetch(`${process.env.REACT_APP_API_URL}/files/${file.id}/purchased`, {
          headers: { Authorization: `Bearer ${jwt}` }
        });
        const data = await res.json();
        if (isMounted && typeof data.purchased === 'boolean') {
          setHasPurchased(data.purchased);
        }
      } catch {}
    };
    checkPurchased();
    return () => { isMounted = false; };
  }, [file.id, isVip, currentUser]);

  // Estado para mostrar el modal de compra exitosa
  const [showSuccessModal, setShowSuccessModal] = useState(false);

  // Estado para el modal de anuncio
  const [adModalOpen, setAdModalOpen] = useState(false);
  const [adLoaded, setAdLoaded] = useState(false);
  const [adContinueEnabled, setAdContinueEnabled] = useState(false);
  const [adAction, setAdAction] = useState(null);
  const adBannerRef = useRef(null);
  const [adCountdown, setAdCountdown] = useState(5);

  // Solo mostrar el modal de publicidad para archivos gratuitos y usuarios no VIP
  const shouldShowAdModal = !isVip && (!currentUser || String(currentUser.modalidad).toLowerCase() !== 'vip');

  // ...existing code...
  // El modal de publicidad ahora solo muestra el mensaje de TikTok

  // Solo habilita el botón "Continuar" si el usuario hizo clic en el anuncio (iframe)
  useEffect(() => {
    if (!adModalOpen || !adLoaded || !adBannerRef.current) return;
    const bannerEl = adBannerRef.current;
    const handleClick = (e) => {
      const iframe = bannerEl.querySelector('iframe');
      // Solo habilita si el click fue sobre el iframe
      if (iframe && e.target === iframe) {
        setAdContinueEnabled(true);
      }
    };
    bannerEl.addEventListener('click', handleClick);

    return () => {
      bannerEl.removeEventListener('click', handleClick);
    };
  }, [adModalOpen, adLoaded]);

  // Detecta blur/focus para habilitar el botón tras volver del anuncio
  useEffect(() => {
    const handleBlur = () => {
      // El usuario salió de la ventana (posible clic en anuncio)
      // Puedes poner un log si quieres: console.log("Ventana perdió foco → probablemente clic en anuncio");
    };
    const handleFocus = () => {
      if (adModalOpen && adLoaded) {
        setAdContinueEnabled(true);
      }
    };
    window.addEventListener("blur", handleBlur);
    window.addEventListener("focus", handleFocus);

    return () => {
      window.removeEventListener("blur", handleBlur);
      window.removeEventListener("focus", handleFocus);
    };
  }, [adModalOpen, adLoaded]);

  // Función para mostrar el modal de anuncio antes de la acción
  const showAdModal = (action) => {
    setAdAction(action);
    setAdModalOpen(true);
    setAdContinueEnabled(false);
  };

  // Ejecutar la acción real después del anuncio
  const handleAdContinue = () => {
    setAdModalOpen(false);
    if (adAction === 'view') {
      if (publicUrl) window.open(publicUrl, '_blank');
    } else if (adAction === 'copy') {
      if (publicUrl) {
        navigator.clipboard.writeText(publicUrl)
          .then(() => {
            setCopied(true);
            setTimeout(() => setCopied(false), 1200);
          });
      }
    } else if (adAction === 'download') {
      if (downloadUrl) window.open(downloadUrl, '_blank');
    }
    setAdAction(null);
  };

  // Descargar usando fetch y blob para forzar descarga real y enviar JWT
  const handleDownload = (e) => {
    if (e && typeof e.preventDefault === 'function') e.preventDefault();
    if (e && typeof e.stopPropagation === 'function') e.stopPropagation();

    if (isVip) {
      if (!isFreeVipUser) {
        setShowVipPayModal(true);
        return;
      }
      if (!downloadUrl || downloadUrl === '#') {
        alert('No hay archivo para descargar.');
        return;
      }
      setDownloads(d => d + 1);
      window.open(downloadUrl, '_blank');
    } else {
      // Gratuito: muestra el modal de publicidad si el usuario no es VIP
      if (shouldShowAdModal) {
        showAdModal('download');
        return;
      }
      setDownloads(d => d + 1);
      window.open(downloadUrl, '_blank');
    }
  };

  // Abrir GitHub Pages en nueva pestaña
  const handleView = (e) => {
    e.stopPropagation();
    if (isVip) {
      if (!isFreeVipUser) {
        setShowVipPayModal(true);
        return;
      }
      if (publicUrl) window.open(publicUrl, '_blank');
    } else {
      if (shouldShowAdModal) {
        showAdModal('view');
        return;
      }
      if (publicUrl) window.open(publicUrl, '_blank');
    }
  };

  // Copiar enlace
  const handleCopyLink = (e, url) => {
    e.stopPropagation();
    if (isVip) {
      if (!isFreeVipUser) {
        setShowVipPayModal(true);
        return;
      }
      if (url) {
        navigator.clipboard.writeText(url)
          .then(() => {
            setCopied(true);
            setTimeout(() => setCopied(false), 1200);
          });
      }
    } else {
      if (shouldShowAdModal) {
        showAdModal('copy');
        return;
      }
      if (url) {
        navigator.clipboard.writeText(url)
          .then(() => {
            setCopied(true);
            setTimeout(() => setCopied(false), 1200);
          });
      }
    }
  };

  // Like robusto y optimista, no cierra modal ni hace refetch global
  const handleLike = async (e) => {
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }
    const jwt = localStorage.getItem('jwt');
    if (!jwt) {
      alert('Inicia sesión para dar like.');
      return;
    }
    const togglingTo = !liked;
    // Actualización optimista inmediata
    setLiked(togglingTo);
    setLikes(prev => prev + (togglingTo ? 1 : -1));
    try {
      const url = `${process.env.REACT_APP_API_URL}/files/${file.id}/${liked ? 'unlike' : 'like'}`;
      const res = await fetch(url, {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${jwt}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({})
      });
      if (!res.ok) {
        // revertir si falló
        setLiked(!togglingTo);
        setLikes(prev => prev + (togglingTo ? -1 : 1));
        alert(`Error al ${liked ? 'quitar' : 'dar'} like: ${res.status}`);
        return;
      }
      const data = await res.json().catch(() => ({}));
      // Si el backend devuelve el total de likes, usa ese número para asegurar consistencia
      if (data && data.likes != null && !Number.isNaN(Number(data.likes))) {
        setLikes(Number(data.likes));
      }
      // No hagas refetch global aquí
      // onLikeChange?.(file.id, { liked: togglingTo });
    } catch (err) {
      setLiked(!togglingTo);
      setLikes(prev => prev + (togglingTo ? -1 : 1));
      console.error('like POST error:', err);
    }
  };

  // Eliminar archivo correctamente y evitar doble clic rápido
  const handleDeleteClick = async (e) => {
    e.stopPropagation();
    if (isDeleting) return; // Bloquea doble clic
    setIsDeleting(true);
    try {
      const jwt = localStorage.getItem('jwt');
      const res = await fetch(`${process.env.REACT_APP_API_URL}/files/delete/${file.id}`, {
        method: 'DELETE',
        headers: jwt ? { Authorization: `Bearer ${jwt}` } : {},
        credentials: 'include'
      });
      if (res.status === 401) {
        // JWT expirado o inválido
        localStorage.removeItem('jwt');
        alert('Tu sesión ha expirado. Por favor, inicia sesión de nuevo.');
        window.location.reload();
        return;
      }
      if (res.ok) {
        if (onDelete) onDelete(file);
        if (onSessionChange) onSessionChange();
      } else {
        alert('No se pudo eliminar el archivo.');
      }
    } catch {
      alert('Error al eliminar el archivo.');
    } finally {
      setIsDeleting(false);
    }
  };

  // Modal de compra exitosa con animación
  const SuccessModal = () => (
    <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white rounded-2xl shadow-2xl border p-8 w-full max-w-sm mx-2 relative flex flex-col items-center animate__animated animate__fadeIn">
        {/* Animación de visto */}
        <div className="mb-4 flex items-center justify-center">
          <svg className="w-20 h-20 text-green-500 animate-bounce" viewBox="0 0 64 64" fill="none">
            <circle cx="32" cy="32" r="30" fill="#d1fae5" />
            <path d="M18 34l10 10 18-18" stroke="#10b981" strokeWidth="5" fill="none" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>
        <h2 className="text-2xl font-bold text-green-600 mb-2 text-center animate__animated animate__fadeInDown">¡Compra exitosa!</h2>
        <p className="text-gray-700 text-center mb-4 text-lg animate__animated animate__fadeInUp">
          Tu archivo VIP ya está disponible para descargar y ver siempre que lo desees.<br />
          <span className="font-semibold text-green-700">Gracias por tu compra en SysteCode.</span>
        </p>
        <button
          className="mt-4 px-6 py-2 rounded bg-green-600 text-white font-semibold hover:bg-green-700 transition"
          onClick={() => setShowSuccessModal(false)}
        >
          Cerrar
        </button>
      </div>
    </div>
  );

  // Modal de pago VIP
  const VIP_EMAILS = [
    "pastranagonzalezbrayanstiven@gmail.com",
    "baqueperez89@gmail.com"
  ];

  const VipModal = () => (
    <div className="fixed inset-0 z-[999] flex items-center justify-center bg-black bg-opacity-60" onClick={() => setShowVipModal(false)}>
      <div
        className="bg-white rounded-2xl shadow-2xl border p-8 w-full max-w-lg mx-2 relative flex flex-col items-center"
        onClick={e => e.stopPropagation()}
        style={{
          maxHeight: '90vh',
          overflowY: 'auto',
          scrollbarWidth: 'thin',
          scrollbarColor: '#fbbf24 #fef3c7',
          touchAction: 'pan-y',
        }}
        tabIndex={0}
      >
        <button
          className="absolute top-3 right-3 text-gray-400 hover:text-yellow-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
          onClick={() => setShowVipModal(false)}
          aria-label="Cerrar"
          type="button"
        >
          ×
        </button>
        <h2 className="text-3xl font-bold text-yellow-500 mb-2 text-center">Archivo VIP</h2>
        <p className="text-gray-700 text-center mb-4 text-lg">
          Este archivo es <span className="font-bold text-yellow-600">VIP</span>.<br />
          Para acceder, realiza el pago seguro con tarjeta o PayPal.<br />
          <span className="text-yellow-700 font-bold text-lg">Precio: $2 USD</span>
        </p>
        {/* Botón de PayPal */}
        <div className="w-full flex justify-center my-4">
          <PayPalButton
            amount={2.00}
            description={file.filename || 'Archivo HTML VIP'}
            receiverEmail={
              VIP_EMAILS.includes(fileOwnerEmail)
                ? "baqueperez89@gmail.com"
                : fileOwnerEmail
            }
            fileId={file.id}
            userId={currentUser?.id}
            clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
            onSuccess={() => {
              setShowVipModal(false);
              setHasPurchased(true);
              setShowSuccessModal(true);
              fetch(`${process.env.REACT_APP_API_URL}/files/${file.id}/purchase`, {
                method: 'POST',
                headers: { Authorization: `Bearer ${localStorage.getItem('jwt')}` }
              }).catch(() => {});
            }}
          />
        </div>
        <div className="mt-4 text-xs text-gray-400 text-center">
          Pago seguro con todas las tarjetas y PayPal.<br />
          Recibirás acceso inmediato tras el pago.
        </div>
      </div>
    </div>
  );

  // Nuevo: estado para mostrar el modal de pago individual VIP
  const [showVipPayModal, setShowVipPayModal] = useState(false);

  // Modal de pago individual VIP
  const VipPayModal = () => (
    <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-black bg-opacity-60">
      <div className="bg-white rounded-2xl shadow-2xl border p-8 max-w-md w-full relative flex flex-col items-center">
        <button
          className="absolute top-3 right-3 text-gray-400 hover:text-yellow-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
          onClick={() => setShowVipPayModal(false)}
          aria-label="Cerrar"
          type="button"
        >×</button>
        <h2 className="text-3xl font-bold text-yellow-500 mb-2 text-center">Pago para acceder a archivo VIP</h2>
        <div className="w-full flex flex-col items-center my-4">
          <PayPalButton
            amount={2.00}
            description={file.filename || 'Archivo HTML VIP'}
            fileId={file.id}
            userId={currentUser?.id}
            clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
            onSuccess={() => {
              setShowVipPayModal(false);
              setHasPurchased(true);
              setShowSuccessModal(true);
            }}
          />
        </div>
        <div className="mt-4 text-xs text-gray-500 text-center">
          Paga $2 USD para acceder a este archivo VIP.<br />
          Si eres usuario VIP mensual, tendrás acceso ilimitado a todos los archivos VIP.<br />
          <span className="block mt-2 text-yellow-700 font-semibold">
            <strong>Importante:</strong> El acceso es solo para este archivo.
          </span>
        </div>
      </div>
    </div>
  );

  // Añade la definición de previewImageUrl (faltaba en tu código)
  const previewImageUrl = file.previewImageUrl || file.preview_image_url || file.image_url || file.image || null;
  // Prioriza la propiedad que realmente tiene la URL del video
  const previewVideoUrl = file.previewVideoUrl || file.preview_video_url || file.video_url || file.video || null;

  // Añade la definición de isOwner (faltaba en tu código)
  const isOwner = currentUser && file.user_id && String(file.user_id) === String(currentUser?.id);

  // Elimina variables no usadas para limpiar warnings
  // const [adReady, setAdReady] = useState(false);
  // const [adCallback, setAdCallback] = useState(null);
  // const showAdAndThen = ...;
  // const handleCloseAd = ...;

  // Añade la definición de EditModal con campos reales de edición
  const [editDescripcion, setEditDescripcion] = useState(file.descripcion || '');
  const [editTipo, setEditTipo] = useState(file.tipo || '');
  const [editCategoria, setEditCategoria] = useState(file.categoria || '');
  const [editImage, setEditImage] = useState(null);
  const [editImagePreview, setEditImagePreview] = useState(null);
  const [editVideo, setEditVideo] = useState(null);
  const [editVideoPreview, setEditVideoPreview] = useState(null);
  const [editSaving, setEditSaving] = useState(false);
  const [editError, setEditError] = useState('');

  // Si quieres que el modal de edición muestre también el nombre del archivo y permita editarlo:
  const [editFilename, setEditFilename] = useState(file.filename || '');

  // Estado para VIP/gratuito editable (solo uno activo)
  const [editVip, setEditVip] = useState(isVip);

  useEffect(() => {
    setEditVip(String(file.epago).toLowerCase() === "vip");
  }, [file.epago]);

  // Maneja cambio de imagen/video según VIP
  const handleEditMediaChange = (e) => {
    const media = e.target.files[0];
    if (!media) {
      setEditImage(null); setEditImagePreview(null);
      setEditVideo(null); setEditVideoPreview(null);
      setEditError('');
      return;
    }
    if (editVip && media.type.startsWith('video/')) {
      setEditVideo(media);
      setEditVideoPreview(URL.createObjectURL(media));
      setEditImage(null); setEditImagePreview(null);
      setEditError('');
    } else if (!editVip && media.type.startsWith('image/')) {
      setEditImage(media);
      setEditImagePreview(URL.createObjectURL(media));
      setEditVideo(null); setEditVideoPreview(null);
      setEditError('');
    } else {
      setEditImage(null); setEditImagePreview(null);
      setEditVideo(null); setEditVideoPreview(null);
      setEditError(editVip ? 'Solo se permiten videos para archivos VIP.' : 'Solo se permiten imágenes para archivos gratuitos.');
    }
  };

  // Actualiza el handleEditSave para enviar imagen o video según VIP
  const handleEditSave = async (e) => {
    e.preventDefault();
    setEditSaving(true);
    setEditError('');
    try {
      const jwt = localStorage.getItem('jwt');
      const formData = new FormData();
      formData.append('filename', editFilename);
      formData.append('descripcion', editDescripcion);
      formData.append('tipo', editTipo);
      formData.append('categoria', editCategoria);
      // Solo admins pueden cambiar VIP/gratuito
      if (currentUser && String(currentUser.rol).toLowerCase() === 'admin') {
        formData.append('epago', editVip ? 'vip' : 'gratuito');
      }
      // Solo envía imagen si es gratuito, video si es VIP
      if (editVip && editVideo) formData.append('video', editVideo);
      if (!editVip && editImage) formData.append('image', editImage);

      const res = await fetch(`${process.env.REACT_APP_API_URL}/files/edit/${file.id}`, {
        method: 'POST',
        headers: jwt ? { Authorization: `Bearer ${jwt}` } : {},
        body: formData,
      });
      const data = await res.json();
      if (!data.success) {
        setEditError(data.message || 'Error al editar');
      } else {
        setEditModalOpen(false);
        // Pasa el archivo actualizado al padre para que lo reemplace en la lista local
        if (onEdit) onEdit(data.file || { ...file, epago: editVip ? 'vip' : 'gratuito' });
        if (onLikeChange) onLikeChange();
      }
    } catch {
      setEditError('Error de red');
    }
    setEditSaving(false);
  };

  // EditModal con input condicional para imagen/video
  const EditModal = () => (
    <div className="fixed inset-0 z-[999] flex items-center justify-center bg-black bg-opacity-40" onClick={() => setEditModalOpen(false)}>
      <form
        className="bg-white rounded-2xl shadow-2xl border p-6 w-full max-w-md mx-2 relative flex flex-col"
        onClick={e => e.stopPropagation()}
        onSubmit={handleEditSave}
      >
        <button
          className="absolute top-3 right-3 text-gray-400 hover:text-purple-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
          onClick={() => setEditModalOpen(false)}
          aria-label="Cerrar"
          type="button"
        >×</button>
        <h2 className="font-bold text-lg mb-2">Editar archivo</h2>
        <label className="text-sm font-semibold mt-2">Nombre del archivo</label>
        <input
          className="border rounded px-2 py-1 w-full mb-2"
          value={editFilename}
          onChange={e => setEditFilename(e.target.value)}
        />
        <label className="text-sm font-semibold mt-2">Descripción</label>
        <textarea
          className="border rounded px-2 py-1 w-full mb-2"
          value={editDescripcion}
          onChange={e => setEditDescripcion(e.target.value)}
          rows={2}
        />
        <label className="text-sm font-semibold">Tipo</label>
        <input
          className="border rounded px-2 py-1 w-full mb-2"
          value={editTipo}
          onChange={e => setEditTipo(e.target.value)}
        />
        <label className="text-sm font-semibold">Categoría</label>
        <input
          className="border rounded px-2 py-1 w-full mb-2"
          value={editCategoria}
          onChange={e => setEditCategoria(e.target.value)}
        />
        {/* Solo admins pueden cambiar VIP/gratuito */}
        {currentUser && String(currentUser.rol).toLowerCase() === 'admin' && (
          <div className="flex items-center gap-6 mb-2 mt-2">
            <label className="flex items-center gap-2">
              <input
                type="checkbox"
                checked={!editVip}
                onChange={() => setEditVip(false)}
                className="form-checkbox h-5 w-5 text-green-500"
              />
              <span className="text-xs font-bold text-green-600">Gratis</span>
            </label>
            <label className="flex items-center gap-2">
              <input
                type="checkbox"
                checked={editVip}
                onChange={() => setEditVip(true)}
                className="form-checkbox h-5 w-5 text-yellow-500"
              />
              <span className="text-xs font-bold text-yellow-600">VIP</span>
            </label>
          </div>
        )}
        <label className="text-sm font-semibold">
          {editVip ? "Video de vista previa (solo VIP)" : "Imagen de vista previa"}
        </label>
        <input
          type="file"
          accept={editVip ? "video/*" : "image/*"}
          className="mb-2"
          onChange={handleEditMediaChange}
        />
        {editVip && editVideoPreview && (
          <video src={editVideoPreview} controls className="mb-2 rounded max-h-32 w-full" />
        )}
        {!editVip && editImagePreview && (
          <img src={editImagePreview} alt="Vista previa" className="mb-2 rounded max-h-32" />
        )}
        {editError && <div className="text-red-500 text-sm mb-2">{editError}</div>}
        <button
          type="submit"
          disabled={editSaving}
          className="bg-blue-600 text-white px-4 py-2 rounded font-semibold"
        >
          {editSaving ? 'Guardando...' : 'Guardar cambios'}
        </button>
      </form>
    </div>
  );

  // Cambia el borde del FileCard según el estado actual (VIP o Gratis)
  // Usa el valor actualizado de file.epago para mostrar el estado correcto
  const isVipActual = String(file.epago).toLowerCase() === "vip";

  // Navegar a la página de detalles del archivo
  const navigate = useNavigate();
  
  const handleCardClick = () => {
    if (!disableModal) {
      navigate(`/archivos/${encodeURIComponent(filename)}`);
    }
  };

  return (
    <>
      <motion.div
        initial={{ opacity: 0, scale: 0.96, y: 20 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        exit={{ opacity: 0, scale: 0.96, y: 20 }}
        transition={{ duration: 0.35, type: 'spring', stiffness: 120 }}
        className={`w-full max-w-md bg-gradient-to-br from-white via-gray-50 to-gray-100 rounded-2xl shadow-lg hover:shadow-2xl transition-all duration-200 overflow-hidden relative cursor-pointer border-[2px] ${
          isVipActual ? 'border-yellow-400' : 'border-green-400'
        }`}
        onClick={handleCardClick}
        tabIndex={0}
        {...props}
        style={{
          margin: '0 auto',
          marginBottom: 28,
          boxShadow: '0 4px 16px rgba(60,60,60,0.09)',
        }}
      >
        {/* Elimina la etiqueta VIP del lado izquierdo */}
        {/* Etiqueta comprado si aplica */}
        {isVipActual && hasPurchased && (
          <motion.span
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.3 }}
            className="absolute top-3 left-3 bg-green-500 text-white px-2 py-1 rounded text-xs font-bold shadow animate-pulse z-20"
            style={{ pointerEvents: 'none' }}
          >
            Comprado
          </motion.span>
        )}
        {/* Header */}
        <div className="px-6 pt-6 pb-4 border-b border-gray-100 flex justify-between items-start gap-3 bg-gradient-to-r from-gray-50 via-white to-gray-100 relative">
          <div className="flex-1 min-w-0">
            <h2 className="text-lg font-bold text-gray-900 mb-1 truncate tracking-tight" title={filename}>
              {filename || 'Archivo sin nombre'}
            </h2>
            <p
              className="text-gray-500 text-sm leading-relaxed break-words max-w-full overflow-hidden"
              style={{
                wordBreak: 'break-word',
                maxHeight: '4.5em',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                display: '-webkit-box',
                WebkitLineClamp: 3,
                WebkitBoxOrient: 'vertical'
              }}
            >
              {file.descripcion?.trim() ? file.descripcion : <span className="italic text-gray-400">No tiene descripción</span>}
            </p>
          </div>
          <motion.div
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.3 }}
            className={`px-2 py-0.5 rounded text-xs font-bold border shadow-sm ${
              isVipActual
                ? 'bg-yellow-100 text-yellow-700 border-yellow-300'
                : 'bg-green-100 text-green-700 border-green-300'
            }`}
          >
            {isVipActual ? 'VIP' : 'GRATUITO'}
          </motion.div>
        </div>

        {/* Imagen */}
        <div className="px-6 pt-4 pb-2">
          <div className="relative rounded-xl overflow-hidden border border-gray-100 mb-4 bg-gray-50 shadow-sm flex items-center justify-center" style={{ minHeight: 160 }}>
            {isVipActual && previewVideoUrl ? (
              <motion.video
                src={previewVideoUrl}
                controls
                autoPlay
                loop
                muted
                className="w-full h-40 object-cover block rounded-xl"
                style={{ background: '#f3f4f6' }}
                initial={{ opacity: 0, scale: 0.97 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.4 }}
              />
            ) : previewImageUrl ? (
              <motion.img
                src={previewImageUrl}
                alt="Vista previa"
                className="w-full h-40 object-cover block rounded-xl"
                style={{ background: '#f3f4f6' }}
                initial={{ opacity: 0, scale: 0.97 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.4 }}
              />
            ) : (
              <motion.div
                className="flex flex-col items-center justify-center h-40"
                initial={{ opacity: 0, scale: 0.97 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.4 }}
              >
                <span className="text-gray-400 text-sm font-semibold mb-1">No hay imagen de vista previa</span>
                <svg width="48" height="48" viewBox="0 0 36 36" fill="none">
                  <rect x="3" y="3" width="30" height="30" rx="6" fill="#f3f4f6" />
                  <rect x="7" y="9" width="22" height="18" rx="3" fill="#e0e7ff" />
                  <text x="50%" y="60%" textAnchor="middle" fill="#6b7280" fontSize="12" fontWeight="bold" fontFamily="monospace">HTML</text>
                </svg>
              </motion.div>
            )}
          </div>

          {/* Features */}
          <div className="mb-3 space-y-2">
            <div className="flex items-center gap-2 text-xs text-gray-600">
              <span className="font-medium">Tipo:</span>
              <span className="bg-gray-100 px-2 py-0.5 rounded shadow">{file.tipo || 'Sin tipo'}</span>
            </div>
            <div className="flex items-center gap-2 text-xs text-gray-600">
              <span className="font-medium">Categoría:</span>
              <span className="bg-gray-100 px-2 py-0.5 rounded shadow">{file.categoria || 'Sin categoría'}</span>
            </div>
            <div className="flex items-center gap-2 text-xs text-gray-600">
              <span className="font-medium">Subido por:</span>
              <span className="text-gray-700 font-semibold">{file.user_name || 'Desconocido'}</span>
            </div>
          </div>

          {/* Pricing & Actions */}
          <div className="pt-3 border-t border-gray-100">
            <div className="flex justify-between items-center mb-3">
              <div className="flex items-baseline gap-1">
                <span className="text-xl font-bold text-gray-800 tracking-tight">
                  {isVipActual ? '$2' : 'Gratis'}
                </span>
                {isVipActual && <span className="text-xs text-gray-500">USD</span>}
              </div>
              {isVipActual && (
                <div className="text-right">
                  <div className="text-xs text-gray-400 line-through mb-0.5">$4</div>
                  <div className="text-xs font-medium text-yellow-700">50% descuento</div>
                </div>
              )}
            </div>
            {/* Acciones originales */}
            <div className="flex flex-wrap gap-2 mb-2 justify-center">
              {/* Aquí van tus botones Ver, Copiar, Descargar, Like, Editar, Eliminar, etc. */}
            </div>
            <p className="text-xs text-gray-400 text-center">
              {isVipActual ? 'Acceso VIP inmediato tras pago.' : 'Acceso gratuito e inmediato.'}
            </p>
          </div>
          {/* Contadores */}
          <div className="flex justify-between mt-3 text-xs text-gray-500">
            <span>Descargas: <span className="font-semibold">{downloads}</span></span>
            <span>Likes: <span className="font-semibold">{typeof likes === 'number' ? likes : ''}</span></span>
          </div>
        </div>
        {/* Etiqueta comprado */}
        {isVipActual && hasPurchased && (
          <motion.span
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.3 }}
            className="absolute top-3 left-3 bg-green-500 text-white px-2 py-1 rounded text-xs font-bold shadow animate-pulse z-10"
          >
            Comprado
          </motion.span>
        )}
      </motion.div>

      {/* Modal de pago individual VIP */}
      {showVipPayModal && <VipPayModal />}

      {/* Modal de edición */}
      {editModalOpen && <EditModal />}
      {isVip && showVipModal && <VipModal />}
      {showSuccessModal && <SuccessModal />}
      {/* Modal de anuncio/banner antes de acción */}
      {adModalOpen && shouldShowAdModal && (
        <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-black bg-opacity-60">
          <div className="bg-white p-6 rounded-lg shadow-lg w-[400px] text-center relative flex flex-col items-center">
            <button
              className="absolute top-3 right-3 text-gray-400 hover:text-purple-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
              onClick={e => {
                e.stopPropagation();
                setAdModalOpen(false);
                setAdAction(null);
              }}
              aria-label="Cerrar"
              type="button"
            >×</button>
            <h2 className="text-lg font-semibold mb-4">¡Apóyame en TikTok!</h2>
            <div className="flex flex-col items-center justify-center mb-4">
              <span className="text-base text-gray-700 font-semibold mb-2">Sígueme en TikTok para apoyar el proyecto y recibir novedades:</span>
              <a
                href="https://www.tiktok.com/@systecode"
                target="_blank"
                rel="noopener noreferrer"
                className="mt-2 px-4 py-2 bg-pink-500 hover:bg-pink-600 text-white rounded-lg font-bold flex items-center gap-2 text-lg shadow"
                style={{ textDecoration: 'none' }}
              >
                <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 48 48" fill="none">
                  <rect width="48" height="48" rx="12" fill="#FEE2E2"/>
                  <path d="M32.5 14.5c-1.1 0-2-.9-2-2V12h-4v16c0 2.2-1.8 4-4 4s-4-1.8-4-4 1.8-4 4-4c.7 0 1.3.2 1.9.5V20.5c-2.6-.3-5.1 1.7-5.4 4.3-.3 2.6 1.7 5.1 4.3 5.4 2.6.3 5.1-1.7 5.4-4.3V16.5c0-.6.4-1 1-1s1 .4 1 1v2c0 1.1.9 2 2 2h2v-4h-2z" fill="#EC4899"/>
                </svg>
                TikTok @systecode
              </a>
            </div>
            <div className="mt-2 text-sm text-blue-600 text-center font-semibold">
              <span>Si ya lo hiciste, solo dale a continuar.</span>
            </div>
            <button
              onClick={handleAdContinue}
              className="px-4 py-2 rounded-lg font-medium w-full mt-4 bg-green-600 text-white hover:bg-green-700"
            >
              Continuar
            </button>
          </div>
        </div>
      )}
    </>
  );
};

export default FileCard;