import React, { useRef, useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import PayPalButton from './PayPalButton';

// NOTA: Este componente solo gestiona el pago de subida VIP ($1 USD) y la subida del archivo.
// Para que el usuario reciba el 90% de las descargas, debe vincular su cuenta PayPal en su perfil.
// El pago de subida va 100% a la cuenta del dueño de la plataforma.

const VIP_EMAILS = [
  "pastranagonzalezbrayanstiven@gmail.com",
  "baqueperez89@gmail.com"
].map(email => email.trim().toLowerCase()); // <-- normaliza los emails VIP

const FileUpload = ({ onUpload, onClose, currentUser, forceVip = false }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [dragActive, setDragActive] = useState(false);
  const [show, setShow] = useState(true);
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [tipo, setTipo] = useState('');
  const [categoria, setCategoria] = useState('');
  const [descripcion, setDescripcion] = useState('');
  const [image, setImage] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);
  // Nuevo: estado para video
  const [video, setVideo] = useState(null);
  const [videoPreview, setVideoPreview] = useState(null);
  // ARREGLO: Si forceVip está activo, siempre envía vip=true al backend
  const [isVip, setIsVip] = useState(forceVip);
  const [showVipPayModal, setShowVipPayModal] = useState(false);
  const [vipPaid, setVipPaid] = useState(false);
  const [epago, setEpago] = useState(forceVip ? 'vip' : 'gratuito');
  const [showWarning, setShowWarning] = useState(false);

  // Detecta la ruta actual
  const pathname = window.location.pathname;
  const isVipRoute = pathname === '/vip' || forceVip;
  const isArchivosRoute = pathname === '/archivos' || pathname === '/perfil';

  // Controla el tipo de archivo permitido según la ruta
  useEffect(() => {
    if (isVipRoute) {
      setEpago('vip');
      setIsVip(true);
    } else if (isArchivosRoute) {
      setEpago('gratuito');
      setIsVip(false);
    }
  }, [isVipRoute, isArchivosRoute, forceVip]);

  const fileInputRef = useRef();
  const modalRef = useRef();

  const handleFilesChange = (files) => {
    const validFiles = Array.from(files).filter(f => f.name.endsWith('.html'));
    if (validFiles.length !== files.length) {
      setError('Solo se permiten archivos .html');
    } else {
      setError('');
    }
    setSelectedFiles(validFiles);
  };

  // Cambia el handler para imagen/video según VIP
  const handleMediaChange = (e) => {
    const file = e.target.files[0];
    if (!file) {
      setImage(null); setImagePreview(null);
      setVideo(null); setVideoPreview(null);
      setError('');
      return;
    }
    if (isVip && file.type.startsWith('video/')) {
      setVideo(file);
      setVideoPreview(URL.createObjectURL(file));
      setImage(null); setImagePreview(null);
      setError('');
    } else if (!isVip && file.type.startsWith('image/')) {
      setImage(file);
      setImagePreview(URL.createObjectURL(file));
      setVideo(null); setVideoPreview(null);
      setError('');
    } else {
      setImage(null); setImagePreview(null);
      setVideo(null); setVideoPreview(null);
      setError(isVip ? 'Solo se permiten videos para archivos VIP.' : 'Solo se permiten imágenes para archivos gratuitos.');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    // Solo muestra el error si currentUser es null, no si es un objeto vacío
    if (!currentUser || !currentUser.email) {
      setError('Debes iniciar sesión con Google antes de subir archivos.');
      return;
    }
    if (selectedFiles.length === 0) {
      setError('Selecciona al menos un archivo .html');
      return;
    }
    if (!tipo || !categoria) {
      setError('Debes seleccionar el tipo de HTML y la categoría.');
      return;
    }
    // Depura el objeto recibido
    console.log("🔥 currentUser en FileUpload:", currentUser);
    // Normaliza el email del usuario y verifica que existe (soporta varias estructuras)
    const userEmail = (
      currentUser?.email ||
      currentUser?.user?.email ||
      currentUser?.username ||
      ''
    ).trim().toLowerCase();
    const isFreeVip = String(currentUser?.modalidad).toLowerCase() === 'vip';
    console.log('currentUser.email:', currentUser?.email, 'userEmail:', userEmail, 'VIP_EMAILS:', VIP_EMAILS);
    if (isVip && !isFreeVip && !vipPaid) {
      setShowVipPayModal(true);
      return;
    }
    // Solo si es VIP la imagen es obligatoria
    if (isVip && !video) {
      setError('Para archivos VIP debes subir un video de vista previa.');
      return;
    }
    setLoading(true);
    let allOk = true;
    const jwt = localStorage.getItem('jwt');
    // Si forceVip está activo, fuerza VIP en el envío
    const epagoValue = forceVip ? 'vip' : epago;
    for (const file of selectedFiles) {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('tipo', tipo);
      formData.append('categoria', categoria);
      formData.append('descripcion', descripcion);
      formData.append('epago', epagoValue); // <-- envía epago como string
      if (isVip && video) formData.append('video', video);
      if (!isVip && image) formData.append('image', image);
      try {
        const res = await fetch(`${process.env.REACT_APP_API_URL}/files/upload`, {
          method: 'POST',
          credentials: 'include',
          headers: jwt ? { Authorization: `Bearer ${jwt}` } : {},
          body: formData,
        });
        const data = await res.json();
        if (!data.success) {
          allOk = false;
          setError(data.message || 'Error al subir el archivo');
        } else if (data.file && typeof window.onNewFile === 'function') {
          window.onNewFile(data.file); // <-- permite refrescar la lista si usas esta función global
        }
      } catch {
        allOk = false;
        setError('Error de red');
      }
    }
    setLoading(false);
    if (allOk) {
      setShow(false);
      if (onUpload) onUpload();
    }
  };

  // Drag & drop handlers
  const handleDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === 'dragenter' || e.type === 'dragover') setDragActive(true);
    else if (e.type === 'dragleave') setDragActive(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      handleFilesChange(e.dataTransfer.files);
      fileInputRef.current.files = e.dataTransfer.files;
    }
  };

  const handleClick = () => {
    fileInputRef.current.click();
  };

  const handleInputChange = (e) => {
    handleFilesChange(e.target.files);
  };

  // Cerrar solo con la X, mostrar advertencia si clic fuera
  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (modalRef.current && !modalRef.current.contains(e.target)) {
        setShowWarning(true);
      }
    };
    if (show) {
      document.addEventListener('mousedown', handleOutsideClick);
    }
    return () => document.removeEventListener('mousedown', handleOutsideClick);
  }, [show]);

  // Corrige el cierre del modal: solo cambia el estado y no lo vuelve a abrir
  const handleCloseModal = () => {
    setShow(false);
    if (onClose) onClose();
  };

  // Evita que el modal se vuelva a abrir automáticamente
  useEffect(() => {
    if (!show && typeof onClose === 'function') {
      onClose();
    }
    // No vuelvas a poner setShow(true) aquí ni en ningún otro lado
  }, [show, onClose]);

  // Maneja el historial para el modal de subida
  useEffect(() => {
    if (show) {
      window.history.pushState({ modal: true }, '');
      const handlePopState = () => {
        setShow(false);
        if (onClose) onClose();
      };
      window.addEventListener('popstate', handlePopState);
      return () => {
        window.removeEventListener('popstate', handlePopState);
        if (window.history.state && window.history.state.modal) {
          window.history.back();
        }
      };
    }
  }, [show, onClose]);

  // Detecta si el usuario es VIP
  const isUserVip = currentUser && String(currentUser.modalidad).toLowerCase() === 'vip';

  // Solo permite mostrar el formulario si el usuario es VIP
  if (forceVip && !isUserVip) return (
    <div className="bg-yellow-100 border border-yellow-300 rounded-xl p-6 text-yellow-800 text-center font-semibold">
      Solo los usuarios VIP pueden subir archivos VIP.
    </div>
  );

  if (!show) return null;

  return (
    <AnimatePresence>
      {/* Overlay difuminado */}
      <motion.div
        className="fixed inset-0 bg-black bg-opacity-30 backdrop-blur-sm z-40"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      />
      {/* Modal principal mejorado y con scroll */}
      <motion.div
        initial={{ opacity: 0, scale: 0.97, y: -20 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        exit={{ opacity: 0, scale: 0.97, y: -20 }}
        transition={{ duration: 0.2 }}
        className="fixed inset-0 flex items-center justify-center z-50"
        style={{ padding: '16px' }}
      >
        <motion.form
          ref={modalRef}
          onSubmit={handleSubmit}
          className="bg-white rounded-2xl shadow-2xl border border-purple-100 w-full max-w-lg flex flex-col items-center relative overflow-y-auto scrollbar-thin scrollbar-thumb-purple-400 scrollbar-track-purple-100"
          style={{
            maxHeight: '90vh',
            minHeight: '480px',
            padding: '32px 24px',
            boxSizing: 'border-box',
          }}
          initial={{ y: 30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          exit={{ y: 30, opacity: 0 }}
          onDragEnter={handleDrag}
        >
          {/* Botón X para cerrar */}
          <button
            type="button"
            onClick={handleCloseModal}
            className="absolute top-5 right-5 text-gray-400 hover:text-purple-600 text-xl font-bold rounded-full w-9 h-9 flex items-center justify-center focus:outline-none transition"
            aria-label="Cerrar"
            tabIndex={0}
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
              <path d="M6 18L18 6M6 6l12 12"/>
            </svg>
          </button>
          <h2 className="text-2xl font-extrabold mb-6 text-purple-700 text-center w-full">Subir archivo VIP</h2>
          <div
            className={`w-full flex flex-col items-center justify-center border-2 rounded-xl transition-colors duration-200 cursor-pointer mb-6 py-10
              ${dragActive ? 'border-purple-500 bg-purple-50' : 'border-gray-300 bg-gray-50 hover:border-purple-400'}`}
            onClick={handleClick}
            onDragEnter={handleDrag}
            onDragLeave={handleDrag}
            onDragOver={handleDrag}
            onDrop={handleDrop}
            tabIndex={0}
          >
            <svg className="w-14 h-14 text-purple-400 mb-3 animate-bounce" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
              <path d="M12 16v-8m0 8l-4-4m4 4l4-4M20 16.58A5 5 0 0017 7H7a5 5 0 00-3 9.58"/>
            </svg>
            <span className="font-medium text-gray-700 text-lg">
              Arrastra aquí tus archivos <span className="text-purple-600">.html</span> o haz clic para seleccionar
            </span>
            <input
              type="file"
              accept=".html"
              ref={fileInputRef}
              className="hidden"
              required
              multiple
              onChange={handleInputChange}
            />
          </div>
          {/* Campos adicionales obligatorios */}
          <div className="w-full grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div>
              <label className="font-semibold text-gray-700 text-sm" htmlFor="tipo-html">
                Tipo de HTML <span className="text-red-500">*</span>
              </label>
              <select
                id="tipo-html"
                value={tipo}
                onChange={e => setTipo(e.target.value)}
                required
                className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500 transition bg-white mt-1"
              >
                <option value="">Selecciona tipo...</option>
                <option value="carta">Carta</option>
                <option value="flores">Flores</option>
                <option value="decorado">Decorado</option>
                <option value="dedicatoria">Dedicatoria</option>
                <option value="globos">Globos</option>
                <option value="otro">Otro</option>
              </select>
            </div>
            <div>
              <label className="font-semibold text-gray-700 text-sm" htmlFor="categoria-html">
                Categoría <span className="text-red-500">*</span>
              </label>
              <select
                id="categoria-html"
                value={categoria}
                onChange={e => setCategoria(e.target.value)}
                required
                className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400 transition bg-white mt-1"
              >
                <option value="">Selecciona categoría...</option>
                <option value="amor">Amor</option>
                <option value="amistad">Amistad</option>
                <option value="romance">Romance</option>
                <option value="otro">Otro</option>
              </select>
            </div>
          </div>
          <div className="w-full mb-6">
            <label className="font-semibold text-gray-700 text-sm" htmlFor="descripcion-html">
              Descripción <span className="text-gray-400 text-xs">(opcional)</span>
            </label>
            <textarea
              id="descripcion-html"
              value={descripcion}
              onChange={e => setDescripcion(e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400 transition bg-white resize-none mt-1"
              rows={2}
              maxLength={200}
              placeholder="Agrega una breve descripción del archivo (máx 200 caracteres)"
            />
          </div>
          {/* Campo para imagen/video de vista previa */}
          <div className="w-full mb-6">
            <label className="font-semibold text-gray-700 text-sm" htmlFor="preview-media">
              {isVip ? "Video de vista previa * obligatorio para VIP" : "Imagen de vista previa (opcional)"}
            </label>
            <input
              type="file"
              id="preview-media"
              accept={isVip ? "video/*" : "image/*"}
              required={isVip}
              onChange={handleMediaChange}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 bg-white mt-1"
            />
            {isVip && videoPreview && (
              <div className="mt-3 flex justify-center">
                <video
                  src={videoPreview}
                  controls
                  className="max-h-40 rounded-lg border shadow w-full"
                  style={{ objectFit: 'cover', maxWidth: '100%' }}
                />
              </div>
            )}
            {!isVip && imagePreview && (
              <div className="mt-3 flex justify-center">
                <img
                  src={imagePreview}
                  alt="Vista previa"
                  className="max-h-40 rounded-lg border shadow"
                  style={{ objectFit: 'cover', maxWidth: '100%' }}
                />
              </div>
            )}
          </div>
          {/* Lista de archivos seleccionados */}
          {selectedFiles.length > 0 && (
            <div className="w-full mb-6">
              <div className="text-sm font-semibold text-gray-700 mb-1">Archivos listos para subir:</div>
              <ul className="list-disc pl-5 text-sm text-gray-600">
                {selectedFiles.map((f, idx) => (
                  <li key={idx}>{f.name}</li>
                ))}
              </ul>
            </div>
          )}
          {error && <div className="text-red-500 text-sm mb-4 w-full text-center">{error}</div>}
          {/* Tipo de acceso: solo gratuito en /archivos y /perfil, solo VIP en /vip */}
          <div className="w-full mb-6">
            <label className="font-semibold text-gray-700 block mb-1">Tipo de acceso</label>
            <div className="flex gap-6">
              <label className={`flex items-center gap-2 ${isVipRoute ? 'opacity-40' : ''}`}>
                <input
                  type="radio"
                  name="epago"
                  value="gratuito"
                  checked={!isVipRoute}
                  readOnly
                  disabled
                />
                <span>Gratuito</span>
                {isVipRoute && (
                  <span className="text-xs text-gray-400 ml-1">(Solo disponible en otras secciones)</span>
                )}
              </label>
              <label className={`flex items-center gap-2 ${!isVipRoute ? 'opacity-40' : ''}`}>
                <input
                  type="radio"
                  name="epago"
                  value="vip"
                  checked={isVipRoute}
                  readOnly
                  disabled
                />
                <span>VIP</span>
                {!isVipRoute && (
                  <span className="text-xs text-gray-400 ml-1">(Solo disponible en sección VIP)</span>
                )}
              </label>
            </div>
          </div>
          <button
            type="submit"
            disabled={loading}
            className="bg-gradient-to-r from-purple-500 to-blue-500 text-white px-8 py-3 rounded-xl font-semibold hover:bg-purple-700 transition w-full shadow text-lg"
          >
            {loading ? 'Subiendo...' : 'Subir'}
          </button>
        </motion.form>
        {dragActive && (
          <div
            className="absolute inset-0 bg-purple-100 bg-opacity-40 rounded-xl pointer-events-none"
          />
        )}
      </motion.div>
      {/* Modal de pago para usuarios no autorizados */}
      {showVipPayModal && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-[999]">
          <div className="bg-white rounded-2xl shadow-2xl border p-8 max-w-md w-full relative flex flex-col items-center">
            <button
              className="absolute top-3 right-3 text-gray-400 hover:text-yellow-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
              onClick={() => setShowVipPayModal(false)}
              aria-label="Cerrar"
              type="button"
            >×</button>
            <h2 className="text-3xl font-bold text-yellow-500 mb-2 text-center">Pago para subir archivo VIP</h2>
            <div className="w-full flex flex-col items-center my-4">
              <PayPalButton
                amount={1.00}
                description="Subida de archivo VIP"
                clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
                onSuccess={() => {
                  setShowVipPayModal(false);
                  setVipPaid(true); // Marca como pagado
                }}
              />
            </div>
            <div className="mt-4 text-xs text-gray-500 text-center">
              Ingresa los datos de tu tarjeta o inicia sesión en PayPal para pagar $1.00 USD.<br />
              Tras el pago podrás subir tu archivo VIP.<br />
              <span className="block mt-2 text-yellow-700 font-semibold">
                <strong>Importante:</strong> Para recibir el 90% de las descargas de tu archivo VIP, debes vincular tu cuenta PayPal en tu perfil.
              </span>
            </div>
          </div>
        </div>
      )}
      {/* Modal de advertencia si clic fuera */}
      {showWarning && (
        <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-[999]">
          <div className="bg-white rounded-2xl shadow-2xl border p-8 max-w-md w-full relative flex flex-col items-center">
            <button
              className="absolute top-3 right-3 text-gray-400 hover:text-red-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
              onClick={() => setShowWarning(false)}
              aria-label="Cerrar"
              type="button"
            >×</button>
            <h2 className="text-xl font-bold text-red-500 mb-2 text-center">Advertencia</h2>
            <div className="text-gray-700 text-center mb-4 text-base">
              Estás intentando cerrar el formulario de subida fuera del botón.<br />
              Por seguridad, solo puedes cerrar el formulario usando la <span className="font-bold text-purple-700">X</span> en la esquina superior.
            </div>
            <button
              className="mt-2 px-6 py-2 rounded bg-purple-600 text-white font-semibold hover:bg-purple-700 transition"
              onClick={() => setShowWarning(false)}
            >
              Entendido
            </button>
          </div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default FileUpload;