import React, { useEffect, useState } from 'react';

const PAGE_SIZE = 20; // Puedes ajustar el tamaño de página
const UsersPage = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedUser, setSelectedUser] = useState(null);
  const [userFiles, setUserFiles] = useState([]);
  const [loadingFiles, setLoadingFiles] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [search, setSearch] = useState('');
  const [userFilesPage, setUserFilesPage] = useState(1);
  const [userFilesHasMore, setUserFilesHasMore] = useState(false);

  const API_URL = process.env.REACT_APP_API_URL;

  // Detectar si el usuario actual es admin
  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetch(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` }
      })
        .then(res => res.json())
        .then(data => setIsAdmin(data.user?.rol?.toLowerCase() === 'admin'))
        .catch(() => setIsAdmin(false));
    }
  }, [API_URL]);

  useEffect(() => {
    setLoading(true);
    const jwt = localStorage.getItem('jwt');
    fetch(`${API_URL}/users/all`, {
      headers: jwt ? { Authorization: `Bearer ${jwt}` } : {},
      credentials: 'include'
    })
      .then(res => res.ok ? res.json() : { users: [] })
      .then(data => setUsers(data.users || []))
      .finally(() => setLoading(false));
  }, [API_URL]);

  const handleDeleteUser = async (id) => {
    if (!window.confirm('¿Seguro que deseas eliminar este usuario permanentemente?')) return;
    const jwt = localStorage.getItem('jwt');
    await fetch(`${API_URL}/users/delete/${id}`, {
      method: 'DELETE',
      headers: jwt ? { Authorization: `Bearer ${jwt}` } : {},
      credentials: 'include'
    });
    setUsers(users => users.filter(u => String(u.id) !== String(id)));
    if (selectedUser && String(selectedUser.id) === String(id)) {
      handleCloseFiles();
    }
  };

  // Carga una página específica de archivos del usuario
  const loadUserFilesPage = async (userId, page = 1) => {
    setLoadingFiles(true);
    try {
      const res = await fetch(`${API_URL}/files?userId=${encodeURIComponent(userId)}&page=${page}&limit=${PAGE_SIZE}`);
      if (!res.ok) throw new Error('Error al cargar archivos');
      const data = await res.json();
      if (page === 1) {
        setUserFiles(data.files || []);
      } else {
        setUserFiles(prev => [...prev, ...(data.files || [])]);
      }
      setUserFilesHasMore(data.hasMore);
      setUserFilesPage(page);
    } catch (e) {
      setUserFiles([]);
      setUserFilesHasMore(false);
    } finally {
      setLoadingFiles(false);
    }
  };

  // Mostrar archivos de un usuario al hacer click, carga primera página
  const handleShowFiles = (user) => {
    setSelectedUser(user);
    loadUserFilesPage(user.id, 1);
  };

  // Cargar más archivos (página siguiente)
  const loadMoreFiles = () => {
    if (selectedUser && userFilesHasMore && !loadingFiles) {
      loadUserFilesPage(selectedUser.id, userFilesPage + 1);
    }
  };

  // Eliminar archivo individual como admin
  const handleDeleteFile = async (fileId) => {
    if (!window.confirm('¿Eliminar este archivo?')) return;
    const jwt = localStorage.getItem('jwt');
    const res = await fetch(`${API_URL}/files/delete/${fileId}`, {
      method: 'DELETE',
      headers: jwt ? { Authorization: `Bearer ${jwt}` } : {}
    });
    if (res.ok) {
      setUserFiles(files => files.filter(f => String(f.id) !== String(fileId)));
    }
  };

  // Cerrar modal y resetear estado
  const handleCloseFiles = () => {
    setSelectedUser(null);
    setUserFiles([]);
    setUserFilesPage(1);
    setUserFilesHasMore(false);
  };

  // Filtro de usuarios por nombre o email
  const filteredUsers = users.filter(u =>
    u.name?.toLowerCase().includes(search.toLowerCase()) ||
    u.email?.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="px-6 py-10">
      <h1 className="text-3xl font-bold mb-6 text-purple-800">Usuarios registrados</h1>
      {/* Filtro de búsqueda de usuarios */}
      <div className="mb-6 max-w-md">
        <input
          type="text"
          placeholder="Buscar usuario por nombre o email..."
          value={search}
          onChange={e => setSearch(e.target.value)}
          className="w-full px-4 py-2 border border-purple-200 rounded-lg shadow focus:outline-none focus:ring-2 focus:ring-purple-400 transition"
        />
      </div>
      {loading ? (
        <div className="text-blue-600 font-semibold">Cargando usuarios...</div>
      ) : (
        <div className="flex flex-col gap-3">
          {filteredUsers.map(u => (
            <div
              key={u.id}
              className="flex items-center gap-3 bg-gray-50 px-3 py-2 rounded shadow cursor-pointer hover:bg-purple-50 transition"
              onClick={() => handleShowFiles(u)}
              tabIndex={0}
            >
              <img src={u.photo} alt={u.name} className="w-7 h-7 rounded-full border" />
              <span className="text-sm font-medium text-gray-700 flex-1">{u.name}</span>
              <span className="text-xs text-gray-400">{u.email}</span>
              <span className="text-xs text-gray-400">{u.rol}</span>
              {isAdmin && (
                <button
                  className="bg-red-100 text-red-600 px-2 py-1 rounded hover:bg-red-200 text-xs font-semibold"
                  onClick={e => { e.stopPropagation(); handleDeleteUser(u.id); }}
                >
                  Eliminar cuenta
                </button>
              )}
            </div>
          ))}
        </div>
      )}

      {/* Modal de archivos del usuario */}
      {selectedUser && (
        <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-lg p-6 max-w-lg w-full relative">
            <button
              className="absolute top-2 right-2 text-gray-500 hover:text-gray-800 text-lg font-bold"
              onClick={handleCloseFiles}
              aria-label="Cerrar"
            >
              ×
            </button>
            <div className="flex items-center gap-3 mb-4">
              <img src={selectedUser.photo} alt={selectedUser.name} className="w-10 h-10 rounded-full border" />
              <div>
                <div className="font-semibold text-gray-800">{selectedUser.name}</div>
                <div className="text-xs text-gray-500">{selectedUser.email}</div>
                <div className="text-xs text-gray-400">{selectedUser.rol}</div>
              </div>
            </div>
            <div className="font-semibold text-purple-700 mb-2">Archivos subidos:</div>
            {loadingFiles ? (
              <div className="text-blue-600 font-semibold">Cargando archivos...</div>
            ) : userFiles.length === 0 ? (
              <div className="text-gray-400 text-sm">No ha subido archivos.</div>
            ) : (
              <>
                <ul className="max-h-60 overflow-y-auto space-y-2">
                  {userFiles.map(f => (
                    <li key={f.id} className="flex items-center gap-2 bg-gray-50 px-2 py-1 rounded shadow">
                      <svg width="20" height="20" viewBox="0 0 36 36" fill="none">
                        <rect x="3" y="3" width="30" height="30" rx="6" fill="#f3f4f6" />
                        <rect x="7" y="9" width="22" height="18" rx="3" fill="#e0e7ff" />
                        <text x="50%" y="60%" textAnchor="middle" fill="#6366f1" fontSize="10" fontWeight="bold" fontFamily="monospace">HTML</text>
                      </svg>
                      <span className="text-sm text-gray-700">{f.filename}</span>
                      <span className="text-xs text-gray-400">{f.tipo}</span>
                      <span className="text-xs text-gray-400">{f.categoria}</span>
                      <span className="text-xs text-gray-400">Likes: {f.likes}</span>
                      <span className="text-xs text-gray-400">Descargas: {f.downloads}</span>
                      {isAdmin && (
                        <button
                          className="ml-auto bg-red-100 text-red-600 px-2 py-1 rounded hover:bg-red-200 text-xs font-semibold"
                          onClick={() => handleDeleteFile(f.id)}
                        >
                          Eliminar
                        </button>
                      )}
                    </li>
                  ))}
                </ul>
                {userFilesHasMore && (
                  <button
                    onClick={loadMoreFiles}
                    disabled={loadingFiles}
                    className="mt-2 px-4 py-2 bg-purple-600 text-white rounded hover:bg-purple-700"
                  >
                    {loadingFiles ? 'Cargando...' : 'Cargar más'}
                  </button>
                )}
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default UsersPage;
