import React, { useEffect, useState, useRef } from 'react';
import FileList from '../components/FileList';
import SearchBar from '../components/SearchBar';
import FileUpload from '../components/FileUpload';
import PayPalButton from '../components/PayPalButton';
import { motion, AnimatePresence } from 'framer-motion';

const PAGE_SIZE = 20;

const FileListPage = () => {
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [userTerm, setUserTerm] = useState('');
  const [tipo, setTipo] = useState('');
  const [categoria, setCategoria] = useState('');
  const [showUpload, setShowUpload] = useState(false);
  const [user, setUser] = useState(null);
  const [alert, setAlert] = useState('');
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(false);
  const [filterPago, setFilterPago] = useState('todos'); // "todos", "gratis", "vip"
  const [showSubscribeModal, setShowSubscribeModal] = useState(false);
  const [showDonateModal, setShowDonateModal] = useState(false);
  const [donateAmount, setDonateAmount] = useState(1); // <-- inicia en 1
  const API_URL = process.env.REACT_APP_API_URL;

  // Agregar estilos CSS personalizados para el scroll
  useEffect(() => {
    const style = document.createElement('style');
    style.innerHTML = `
      /* Scroll personalizado para toda la página */
      ::-webkit-scrollbar {
        width: 12px;
      }
      
      ::-webkit-scrollbar-track {
        background: linear-gradient(180deg, #f1f5f9 0%, #e2e8f0 100%);
        border-radius: 10px;
      }
      
      ::-webkit-scrollbar-thumb {
        background: linear-gradient(180deg, #6366f1 0%, #8b5cf6 50%, #a855f7 100%);
        border-radius: 10px;
        border: 2px solid #f1f5f9;
        transition: all 0.3s ease;
      }
      
      ::-webkit-scrollbar-thumb:hover {
        background: linear-gradient(180deg, #4f46e5 0%, #7c3aed 50%, #9333ea 100%);
        border-color: #e2e8f0;
        transform: scale(1.1);
      }
      
      ::-webkit-scrollbar-corner {
        background: #f1f5f9;
      }
      
      /* Para Firefox */
      html {
        scrollbar-width: thin;
        scrollbar-color: #8b5cf6 #f1f5f9;
      }
    `;
    document.head.appendChild(style);
    
    return () => {
      document.head.removeChild(style);
    };
  }, []);

  // fetchWithAuth para renovar el token si expira
  async function fetchWithAuth(url, options = {}) {
    let token = localStorage.getItem('jwt');
    let refreshToken = localStorage.getItem('refreshToken'); // importante tener el refreshToken en localStorage

    const fetchWithToken = async (tokenToUse) => {
      return fetch(url, {
        ...options,
        headers:
          {
            ...(options.headers || {}),
            Authorization: `Bearer ${tokenToUse}`,
            'Content-Type': 'application/json',
          },
        // no necesitas credentials si no usas cookies
      });
    };

    let response = await fetchWithToken(token);

    if (response.status === 401 && refreshToken) {
      // Intentar renovar el token enviando el refreshToken en body (o header)
      const refreshRes = await fetch(
        `${process.env.REACT_APP_API_URL}/auth/refresh`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'x-refresh-token': refreshToken, // o lo mandas en el body
          },
          body: JSON.stringify({ refreshToken }),
        }
      );

      if (refreshRes.ok) {
        const data = await refreshRes.json();
        token = data.accessToken;
        localStorage.setItem('jwt', token);
        // Reintentar la petición original con nuevo token
        response = await fetchWithToken(token);
      } else {
        // Si falla el refresh, limpiar tokens y redirigir a login
        localStorage.removeItem('jwt');
        localStorage.removeItem('refreshToken');
        window.location.href = '/login';
      }
    } else if (response.status === 401) {
      // Sin refresh token o no autorizado
      localStorage.removeItem('jwt');
      localStorage.removeItem('refreshToken');
      window.location.href = '/login';
    }

    return response;
  }

  useEffect(() => {
    setLoading(true);
    // Filtra por epago según el combo
    let epagoParam = '';
    if (filterPago === 'vip') epagoParam = '&epago=vip';
    else if (filterPago === 'gratis') epagoParam = '&epago=gratis';
    fetchWithAuth(
      `${API_URL}/files?search=${encodeURIComponent(
        searchTerm
      )}&user=${encodeURIComponent(userTerm)}${epagoParam}&tipo=${encodeURIComponent(
        tipo
      )}&categoria=${encodeURIComponent(categoria)}&page=${page}&limit=${PAGE_SIZE}`
    )
      .then((res) => (res.ok ? res.json() : { files: [], hasMore: false }))
      .then((data) => {
        setFiles(data.files || []);
        setHasMore(data.hasMore || false);
      })
      .catch(() => {
        setFiles([]);
        setHasMore(false);
      })
      .finally(() => setLoading(false));
  }, [API_URL, searchTerm, userTerm, tipo, categoria, page, filterPago]);

  useEffect(() => {
    // Detectar usuario logueado
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetchWithAuth(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` },
      })
        .then((res) => res.json())
        .then((data) => setUser(data.user || null))
        .catch(() => setUser(null));
    } else {
      setUser(null);
    }
  }, [API_URL]);

  const refreshFiles = () => {
    setLoading(true);
    fetchWithAuth(
      `${API_URL}/files?search=${encodeURIComponent(
        searchTerm
      )}&user=${encodeURIComponent(userTerm)}&tipo=${encodeURIComponent(
        tipo
      )}&categoria=${encodeURIComponent(categoria)}`
    )
      .then((res) => (res.ok ? res.json() : { files: [] }))
      .then((data) => setFiles(data.files || []))
      .finally(() => setLoading(false));
  };

  const handleUploadClick = () => {
    if (user) {
      setShowUpload(true);
      setAlert('');
    } else {
      setShowLoginModal(true);
      setAlert('');
    }
  };

  const handleUpload = (newFile) => {
    setFiles((prev) => [...prev, newFile]);
    // O mejor: refetch para asegurar que el estado está sincronizado con la BD
    refreshFiles();
  };

  // Modal de advertencia para login Google
  const LoginModal = () => (
    <AnimatePresence>
      <motion.div
        className="fixed inset-0 bg-black bg-opacity-40 z-50 flex items-center justify-center"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          className="bg-white rounded-xl shadow-2xl p-8 max-w-xs w-full flex flex-col items-center relative border border-purple-200"
          initial={{ scale: 0.95, y: -20, opacity: 0 }}
          animate={{ scale: 1, y: 0, opacity: 1 }}
          exit={{ scale: 0.95, y: -20, opacity: 0 }}
        >
          <button
            className="absolute top-3 right-3 text-gray-400 hover:text-purple-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
            onClick={() => setShowLoginModal(false)}
            aria-label="Cerrar"
            type="button"
          >
            ×
          </button>
          <svg
            className="w-16 h-16 text-purple-400 mb-3"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            viewBox="0 0 24 24"
          >
            <path d="M12 9v2m0 4h.01M21 12c0 4.97-4.03 9-9 9s-9-4.03-9-9 4.03-9 9-9 9 4.03 9 9z" />
          </svg>
          <div className="text-xl font-bold text-purple-700 mb-2 text-center">
            Debes iniciar sesión
          </div>
          <div className="text-gray-600 text-center mb-5">
            Para subir archivos HTML necesitas iniciar sesión con Google.
          </div>
          <button
            className="gsi-material-button w-full"
            onClick={() => {
              setShowLoginModal(false);
              window.location.href = `${process.env.REACT_APP_API_URL}/auth/google`;
            }}
            type="button"
          >
            <div className="gsi-material-button-state"></div>
            <div className="gsi-material-button-content-wrapper">
              <div className="gsi-material-button-icon">
                <svg
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 48 48"
                  style={{ display: 'block' }}
                >
                  <path
                    fill="#EA4335"
                    d="M24 9.5c3.54 0 6.71 1.22 9.21 3.6l6.85-6.85C35.9 2.38 30.47 0 24 0 14.62 0 6.51 5.38 2.56 13.22l7.98 6.19C12.43 13.72 17.74 9.5 24 9.5z"
                  ></path>
                  <path
                    fill="#4285F4"
                    d="M46.98 24.55c0-1.57-.15-3.09-.38-4.55H24v9.02h12.94c-.58 2.96-2.26 5.48-4.78 7.18l7.73 6c4.51-4.18 7.09-10.36 7.09-17.65z"
                  ></path>
                  <path
                    fill="#FBBC05"
                    d="M10.53 28.59c-.48-1.45-.76-2.99-.76-4.59s.27-3.14.76-4.59l-7.98-6.19C.92 16.46 0 20.12 0 24c0 3.88.92 7.54 2.56 10.78l7.97-6.19z"
                  ></path>
                  <path
                    fill="#34A853"
                    d="M24 48c6.48 0 11.93-2.13 15.89-5.81l-7.73-6c-2.15 1.45-4.92 2.3-8.16 2.3-6.26 0-11.57-4.22-13.47-9.91l-7.98 6.19C6.51 42.62 14.62 48 24 48z"
                  ></path>
                  <path fill="none" d="M0 0h48v48H0z"></path>
                </svg>
              </div>
              <span className="gsi-material-button-contents">
                Iniciar sesión con Google
              </span>
              <span style={{ display: 'none' }}>Iniciar sesión con Google</span>
            </div>
          </button>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );

  // Suscripción: función para actualizar modalidad tras pago
  const handleSubscriptionSuccess = async () => {
    try {
      const jwt = localStorage.getItem('jwt');
      await fetch(`${API_URL}/users/subscribe-vip`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${jwt}` },
      });
      // Refresca usuario
      fetchWithAuth(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` },
      })
        .then((res) => res.json())
        .then((data) => setUser(data.user || null));
      setShowSubscribeModal(false);
      alert('¡Ahora eres usuario VIP por 1 mes!');
    } catch {
      alert('Error al activar la suscripción VIP.');
    }
  };

  // Escuchar evento global de login/logout para actualizar el estado en todas las páginas
  useEffect(() => {
    const handleLogin = () => {
      const jwt = localStorage.getItem('jwt');
      if (jwt) {
        fetch(`${API_URL}/auth/me`, {
          headers: { Authorization: `Bearer ${jwt}` },
        })
          .then((res) => res.json())
          .then((data) => setUser(data.user || null))
          .catch(() => setUser(null));
      } else {
        setUser(null);
      }
    };
    window.addEventListener('storage', handleLogin);
    window.addEventListener('login', handleLogin);
    return () => {
      window.removeEventListener('storage', handleLogin);
      window.removeEventListener('login', handleLogin);
    };
  }, [API_URL]);

  // ...existing code...

  const handleSearch = (term) => {
    setSearchTerm(term);
  };

  // Referencia solo para el banner móvil
  const bannerMobileRef = useRef(null);

  useEffect(() => {
    if (bannerMobileRef.current) {
      bannerMobileRef.current.innerHTML = '';
      const script1 = document.createElement('script');
      script1.type = 'text/javascript';
      script1.innerHTML = `
        atOptions = {
          'key' : '59100bc8ee7a09132f72ceeec85b6cf2',
          'format' : 'iframe',
          'height' : 250,
          'width' : 300,
          'params' : {}
        };
      `;
      const script2 = document.createElement('script');
      script2.type = 'text/javascript';
      script2.src =
        '//www.highperformanceformat.com/59100bc8ee7a09132f72ceeec85b6cf2/invoke.js';
      script2.setAttribute('data-adsterra', 'true');
      bannerMobileRef.current.appendChild(script1);
      bannerMobileRef.current.appendChild(script2);
    }
    // Limpia los scripts y el banner al desmontar
    return () => {
      if (bannerMobileRef.current) bannerMobileRef.current.innerHTML = '';
      Array.from(document.querySelectorAll('script[data-adsterra]')).forEach(s => s.remove());
    };
  }, []);
  return (
    <div className="px-6 py-10 max-w-5xl mx-auto">
      {/* Banner eliminado, ahora se muestra en DashboardNavbar */}

  {/* ...existing code... */}

      <div className="flex flex-col md:flex-row md:justify-between md:items-center mb-6 gap-4">
        <div className="flex flex-row gap-2">
          <button
            className={`bg-gradient-to-r from-blue-500 to-purple-500 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-blue-700 transition ${!user ? 'opacity-60 cursor-not-allowed' : ''}`}
            onClick={handleUploadClick}
            disabled={false}
          >
            Subir archivos
          </button>
          <button
            className="bg-gradient-to-r from-yellow-400 to-yellow-600 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-yellow-700 transition"
            onClick={() => {
              if (!user) {
                setShowLoginModal(true);
                setAlert('');
              } else {
                setShowSubscribeModal(true);
              }
            }}
            disabled={false}
          >
            Suscribirse
          </button>
          <button
            className="bg-gradient-to-r from-gray-400 to-gray-600 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-gray-700 transition"
            onClick={() => {
              if (!user) {
                setShowLoginModal(true);
                setAlert('');
              } else {
                setShowDonateModal(true);
              }
            }}
            disabled={false}
          >
            Donar
          </button>
        </div>
      </div>
      {alert && (
        <div className="mb-4 text-red-600 font-semibold text-center">{alert}</div>
      )}
      {showUpload && user && (
        <FileUpload
          onUpload={handleUpload}
          onClose={() => setShowUpload(false)}
          currentUser={user}
          forceVip={false} // Solo subida gratuita
        />
      )}
      {/* Modal de suscripción VIP SOLO con PayPalButton */}
      <AnimatePresence>
        {showSubscribeModal && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-40">
            <div className="relative p-4 w-full max-w-2xl max-h-full">
              <div className="relative bg-white rounded-lg shadow-sm"
                style={{ maxHeight: '90vh', overflowY: 'auto' }}>
                <div className="flex items-center justify-between p-4 border-b rounded-t border-gray-200">
                  <h3 className="text-xl font-semibold text-gray-900">
                    Suscripción VIP mensual
                  </h3>
                  <button
                    type="button"
                    className="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ms-auto inline-flex justify-center items-center"
                    onClick={() => setShowSubscribeModal(false)}
                    aria-label="Cerrar"
                  >
                    <svg className="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 14">
                        <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"/>
                    </svg>
                    <span className="sr-only">Cerrar modal</span>
                  </button>
                </div>
                <div className="p-4 space-y-4">
                  {/* ...contenido del modal de suscripción... */}
                  <PayPalButton
                    amount={4.00}
                    description={'Suscripción VIP mensual'}
                    fileId={null}
                    userId={user?.id}
                    clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
                    onSuccess={handleSubscriptionSuccess}
                  />
                </div>
                <div className="flex items-center p-4 border-t border-gray-200 rounded-b">
                  {/* Botón de cerrar o acción extra si lo necesitas */}
                </div>
              </div>
            </div>
          </div>
        )}
      </AnimatePresence>
      {/* Modal de donación */}
      <AnimatePresence>
        {showDonateModal && (
          <motion.div
            className="fixed inset-0 z-[9999] flex items-center justify-center bg-black bg-opacity-40"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setShowDonateModal(false)}
          >
            <motion.div
              className="bg-white rounded-2xl shadow-2xl border p-8 w-full max-w-md mx-2 relative flex flex-col items-center"
              style={{ maxHeight: '90vh', overflowY: 'auto' }}
              initial={{ scale: 0.95, y: 30, opacity: 0 }}
              animate={{ scale: 1, y: 0, opacity: 1 }}
              exit={{ scale: 0.95, y: 30, opacity: 0 }}
              transition={{ duration: 0.25 }}
              onClick={e => e.stopPropagation()}
            >
              <button
                className="absolute top-3 right-3 text-gray-400 hover:text-gray-700 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
                onClick={() => setShowDonateModal(false)}
                aria-label="Cerrar"
                type="button"
              >×</button>
              <div className="mb-4 text-lg text-gray-700 font-semibold text-center">
                ¡Gracias por tu interés en donar!<br />
                Tu aporte ayuda a mantener y mejorar la plataforma.<br />
                Ingresa la cantidad que deseas donar:
              </div>
              <div className="w-full flex flex-col items-center mb-4">
                <input
                  type="number"
                  min={1}
                  max={100}
                  step={1}
                  value={donateAmount}
                  onChange={e => {
                    let val = Number(e.target.value);
                    if (isNaN(val)) val = 1;
                    if (val < 1) val = 1;
                    if (val > 100) val = 100;
                    setDonateAmount(val);
                  }}
                  className="w-32 border border-gray-300 rounded px-3 py-2 text-center text-lg font-bold text-gray-800 focus:outline-none focus:ring-2 focus:ring-gray-400"
                  placeholder="Monto USD"
                />
                <div className="text-xs text-gray-400 mt-1">
                  Mínimo $1 USD • Máximo $100 USD
                </div>
              </div>
              <div className="w-full flex justify-center my-2">
                <PayPalButton
                  amount={donateAmount}
                  description={`Donación voluntaria`}
                  fileId={null}
                  userId={user?.id}
                  clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
                  onSuccess={() => {
                    setShowDonateModal(false);
                    setDonateAmount(1); // <-- vuelve a 1 después de donar
                    alert('¡Gracias por tu donación!');
                  }}
                  disabled={donateAmount < 1}
                />
              </div>
              <div className="mt-4 text-xs text-gray-500 text-center">
                Tu donación es voluntaria y no otorga acceso VIP.<br />
                ¡Gracias por apoyar el proyecto!
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
      {/* Lista de archivos */}
      <FileList
        files={files}
        currentUser={user}
        loading={loading}
        onLikeChange={refreshFiles}
        page={page}
        setPage={setPage}
        hasMore={hasMore}
        searchTerm={searchTerm}
        setSearchTerm={setSearchTerm}
        filterPago={filterPago}
        setFilterPago={setFilterPago}
        title="" // El título ya está arriba
      />
      {showLoginModal && <LoginModal />}
    </div>
  );
};

export default FileListPage;



