import React, { useEffect, useRef, useState } from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';

const PayPalButton = ({
  amount = 1.0,
  description = 'Archivo HTML VIP',
  fileId,
  userId,
  onSuccess,
  clientId
}) => {
  const paypalRef = useRef();
  const [loading, setLoading] = useState(false);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'info' });

  useEffect(() => {
    const realClientId = clientId || process.env.REACT_APP_PAYPAL_CLIENT_ID;
    if (!realClientId) {
      setSnackbar({ open: true, message: 'Falta el Client ID de PayPal', severity: 'error' });
      return;
    }

    if (paypalRef.current) paypalRef.current.innerHTML = '';

    const existingScript = document.querySelector('#paypal-sdk');
    if (existingScript) existingScript.remove();

    const script = document.createElement('script');
    script.id = 'paypal-sdk';
    script.src = `https://www.paypal.com/sdk/js?client-id=${realClientId}&currency=USD&intent=capture`;
    script.async = true;
    script.onload = () => renderButton();
    document.body.appendChild(script);

    return () => { script.remove(); };

    function renderButton() {
      if (!window.paypal) {
        setSnackbar({ open: true, message: 'Error cargando PayPal SDK', severity: 'error' });
        return;
      }

      window.paypal.Buttons({
        style: { layout: 'vertical', color: 'gold', shape: 'rect', label: 'paypal' },
        createOrder: (data, actions) => {
          const uniqueInvoiceId = `${userId}-${Date.now()}`;
          return actions.order.create({
            purchase_units: [{
              amount: { value: amount.toFixed(2), currency_code: 'USD' },
              description,
              custom_id: String(fileId),
              invoice_id: uniqueInvoiceId
            }],
            application_context: { shipping_preference: 'NO_SHIPPING' },
          });
        },
        onApprove: async (data, actions) => {
          setLoading(true);
          try {
            const details = await actions.order.capture();
            if (details.status !== 'COMPLETED') {
              setSnackbar({ open: true, message: 'El pago no fue completado correctamente.', severity: 'error' });
              setLoading(false);
              return;
            }

            const payerEmail = details.payer.email_address;
            const totalAmount = details.purchase_units[0].amount.value;
            const currency = details.purchase_units[0].amount.currency_code;

            if (currency !== 'USD' || Number(totalAmount) !== Number(amount)) {
              setSnackbar({ open: true, message: 'Monto o moneda incorrecta. Contacta soporte.', severity: 'error' });
              setLoading(false);
              return;
            }

            // Solo llamar a /files/:id/confirmPurchase si fileId es válido y no null
            if (
              fileId !== null &&
              fileId !== undefined &&
              fileId !== 'null' &&
              !isNaN(Number(fileId))
            ) {
              await fetch(`${process.env.REACT_APP_API_URL}/files/${fileId}/confirmPurchase`, {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json',
                  Authorization: `Bearer ${localStorage.getItem('jwt')}`
                },
                body: JSON.stringify({ orderID: data.orderID, payerID: details.payer.payer_id, payerEmail, amount: totalAmount })
              });
            }

            setSnackbar({ open: true, message: 'Pago confirmado con éxito', severity: 'success' });
            if (onSuccess) onSuccess();
            if (amount === 2) {
              window.location.href = '/vip';
            }
          } catch (err) {
            console.error(err);
            setSnackbar({ open: true, message: 'Error al confirmar la compra', severity: 'error' });
          } finally {
            setLoading(false);
          }
        },
        onError: (err) => {
          console.error(err);
          setSnackbar({ open: true, message: 'Error en el pago: ' + err, severity: 'error' });
        }
      }).render(paypalRef.current);
    }
  }, [amount, description, fileId, userId, onSuccess, clientId]);

  // Validación: si no está logueado, muestra aviso y no renderiza el botón de PayPal
  const isLoggedIn = !!localStorage.getItem('jwt') && !!userId;

  if (!isLoggedIn) {
    return (
      <div className="mb-4 text-lg text-red-600 font-semibold text-center">
        Debes iniciar sesión con Google para poder pagar y desbloquear archivos VIP.<br />
        <button
          className="mt-3 px-5 py-2 bg-blue-600 text-white rounded-lg font-bold shadow hover:bg-blue-700 transition"
          onClick={() => window.location.href = `${process.env.REACT_APP_API_URL}/auth/google?redirect_uri=https://codigossyste.onrender.com/archivos`}
        >
          Iniciar sesión con Google
        </button>
      </div>
    );
  }

  return (
    <div style={{ width: '100%', textAlign: 'center' }}>
      {/* Mensaje detallado de beneficios solo para suscripción VIP mensual */}
      {(amount === 4 && description === 'Suscripción VIP mensual') && (
        <div className="mb-4 text-lg text-yellow-700 font-semibold text-center">
          <span className="text-yellow-600 font-bold text-2xl">Suscripción VIP mensual</span> <span className="text-yellow-600 font-bold">$4 USD</span>
          <br />
          <span className="block mt-2 text-base text-gray-700 font-semibold">¿Qué beneficios recibes?</span>
          <ul className="list-disc list-inside text-left mt-2 text-sm text-yellow-900">
            <li>Acceso ilimitado a <b>todos los archivos VIP</b> durante 1 mes.</li>
            <li>Descarga y visualización sin anuncios ni esperas.</li>
            <li>Puedes pedir que te <b>editen cualquier archivo VIP</b> a tu gusto, gratis.</li>
            <li>Soporte prioritario por WhatsApp.</li>
            <li>Tu nombre aparecerá como <b>usuario VIP</b> en la plataforma.</li>
            <li>Recibes todas las actualizaciones y nuevos archivos VIP automáticamente.</li>
            <li>Ayudas a mantener y mejorar la plataforma.</li>
          </ul>
          <div className="mt-3 text-yellow-700 text-sm">
            <b>¡Activa tu suscripción y disfruta todos los beneficios VIP!</b>
          </div>
        </div>
      )}
      {/* Mensaje para compra de archivo VIP individual */}
      {(amount === 2 && description !== 'Suscripción VIP mensual') && (
        <div className="mb-4 text-lg text-yellow-700 font-semibold text-center">
          Archivo VIP por <span className="text-yellow-600 font-bold">$2 USD</span><br />
          Accede a este archivo VIP para siempre.<br />
          O paga la suscripción mensual de <span className="text-yellow-600 font-bold">$4 USD</span> y tendrás acceso a todos los archivos VIP y edición personalizada.
        </div>
      )}
      {/* Mensaje para donación */}
      {(description === 'Donación voluntaria') && null}
      {loading && <CircularProgress />}
      <div ref={paypalRef} style={{ marginTop: loading ? 10 : 0 }} />
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setSnackbar({ ...snackbar, open: false })} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default PayPalButton;
