import React, { useState, useEffect, useRef } from 'react';

const API_URL = process.env.REACT_APP_API_URL;

// Lista de palabras prohibidas (puedes ampliar)
const BAD_WORDS = [
  'puta','mierda','cabron','pendejo','maricon','joder','coño','culero','puto','gilipollas','zorra','imbecil','idiota','cagada','perra','tonto','estupido','malparido',
  'fuck','shit','bitch','asshole','dick','cunt','bastard','fag','motherfucker','slut','whore','jerk','idiot','stupid','retard','damn','crap','suck','pussy',
  'porn','porno','xxx','sex','sexo','nude','nudes','naked','desnudo','desnuda'
];

// Regex para enlaces/urls
const URL_REGEX = /(https?:\/\/|www\.|\.com\b|\.net\b|\.org\b|\.io\b|\.xyz\b|\.gg\b|\.me\b|\.to\b|\.ly\b|\.co\b)/i;

// Obtiene el usuario logueado desde localStorage (Google)
function getCurrentUser() {
  try {
    const userStr = localStorage.getItem('user');
    if (userStr) return JSON.parse(userStr);
    return null;
  } catch {}
  return null;
}

const GlobalChatBubble = () => {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState([]);
  // LOG: Estado inicial
  const [input, setInput] = useState('');
  const [dropdownOpen, setDropdownOpen] = useState(null);
  const [reactions, setReactions] = useState({}); // {msgId: [{emoji, count}]}
  const [errorMsg, setErrorMsg] = useState('');
  const [hasUnread, setHasUnread] = useState(false);
  const [forceUser, setForceUser] = useState(null);
  const [loadingMessages, setLoadingMessages] = useState(false);
  const [viewportHeight] = useState(window.innerHeight); // No se usa setViewportHeight
  const messagesEndRef = useRef(null);


  // Estado para el usuario actual
  const [currentUser, setCurrentUser] = useState(getCurrentUser() || forceUser);
  const isLoggedIn = !!currentUser;
  const isAdmin = currentUser && currentUser.rol === 'admin';

  // Actualiza el usuario si cambia en localStorage
  useEffect(() => {
    const handleStorage = () => {
      const user = getCurrentUser();
      setCurrentUser(user || forceUser);
    };
    window.addEventListener('storage', handleStorage);
    // También actualiza al cambiar forceUser
    setCurrentUser(getCurrentUser() || forceUser);
    return () => window.removeEventListener('storage', handleStorage);
  }, [forceUser]);

  // Actualiza el usuario en localStorage si tienes el JWT pero no el usuario
  useEffect(() => {
    if (!getCurrentUser() && localStorage.getItem('jwt')) {
      fetch(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('jwt')}` }
      })
        .then(res => res.ok ? res.json() : null)
        .then(data => {
          if (data && data.user) {
            localStorage.setItem('user', JSON.stringify(data.user));
            setForceUser(data.user);
          }
        });
    }
  }, []);

  // Cargar mensajes del backend
  // Cambia el fetchMessages para que solo actualice los mensajes si hay nuevos o si está vacío.
  // Así no borra los mensajes locales recién enviados.
  const fetchMessages = async () => {
    console.log('[GlobalChatBubble] fetchMessages: inicio');
    setLoadingMessages(true);
    try {
      const res = await fetch(`${API_URL}/chat/messages`);
      const data = await res.json();
      // Solo actualiza si hay mensajes nuevos o si no hay mensajes locales
      if (!Array.isArray(data.messages)) {
        setLoadingMessages(false);
        return;
      }
      let mensajesActualizados = false;
      if (
        messages.length === 0 ||
        (data.messages.length > 0 &&
          (!messages[messages.length - 1] ||
            data.messages[data.messages.length - 1].created_at !== messages[messages.length - 1].created_at))
      ) {
        setMessages(data.messages);
        mensajesActualizados = true;
      }
      // Detectar si hay mensajes no leídos
      if (Array.isArray(data.messages) && data.messages.length > 0) {
        const lastMsg = data.messages[data.messages.length - 1];
        const lastRead = localStorage.getItem('globalChatLastRead');
        // Si el chat está cerrado y hay mensajes nuevos
        if (!open && (!lastRead || new Date(lastMsg.created_at) > new Date(lastRead))) {
          setHasUnread(true);
        } else if (!open) {
          setHasUnread(false);
        }
      }
      // Actualiza reacciones
      if (data.reactions) {
        setReactions(data.reactions);
      }
    } catch (error) {
      // No borres los mensajes locales si hay error
    } finally {
      setLoadingMessages(false);
    }
  };

  // Guardar el último mensaje leído al abrir el chat
  useEffect(() => {
    if (open && messages.length > 0) {
      const lastMsg = messages[messages.length - 1];
      if (lastMsg?.created_at) {
        localStorage.setItem('globalChatLastRead', lastMsg.created_at);
        setHasUnread(false);
      }
    }
  }, [open, messages]);

  // Solo cargar historial inicial con fetch al abrir el chat
  useEffect(() => {
    if (open) {
      console.log('[GlobalChatBubble] useEffect open: fetchMessages');
      fetchMessages();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open]);

  // Scroll al final al recibir mensajes
  useEffect(() => {
    if (open && messagesEndRef.current) {
      console.log('[GlobalChatBubble] useEffect scroll: mensajes', messages);
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages, open]);

  // Cierra el dropdown al hacer clic fuera
  useEffect(() => {
    if (dropdownOpen === null) return;
    const handleClick = (e) => {
      // Si el click no es en el menú ni en el botón, cierra
      if (!e.target.closest('.dropdown-chat-menu') && !e.target.closest('.dropdown-chat-btn')) {
        setDropdownOpen(null);
      }
    };
    document.addEventListener('mousedown', handleClick);
    return () => document.removeEventListener('mousedown', handleClick);
  }, [dropdownOpen]);

  // Cerrar chat al hacer clic fuera del modal y deshabilitar scroll en móviles
  useEffect(() => {
    if (open) {
      // Deshabilitar scroll de la página principal en móviles
      if (window.innerWidth < 640) {
        document.body.style.overflow = 'hidden';
      }
      const handleClickOutside = (e) => {
        const modal = document.querySelector('.global-chat-modal');
        if (modal && !modal.contains(e.target)) {
          setOpen(false);
        }
      };
      document.addEventListener('mousedown', handleClickOutside);
      return () => {
        document.removeEventListener('mousedown', handleClickOutside);
        // Restaurar scroll al cerrar el chat
        if (window.innerWidth < 640) {
          document.body.style.overflow = '';
        }
      };
    }
  }, [open]);

  // Cerrar chat en móvil al presionar atrás
  useEffect(() => {
    if (!open) return;
    window.history.pushState({ chatOpen: true }, '');
    const handlePopState = () => setOpen(false);
    window.addEventListener('popstate', handlePopState);
    return () => {
      window.removeEventListener('popstate', handlePopState);
      if (window.history.state && window.history.state.chatOpen) {
        window.history.back();
      }
    };
  }, [open]);

  // Actualización en tiempo real usando WebSocket (Socket.io)
  const [socket, setSocket] = useState(null);

  // Conexión WebSocket para chat global en tiempo real
  useEffect(() => {
    if (!open) return;
    console.log('[GlobalChatBubble] useEffect WebSocket: open');
    let ioScript;
    let s;
    const connectSocket = () => {
      if (window.io && !socket) {
        s = window.io(API_URL, { transports: ['websocket'] });
        setSocket(s);
        console.log('[GlobalChatBubble] WebSocket conectado');
        s.on('chatMessage', msg => {
          console.log('[GlobalChatBubble] WebSocket chatMessage', msg);
          // Al recibir un nuevo mensaje, recarga el historial desde el backend
          fetchMessages();
        });
        s.on('chatHistory', msgs => {
          console.log('[GlobalChatBubble] WebSocket chatHistory', msgs);
          setMessages(msgs);
        });
        // Recibir reacciones en tiempo real (preparado)
        s.on('chatReaction', () => {
          console.log('[GlobalChatBubble] WebSocket chatReaction');
          // Al recibir una reacción, recarga mensajes y reacciones desde el backend
          fetchMessages();
        });
        s.emit('getChatHistory');
        console.log('[GlobalChatBubble] WebSocket emit getChatHistory');
      }
    };
    if (!window.io) {
      ioScript = document.createElement('script');
      ioScript.src = 'https://cdn.socket.io/4.5.4/socket.io.min.js';
      ioScript.async = true;
      document.body.appendChild(ioScript);
      ioScript.onload = connectSocket;
    } else {
      connectSocket();
    }
    return () => {
      if (s) s.disconnect();
      if (ioScript) ioScript.remove();
    };
    // eslint-disable-next-line
  }, [open]);

  // Enviar mensaje: WebSocket y backend
  const handleSend = async (e) => {
    e.preventDefault();
    // Si solo admin puede escribir y no es admin, no permite
    if (onlyAdminCanWrite && !isAdmin) {
      setErrorMsg('Solo los administradores pueden escribir en este chat.');
      return;
    }
    if (!isLoggedIn || !input.trim()) return;
    let censored = input;
    let foundBad = false;
    BAD_WORDS.forEach(word => {
      const regex = new RegExp(`\\b${word}\\b`, 'gi');
      if (regex.test(censored)) foundBad = true;
      censored = censored.replace(regex, '***');
    });
    if (URL_REGEX.test(censored)) {
      foundBad = true;
      censored = censored.replace(URL_REGEX, '[enlace bloqueado]');
    }
    if (foundBad) {
      setErrorMsg('Tu mensaje contiene palabras o enlaces prohibidos.');
      return;
    }
    // Enviar a backend para guardar en base de datos
    try {
      await fetch(`${API_URL}/chat/messages`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('jwt')}`
        },
        body: JSON.stringify({ text: censored })
      });
      // No emitir por WebSocket ni agregar el mensaje localmente
      // El mensaje será recibido por el evento 'chatMessage' del backend
      setInput('');
      setErrorMsg('');
    } catch {
      setErrorMsg('Error al guardar el mensaje.');
    }
  };

  // Copiar mensaje al portapapeles
  const handleCopy = (msg) => {
    navigator.clipboard.writeText(msg.text);
    setDropdownOpen(null);
  };

  // Responder (solo pone el texto en el input)
  const handleReply = (msg) => {
    setInput(`@${msg.name}: `);
    setDropdownOpen(null);
  };

  // Emojis principales
  const MAIN_EMOJIS = ['👍', '❤️', '😂', '😮', '😭', '🙏'];

  // Muestra todos los mensajes (no solo los últimos 5)
  const displayedMessages = messages;

  // Añadir emoji al input
  const handleEmojiClick = (emoji) => {
    setInput(prev => prev + emoji);
  };

  // Añadir reacción a mensaje y emitir por WebSocket
  const handleReaction = (msgId, emoji) => {
    if (socket) {
      socket.emit('chatReaction', { msgId, emoji });
    }
    // La actualización de reacciones se hará cuando el backend emita el evento y se recargue con fetchMessages
  };

  // Estado para solo admin puede escribir
  const [onlyAdminCanWrite, setOnlyAdminCanWrite] = useState(false);

  // Función para borrar mensaje (solo admin)
  const handleDeleteMessage = async (msgId) => {
    if (!isAdmin || !msgId) return;
    try {
      await fetch(`${API_URL}/chat/messages/${msgId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${localStorage.getItem('jwt')}`
        }
      });
      setDropdownOpen(null);
      fetchMessages();
    } catch {
      setErrorMsg('Error al borrar el mensaje.');
    }
  };

  // Responsive estilo Facebook Messenger
  return (
    <>
      {/* Burbuja flotante profesional */}
      <button
        className="fixed bottom-4 right-4 z-[9999] bg-gradient-to-br from-blue-900 via-blue-700 to-blue-600 text-white rounded-full shadow-xl p-4 flex items-center justify-center transition hover:scale-105"
        style={{ boxShadow: '0 6px 24px rgba(30,40,80,0.18)', position: 'fixed' }}
        onClick={() => setOpen(true)}
        aria-label="Abrir chat global"
      >
        {/* Icono de chat moderno */}
        <svg className="w-8 h-8" fill="none" stroke="currentColor" strokeWidth="2.2" viewBox="0 0 24 24">
          <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z" />
        </svg>
        {hasUnread && (
          <span
            className="absolute top-2 right-2 w-3 h-3 bg-pink-500 rounded-full border-2 border-white animate-pulse"
            style={{ zIndex: 10000 }}
          />
        )}
      </button>
      {/* Modal profesional, fondo blanco y responsivo, posición y tamaño adaptativo */}
      {open && (
        <div
          className={
            `fixed inset-0 z-[99999] flex pointer-events-none bg-black bg-opacity-30 ` +
            `sm:items-end sm:justify-end sm:bg-transparent`
          }
        >
          <div
            className={
              `relative w-full pointer-events-auto global-chat-modal flex flex-col ` +
              `sm:max-w-lg sm:m-6 sm:w-[420px] md:max-w-xl md:w-[520px] lg:max-w-2xl lg:w-[600px] xl:max-w-2xl xl:w-[650px]`
            }
            style={{
              maxWidth: window.innerWidth >= 1280 ? '650px' : window.innerWidth >= 1024 ? '600px' : window.innerWidth >= 768 ? '520px' : window.innerWidth >= 640 ? '420px' : '100vw',
              minWidth: '0',
              height: window.innerWidth < 640 ? viewportHeight : '90vh',
              minHeight: window.innerWidth < 640 ? Math.min(viewportHeight, 400) : '350px',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'flex-end',
              justifyContent: 'flex-end',
              boxSizing: 'border-box',
              overflow: 'hidden',
            }}
          >
            <div
              className="bg-white rounded-3xl shadow-2xl border border-blue-900 flex flex-col w-full h-full"
              style={{
                height: '100%',
                minHeight: window.innerWidth < 640 ? Math.min(viewportHeight, 400) : '350px',
                borderRadius: window.innerWidth < 640 ? '0 0 1.5rem 1.5rem' : '1.5rem',
                boxSizing: 'border-box',
                overflow: 'hidden',
                padding: 0,
                display: 'flex',
                flexDirection: 'column',
              }}
            >
              {/* Encabezado fijo */}
              <div className="flex items-center justify-between px-3 sm:px-7 py-3 sm:py-5 border-b border-blue-900 bg-gradient-to-r from-blue-900 via-blue-800 to-blue-700 rounded-t-3xl flex-shrink-0">
                <div className="flex items-center gap-2">
                  {/* Icono chat */}
                  <svg className="w-7 h-7 text-blue-100" fill="none" stroke="currentColor" strokeWidth="2.2" viewBox="0 0 24 24">
                    <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z" />
                  </svg>
                  <span className="font-bold text-xl text-white tracking-wide">Chat global</span>
                </div>
                <button
                  className="text-blue-200 hover:text-pink-400 text-2xl font-bold rounded-full w-9 h-9 flex items-center justify-center focus:outline-none transition hover:bg-blue-800"
                  onClick={() => setOpen(false)}
                  aria-label="Cerrar"
                  type="button"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2.5" viewBox="0 0 24 24"><path d="M6 18L18 6M6 6l12 12" /></svg>
                </button>
              </div>
              {/* Opciones extra para admin */}
              {isAdmin === true && (
                <div className="px-5 pt-2 flex gap-2 items-center flex-shrink-0">
                  <label className="text-xs font-semibold text-blue-200 flex items-center gap-2">
                    <input
                      type="checkbox"
                      checked={onlyAdminCanWrite}
                      onChange={e => setOnlyAdminCanWrite(e.target.checked)}
                      className="mr-1 accent-pink-500"
                    />
                    <svg className="w-4 h-4 text-pink-400" fill="currentColor" viewBox="0 0 24 24"><path d="M12 2a7 7 0 0 1 7 7c0 3.87-3.13 7-7 7s-7-3.13-7-7a7 7 0 0 1 7-7zm0 2a5 5 0 0 0-5 5c0 2.76 2.24 5 5 5s5-2.24 5-5a5 5 0 0 0-5-5zm0 14c-4.42 0-8 1.79-8 4v2h16v-2c0-2.21-3.58-4-8-4z"/></svg>
                    Solo admin puede escribir
                  </label>
                </div>
              )}
              {/* Botón de soporte */}
              <div className="px-3 sm:px-7 pb-2 flex justify-end flex-shrink-0">
                <a
                  href="https://wa.me/5930999562442"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-sm sm:text-base text-green-400 hover:underline font-semibold flex items-center gap-1 py-1 px-3 rounded-full border border-green-300 shadow"
                  style={{ minHeight: '36px', minWidth: '80px' }}
                  title="Soporte por WhatsApp"
                >
                  <svg className="w-5 h-5 sm:w-6 sm:h-6 text-green-400" fill="currentColor" viewBox="0 0 24 24"><path d="M17.472 14.382c-.297-.149-1.758-.867-2.028-.967-.271-.099-.468-.149-.666.15-.198.297-.767.967-.941 1.166-.173.198-.347.223-.644.075-.297-.149-1.255-.462-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.372-.025-.521-.075-.149-.666-1.611-.912-2.207-.242-.579-.487-.5-.666-.51-.173-.008-.372-.01-.571-.01-.198 0-.52.075-.792.372-.271.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.099 3.205 5.077 4.377.711.306 1.263.489 1.695.625.712.227 1.36.195 1.872.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.075-.124-.271-.198-.568-.347z"/></svg>
                  <span className="font-bold text-green-500" style={{ fontSize: '1em' }}>Soporte</span>
                </a>
              </div>
              {/* Mensajes: área scrollable y flexible */}
              <div
                className="space-y-4 overflow-y-auto px-2 sm:px-7 py-2 flex-1"
                style={{
                  flex: 1,
                  minHeight: '120px',
                  display: 'flex',
                  flexDirection: 'column',
                  gap: window.innerWidth < 640 ? '8px' : '16px',
                  paddingBottom: window.innerWidth < 640 ? '8px' : '24px',
                  overflowY: 'auto',
                  overflowX: 'hidden',
                  maxHeight: 'none',
                }}
                tabIndex={0}
              >
                {loadingMessages ? (
                  <div role="status" className="max-w-sm animate-pulse mx-auto my-6">
                    <div className="h-2.5 bg-gray-200 rounded-full dark:bg-gray-700 w-48 mb-4"></div>
                    <div className="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[360px] mb-2.5"></div>
                    <div className="h-2 bg-gray-200 rounded-full dark:bg-gray-700 mb-2.5"></div>
                    <div className="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[330px] mb-2.5"></div>
                    <div className="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[300px] mb-2.5"></div>
                    <div className="h-2 bg-gray-200 rounded-full dark:bg-gray-700 max-w-[360px]"></div>
                    <span className="sr-only">Loading...</span>
                  </div>
                ) : (
                  <>
                    {displayedMessages.map(msg => {
                      const isMine = currentUser && (String(msg.user_id) === String(currentUser.id));
                      return (
                        <div
                          key={msg.id || msg.created_at}
                          className={`flex items-end gap-2.5 group relative ${isMine ? 'justify-end' : 'justify-start'}`}
                          style={{ justifyContent: isMine ? 'flex-end' : 'flex-start' }}
                        >
                          {!isMine && (
                            <img className="w-8 h-8 sm:w-9 sm:h-9 rounded-full border-2 border-blue-900 shadow" src={msg.photo || '/docs/images/people/profile-picture-3.jpg'} alt={msg.name + ' avatar'} />
                          )}
                          <div
                            className={`flex flex-col max-w-[95vw] sm:max-w-[80%] md:max-w-[70%] leading-1.5 px-2 sm:px-4 py-2 sm:py-3 rounded-3xl shadow-lg border ${isMine ? 'text-blue-900 ml-auto border-blue-200' : 'bg-white text-blue-900 border-blue-100'}`}
                            style={{
                              alignItems: isMine ? 'flex-end' : 'flex-start',
                              marginLeft: isMine ? 'auto' : 0,
                              marginRight: isMine ? 0 : 'auto',
                              wordBreak: 'break-word',
                              fontSize: window.innerWidth < 640 ? '0.97rem' : '1rem',
                              boxShadow: isMine ? '0 2px 12px rgba(60,80,180,0.10)' : '0 2px 8px rgba(60,80,180,0.07)',
                              textAlign: isMine ? 'right' : 'left',
                              background: isMine ? '#B3E0FF' : undefined,
                            }}
                          >
                            <div className="flex items-center space-x-2 justify-between w-full">
                              <span className={`text-sm font-semibold flex items-center gap-1 ${isMine ? 'text-white' : 'text-blue-900'}`}>{msg.name}
                                {msg.modalidad === 'vip' && (
                                  <>
                                    <span className="inline-flex items-center px-2 py-0.5 ml-1 rounded-full text-xs font-bold bg-yellow-300 text-yellow-900 border border-yellow-400" style={{verticalAlign:'middle'}}>VIP</span>
                                    <svg className="w-4 h-4 text-yellow-400 ml-1" fill="currentColor" viewBox="0 0 24 24"><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/></svg>
                                  </>
                                )}
                              </span>
                              <span className="text-xs font-normal text-blue-300 flex items-center gap-1">
                                <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M12 8v4l3 3"/></svg>
                                {msg.created_at ? new Date(msg.created_at).toLocaleTimeString().slice(0,5) : ''}
                              </span>
                              {/* Menú de opciones (tres puntitos) */}
                              <div className="relative">
                                <button
                                  className="dropdown-chat-btn text-blue-400 hover:text-blue-600 px-2 py-1 rounded-full focus:outline-none"
                                  onClick={() => setDropdownOpen(dropdownOpen === msg.id ? null : msg.id)}
                                  type="button"
                                  title="Opciones"
                                >
                                  <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24"><circle cx="5" cy="12" r="2"/><circle cx="12" cy="12" r="2"/><circle cx="19" cy="12" r="2"/></svg>
                                </button>
                                {dropdownOpen === msg.id && (
                                  <div className="dropdown-chat-menu absolute right-0 top-8 bg-white border border-blue-200 rounded-xl shadow-lg flex flex-col min-w-[120px] z-50">
                                    <button
                                      className="text-left px-4 py-2 hover:bg-blue-50 text-blue-900"
                                      onClick={() => { handleReply(msg); setDropdownOpen(null); }}
                                    >Responder</button>
                                    <button
                                      className="text-left px-4 py-2 hover:bg-blue-50 text-blue-900"
                                      onClick={() => { handleCopy(msg); setDropdownOpen(null); }}
                                    >Copiar</button>
                                    {/* Botón borrar solo para admin */}
                                    {isAdmin && (
                                      <button
                                        className="text-left px-4 py-2 hover:bg-pink-50 text-pink-600 font-semibold"
                                        onClick={() => handleDeleteMessage(msg.id)}
                                      >Borrar mensaje</button>
                                    )}
                                    <div className="border-t border-blue-100 my-1" />
                                    <div className="flex gap-1 px-4 py-2">
                                      {MAIN_EMOJIS.map(emoji => (
                                        <button
                                          key={emoji}
                                          type="button"
                                          className={`text-xl px-1 py-0.5 rounded-full border-2 border-blue-100 hover:bg-blue-100 focus:outline-none transition ${reactions[msg.id || msg.created_at] === emoji ? 'bg-blue-200 border-pink-400' : ''}`}
                                          onClick={() => { handleReaction(msg.id || msg.created_at, emoji); setDropdownOpen(null); }}
                                          title={`Reaccionar con ${emoji}`}
                                        >
                                          {emoji}
                                        </button>
                                      ))}
                                      {/* Mostrar reacción actual */}
                                      {reactions[msg.id || msg.created_at] && (
                                        <span className="ml-2 text-xl">{reactions[msg.id || msg.created_at]}</span>
                                      )}
                                    </div>
                                  </div>
                                )}
                              </div>
                            </div>
                            <p className="text-base font-normal py-2 break-words" style={{fontSize:window.innerWidth<640?'0.97rem':'1rem'}}>{msg.text}</p>
                            <span className="text-xs font-normal text-blue-300">{isMine ? 'Tú' : ''}</span>
                          </div>
                          {isMine && (
                            <img className="w-8 h-8 sm:w-9 sm:h-9 rounded-full border-2 border-blue-900 shadow" src={currentUser.photo || '/docs/images/people/profile-picture-3.jpg'} alt="Tu avatar" />
                          )}
                        </div>
                      );
                    })}
                    <div ref={messagesEndRef} />
                  </>
                )}
              </div>
              {/* Input y error: parte inferior, no fija, pero visible al hacer scroll */}
              <form className="mt-4 flex gap-2 px-2 sm:px-7 pb-3 flex-shrink-0" onSubmit={handleSend}>
                <div className="flex items-center flex-1 bg-blue-50 rounded-full px-2 sm:px-4 py-2 border-2 border-blue-200 relative shadow-md">
                  <svg className="w-5 h-5 sm:w-6 sm:h-6 text-blue-400 mr-2" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><circle cx="12" cy="12" r="10"/><path d="M16 12a4 4 0 1 1-8 0 4 4 0 0 1 8 0z"/></svg>
                  <input
                    type="text"
                    className="flex-1 bg-transparent border-none outline-none text-base text-blue-900 placeholder-blue-400"
                    style={{fontSize:window.innerWidth<640?'1rem':'1.08rem'}}
                    placeholder={isLoggedIn ? "Escribe tu mensaje..." : "Inicia sesión para chatear"}
                    value={input}
                    onChange={e => { setInput(e.target.value); setErrorMsg(''); }}
                    disabled={!isLoggedIn}
                  />
                  {/* Botón de emoji para abrir menú */}
                  <button
                    type="button"
                    className="ml-1 sm:ml-2 text-2xl px-1 sm:px-2 py-1 rounded-full bg-white border border-blue-200 shadow hover:bg-blue-100 focus:outline-none"
                    onClick={() => setDropdownOpen(dropdownOpen === 'emoji' ? null : 'emoji')}
                    disabled={!isLoggedIn}
                    title="Insertar emoji"
                  >
                    😊
                  </button>
                  {/* Menú de emojis flotante */}
                  {dropdownOpen === 'emoji' && isLoggedIn && (
                    <div className="absolute bottom-12 right-0 bg-white border border-blue-200 rounded-xl shadow-lg flex gap-1 p-2 z-50">
                      {MAIN_EMOJIS.map(emoji => (
                        <button
                          key={emoji}
                          type="button"
                          className="text-2xl px-1 sm:px-2 py-1 rounded-full hover:bg-blue-100 focus:outline-none"
                          onClick={() => { handleEmojiClick(emoji); setDropdownOpen(null); }}
                          title={`Insertar ${emoji}`}
                        >
                          {emoji}
                        </button>
                      ))}
                    </div>
                  )}
                </div>
                <button
                  type="submit"
                  className={`bg-gradient-to-br from-blue-900 via-blue-700 to-blue-600 text-white px-4 sm:px-5 py-2 rounded-full font-semibold shadow hover:scale-105 transition ${!isLoggedIn || !input.trim() ? 'opacity-60 cursor-not-allowed' : ''}`}
                  disabled={!isLoggedIn || !input.trim()}
                  title="Enviar"
                >
                  <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" strokeWidth="2.5" viewBox="0 0 24 24"><path d="M5 12h14M12 5l7 7-7 7" /></svg>
                </button>
              </form>
              {/* Los emojis para insertar solo aparecen en el menú del input */}
              {errorMsg && (
                <div className="mt-2 text-xs text-pink-400 text-center font-semibold">{errorMsg}</div>
              )}
              {!isLoggedIn && (
                <div className="mt-2 text-xs text-pink-400 text-center font-semibold">
                  <svg className="w-4 h-4 inline mr-1" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M12 8v4l3 3"/></svg>
                  Debes iniciar sesión con Google para enviar mensajes.
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default GlobalChatBubble;

