import React, { useEffect, useState, useRef } from 'react';
import FileCard from './FileCard';
import { motion, AnimatePresence } from 'framer-motion';

const FileList = ({
  files,
  currentUser,
  onDelete,
  onSessionChange,
  loading,
  onLikeChange,
  page,
  setPage,
  hasMore,
  title = "Lista de todos los archivos",
  onEdit,
  purchasedIds = [],
  searchTerm,
  setSearchTerm,
  filterPago,
  setFilterPago,
}) => {
  const [topFiles, setTopFiles] = useState([]);
  const [modalFile, setModalFile] = useState(null);
  const [isCarouselPaused, setIsCarouselPaused] = useState(false);
  const [vipFiles, setVipFiles] = useState([]);
  const carouselRef = useRef(null);

  useEffect(() => {
    // Cargar los 10 archivos más populares (más descargas)
    const API_URL = process.env.REACT_APP_API_URL;
    fetch(`${API_URL}/files?limit=10&orderBy=downloads`)
      .then(res => res.ok ? res.json() : { files: [] })
      .then(data => setTopFiles(data.files || []));
  }, []);

  // Actualiza los archivos VIP cada vez que cambian los archivos o se edita algo
  // Carrusel VIP: solo los 10 últimos archivos VIP recientes (por fecha de creación)
  useEffect(() => {
    const API_URL = process.env.REACT_APP_API_URL;
    fetch(`${API_URL}/files?epago=vip&limit=1000&orderBy=created_at`)
      .then(res => res.ok ? res.json() : { files: [] })
      .then(data => {
        // Filtra explícitamente por epago === 'vip'
        let vipArr = (data.files || []).filter(f => String(f.epago).toLowerCase() === 'vip');
        // Ordena por fecha de creación descendente (más reciente primero)
        vipArr = vipArr.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
        // Toma solo los últimos 10 (más recientes)
        vipArr = vipArr.slice(0, 10);
        setVipFiles(vipArr);
      });
  }, [files]); // <-- depende de files

  useEffect(() => {
    if (!carouselRef.current) return;
    let frameId;
    let direction = 1;
    const speed = 0.5;

    const animate = () => {
      if (!carouselRef.current || isCarouselPaused) {
        frameId = requestAnimationFrame(animate);
        return;
      }

      const scrollContainer = carouselRef.current;
      scrollContainer.scrollLeft += speed * direction;

      if (scrollContainer.scrollLeft >= scrollContainer.scrollWidth - scrollContainer.clientWidth) {
        direction = -1;
      } else if (scrollContainer.scrollLeft <= 0) {
        direction = 1;
      }

      frameId = requestAnimationFrame(animate);
    };

    frameId = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(frameId);
  }, [isCarouselPaused]);

  // Elimina localFiles y filtra directamente sobre files
  // const [localFiles, setLocalFiles] = useState(files);
  // useEffect(() => { setLocalFiles(files); }, [files]);


  // Filtra los archivos según el buscador y el combo box
  const filteredFiles = files.filter(file => {
    const matchName = !searchTerm || (file.filename && file.filename.toLowerCase().includes(searchTerm.toLowerCase()));
    let matchPago = true;
    if (filterPago === 'vip') matchPago = String(file.epago).toLowerCase() === 'vip';
    else if (filterPago === 'gratuito') matchPago = String(file.epago).toLowerCase() === 'gratuito';
    return matchName && matchPago;
  });

  // Banner Adsterra: evitar reinicio en cada render
  const bannerRef = useRef(null);
  useEffect(() => {
    if (bannerRef.current) {
      bannerRef.current.innerHTML = '';
      window.atOptions = {
        key: 'e4c11c1ea1cc54b5fdfd61ef580a6a7b',
        format: 'iframe',
        height: 60,
        width: 468,
        params: {}
      };
      const script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = '//felllilyforegoing.com/e4c11c1ea1cc54b5fdfd61ef580a6a7b/invoke.js';
      script.id = 'adsterra-banner-filelist';
      bannerRef.current.appendChild(script);
    }
    // Limpia el banner al desmontar
    const bannerNode = bannerRef.current;
    return () => {
      if (bannerNode) bannerNode.innerHTML = '';
      Array.from(document.querySelectorAll('#adsterra-banner-filelist')).forEach(s => s.remove());
    };
  }, []);

  // Maneja la actualización local del archivo editado
  const handleEditLocal = (updatedFile) => {
    // Actualiza la lista localmente si el archivo editado existe
    if (updatedFile && updatedFile.id) {
      // Si files viene del padre y se actualiza por refetch, solo llama a onEdit
      if (onEdit) onEdit(updatedFile);
      // Si quieres actualizar localmente la lista sin refetch global:
      // files = files.map(f => f.id === updatedFile.id ? updatedFile : f);
    }
  };

  // Maneja el historial para el modal de detalles
  useEffect(() => {
    if (modalFile) {
      window.history.pushState({ modal: true }, '');
      const handlePopState = () => {
        setModalFile(null);
        setIsCarouselPaused(false);
      };
      window.addEventListener('popstate', handlePopState);
      return () => {
        window.removeEventListener('popstate', handlePopState);
        if (window.history.state && window.history.state.modal) {
          window.history.back();
        }
      };
    }
  }, [modalFile]);


  return (
    <div className="relative">
      {/* Carrusel de archivos más populares */}
      <section className="w-full py-6 px-1 md:px-4 mb-8 bg-gradient-to-r from-purple-50 via-blue-50 to-white rounded-2xl shadow-inner">
        <h2 className="text-xl md:text-2xl font-bold text-purple-700 mb-3 text-center tracking-tight">
          Top 10 archivos más descargados
        </h2>
        <div className="relative">
          <div
            ref={carouselRef}
            className="popular-carousel flex gap-6 overflow-x-auto scroll-smooth scrollbar-thin scrollbar-thumb-purple-400 scrollbar-track-purple-100"
            onMouseEnter={() => setIsCarouselPaused(true)}
            onMouseLeave={() => setIsCarouselPaused(false)}
            onFocus={() => setIsCarouselPaused(true)}
            onBlur={() => setIsCarouselPaused(false)}
            onScroll={() => setIsCarouselPaused(true)}
            tabIndex={0}
            style={{ minHeight: 220, padding: '8px 0', WebkitOverflowScrolling: 'touch' }}
          >
            {[...topFiles, ...topFiles].map((file, idx) => (
              <div
                key={file.id + '-' + idx}
                className="min-w-[300px] max-w-[340px] flex-shrink-0 hover:scale-105 transition-transform duration-200"
                draggable={false}
                onDragStart={e => e.preventDefault()}
                onClick={e => { e.stopPropagation(); setModalFile(file); setIsCarouselPaused(true); }}
                style={{ pointerEvents: modalFile ? 'none' : 'auto' }}
                aria-label={`Ver detalles de ${file.filename}`}
              >
                <FileCard file={file} currentUser={currentUser} disableModal={true} />
              </div>
            ))}
          </div>
        </div>
        <AnimatePresence>
          {modalFile && (
            <motion.div
              className="fixed inset-0 z-[999] flex items-center justify-center bg-black bg-opacity-40"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={() => { setModalFile(null); setIsCarouselPaused(false); }}
            >
              <motion.div
                className="bg-white rounded-2xl shadow-2xl border p-6 w-full max-w-md mx-2 relative flex flex-col"
                initial={{ scale: 0.95, y: 30, opacity: 0 }}
                animate={{ scale: 1, y: 0, opacity: 1 }}
                exit={{ scale: 0.95, y: 30, opacity: 0 }}
                transition={{ duration: 0.25 }}
                onClick={e => e.stopPropagation()}
              >
                <button
                  className="absolute top-3 right-3 text-gray-400 hover:text-purple-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
                  onClick={() => { setModalFile(null); setIsCarouselPaused(false); }}
                  aria-label="Cerrar"
                  type="button"
                >
                  ×
                </button>
                <FileCard file={modalFile} currentUser={currentUser} />
              </motion.div>
            </motion.div>
          )}
        </AnimatePresence>
      </section>

      {/* Carrusel horizontal de archivos VIP recientes, visible en cualquier ruta */}
      {vipFiles.length > 0 && (
        <section className="w-full py-6 px-1 md:px-4 mb-8 bg-gradient-to-r from-yellow-50 via-yellow-100 to-white rounded-2xl shadow-inner">
          <h2 className="text-xl md:text-2xl font-bold text-yellow-700 mb-3 text-center tracking-tight">
            Archivos VIP recientes
          </h2>
          <div className="relative">
            <div
              className="vip-carousel flex gap-6 overflow-x-auto scroll-smooth scrollbar-thin scrollbar-thumb-yellow-400 scrollbar-track-yellow-100"
              style={{
                minHeight: 220,
                padding: '8px 0',
                WebkitOverflowScrolling: 'touch',
              }}
            >
              {vipFiles.map((file, idx) => (
                <div
                  key={file.id + '-' + idx}
                  className="min-w-[300px] max-w-[340px] flex-shrink-0 hover:scale-105 transition-transform duration-200"
                  draggable={false}
                  onDragStart={e => e.preventDefault()}
                  onClick={e => {
                    e.stopPropagation();
                    setModalFile(file);
                    setIsCarouselPaused(true);
                  }}
                  style={{ pointerEvents: modalFile ? 'none' : 'auto' }}
                  aria-label={`Ver detalles de ${file.filename}`}
                >
                  <FileCard
                    file={file}
                    currentUser={currentUser}
                    disableModal={true}
                  />
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Panel de título y buscador integrado */}
      <div className="w-full max-w-5xl mx-auto mb-2 px-2">
        <div className="bg-white rounded-xl shadow border border-gray-100 p-4 text-center flex flex-col md:flex-row md:items-center md:justify-between gap-4">
          <h3 className="font-semibold text-purple-700 mb-2 text-lg md:mb-0">
            Lista de todos los archivos
          </h3>
          <div className="flex flex-row gap-2 w-full md:w-auto justify-center">
            <input
              type="text"
              placeholder="Buscar por nombre de archivo..."
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              className="border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500 transition shadow bg-gradient-to-r from-blue-50 via-white to-purple-50 w-full md:w-64"
              autoComplete="off"
            />
            <select
              value={filterPago}
              onChange={e => setFilterPago(e.target.value)}
              className="border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-yellow-400 transition bg-white w-full md:w-40"
            >
              <option value="todos">Todos</option>
              <option value="gratuito">Gratuito</option>
              <option value="vip">VIP</option>
            </select>
          </div>
        </div>
      </div>
      {/* Banner Adsterra integrado debajo del panel (no se reinicia) */}
      <div className="w-full flex justify-center items-center mb-6">
        <div
          ref={bannerRef}
          className="w-full max-w-[468px] min-h-[60px] bg-gray-50 border border-gray-200 rounded-lg overflow-hidden flex items-center justify-center"
          style={{ minHeight: 60 }}
        />
      </div>

      <div className="overflow-x-auto pb-2 bg-gradient-to-r from-blue-50 via-white to-purple-50 rounded-xl">
        <div
          className="grid grid-cols-1 sm:grid-cols-2 gap-6 min-h-[120px] w-full max-w-5xl mx-auto py-4 px-2"
          style={{ maxHeight: '60vh', overflowY: 'auto' }}
        >
          {loading ? (
            <div className="col-span-2 flex justify-center items-center py-10">
              <div className="animate-spin rounded-full h-10 w-10 border-t-2 border-b-2 border-blue-600"></div>
              <span className="ml-3 text-blue-600 font-semibold">Cargando archivos...</span>
            </div>
          ) : filteredFiles.length === 0 ? (
            <div className="col-span-2 text-center text-gray-400 py-10">No hay archivos para mostrar.</div>
          ) : (
            <AnimatePresence>
              {filteredFiles.map((file, idx) => (
                <motion.div
                  key={file.id}
                  initial={{ opacity: 0, x: -30, scale: 0.97 }}
                  animate={{ opacity: 1, x: 0, scale: 1 }}
                  exit={{ opacity: 0, x: -30, scale: 0.97 }}
                  transition={{ duration: 0.3, delay: idx * 0.03 }}
                >
                  <FileCard
                    file={file}
                    currentUser={currentUser}
                    onDelete={onDelete}
                    onSessionChange={onSessionChange}
                    onLikeChange={onLikeChange}
                    onEdit={handleEditLocal}
                    hasPurchased={purchasedIds.includes(file.id)}
                    disableModal={false} // <-- Asegura que el modal de anuncio se muestre en la lista principal
                  />
                </motion.div>
              ))}
            </AnimatePresence>
          )}
        </div>
      </div>

      {(hasMore || page > 1) && (
        <div className="flex justify-center items-center gap-2 bg-white/80 rounded-lg shadow px-2 py-1 mt-4">
          <button
            className="px-2 py-1 text-sm bg-gray-200 rounded hover:bg-blue-200 transition"
            disabled={page === 1}
            onClick={() => setPage(page - 1)}
          >
            &larr;
          </button>
          <span className="text-xs text-gray-600 font-semibold">Página {page}</span>
          <button
            className="px-2 py-1 text-sm bg-gray-200 rounded hover:bg-blue-200 transition"
            disabled={!hasMore}
            onClick={() => setPage(page + 1)}
          >
            &rarr;
          </button>
        </div>
      )}
    </div>
  );
};

export default FileList;

