import React, { useEffect, useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import FileList from '../components/FileList';
import FileUpload from '../components/FileUpload';

const PAGE_SIZE = 20;

const PerfilPage = () => {
  const [user, setUser] = useState(null);
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showUpload, setShowUpload] = useState(false);
  const [alert, setAlert] = useState('');
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(false);
  const [earnings, setEarnings] = useState(0);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterPago, setFilterPago] = useState('todos');
  const API_URL = process.env.REACT_APP_API_URL;
  const bannerRef = useRef(null);

  async function fetchWithAuth(url, options = {}) {
    let token = localStorage.getItem('jwt');
    let response = await fetch(url, {
      ...options,
      headers: {
        ...(options.headers || {}),
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      credentials: 'include'
    });

    if (response.status === 401) {
      const refreshRes = await fetch(`${process.env.REACT_APP_API_URL}/auth/refresh`, {
        method: 'POST',
        credentials: 'include'
      });
      if (refreshRes.ok) {
        const data = await refreshRes.json();
        token = data.accessToken;
        localStorage.setItem('jwt', token);
        response = await fetch(url, {
          ...options,
          headers: {
            ...(options.headers || {}),
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          credentials: 'include'
        });
      } else {
        window.location.href = '/login';
      }
    }
    return response;
  }

  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetchWithAuth(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` }
      })
        .then(res => res.json())
        .then(data => setUser(data.user || null))
        .catch(() => setUser(null));
    }
  }, [API_URL]);

  const refreshFiles = async (pageNum) => {
    if (!user) return;
    setLoading(true);
    try {
      const res = await fetchWithAuth(
        `${API_URL}/files?user=${encodeURIComponent(user.name)}&page=${pageNum}&limit=${PAGE_SIZE}`
      );
      if (!res.ok) throw new Error('Error fetching files');
      const data = await res.json();
      setFiles(data.files || []);
      setHasMore(data.hasMore);
    } catch (error) {
      console.error('Error cargando archivos:', error);
      setFiles([]);
      setHasMore(false);
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    refreshFiles(page);
    // eslint-disable-next-line
  }, [API_URL, user, page]);

  useEffect(() => {
    if (!user) {
      setEarnings(0);
      return;
    }
    const fetchEarnings = async () => {
      try {
        const res = await fetch(`${API_URL}/files?user=${encodeURIComponent(user.name)}&vip=true&limit=1000`);
        const data = await res.json();
        let total = 0;
        (data.files || []).forEach(f => {
          if (f.vip) {
            total += (Number(f.downloads) || 0) * 0.9;
          }
        });
        setEarnings(total);
      } catch {
        setEarnings(0);
      }
    };
    fetchEarnings();
    const interval = setInterval(fetchEarnings, 5000);
    return () => clearInterval(interval);
  }, [user, API_URL]);

  const handleUploadClick = () => {
    if (user) {
      setShowUpload(true);
      setAlert('');
    } else {
      setShowLoginModal(true);
      setAlert('');
    }
  };

  const LoginModal = () => (
    <AnimatePresence>
      <motion.div
        className="fixed inset-0 bg-black bg-opacity-40 z-50 flex items-center justify-center"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          className="bg-white rounded-xl shadow-2xl p-8 max-w-xs w-full flex flex-col items-center relative border border-purple-200"
          initial={{ scale: 0.95, y: -20, opacity: 0 }}
          animate={{ scale: 1, y: 0, opacity: 1 }}
          exit={{ scale: 0.95, y: -20, opacity: 0 }}
        >
          <button
            className="absolute top-3 right-3 text-gray-400 hover:text-purple-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
            onClick={() => setShowLoginModal(false)}
            aria-label="Cerrar"
            type="button"
          >
            ×
          </button>
          <svg className="w-16 h-16 text-purple-400 mb-3" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
            <path d="M12 9v2m0 4h.01M21 12c0 4.97-4.03 9-9 9s-9-4.03-9-9 4.03-9 9-9 9 4.03 9 9z" />
          </svg>
          <div className="text-xl font-bold text-purple-700 mb-2 text-center">Debes iniciar sesión</div>
          <div className="text-gray-600 text-center mb-5">
            Para subir archivos HTML necesitas iniciar sesión con Google.
          </div>
          <button
            className="gsi-material-button w-full"
            onClick={() => {
              setShowLoginModal(false);
              window.location.href = `${process.env.REACT_APP_API_URL}/auth/google`;
            }}
            type="button"
          >
            <div className="gsi-material-button-state"></div>
            <div className="gsi-material-button-content-wrapper">
              <div className="gsi-material-button-icon">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" style={{ display: 'block' }}>
                  <path fill="#EA4335" d="M24 9.5c3.54 0 6.71 1.22 9.21 3.6l6.85-6.85C35.9 2.38 30.47 0 24 0 14.62 0 6.51 5.38 2.56 13.22l7.98 6.19C12.43 13.72 17.74 9.5 24 9.5z"></path>
                  <path fill="#4285F4" d="M46.98 24.55c0-1.57-.15-3.09-.38-4.55H24v9.02h12.94c-.58 2.96-2.26 5.48-4.78 7.18l7.73 6c4.51-4.18 7.09-10.36 7.09-17.65z"></path>
                  <path fill="#FBBC05" d="M10.53 28.59c-.48-1.45-.76-2.99-.76-4.59s.27-3.14.76-4.59l-7.98-6.19C.92 16.46 0 20.12 0 24c0 3.88.92 7.54 2.56 10.78l7.97-6.19z"></path>
                  <path fill="#34A853" d="M24 48c6.48 0 11.93-2.13 15.89-5.81l-7.73-6c-2.15 1.45-4.92 2.3-8.16 2.3-6.26 0-11.57-4.22-13.47-9.91l-7.98 6.19C6.51 42.62 14.62 48 24 48z"></path>
                  <path fill="none" d="M0 0h48v48H0z"></path>
                </svg>
              </div>
              <span className="gsi-material-button-contents">Iniciar sesión con Google</span>
              <span style={{ display: 'none' }}>Iniciar sesión con Google</span>
            </div>
          </button>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );

  useEffect(() => {
    const handleLogin = () => {
      const jwt = localStorage.getItem('jwt');
      if (jwt) {
        fetch(`${API_URL}/auth/me`, {
          headers: { Authorization: `Bearer ${jwt}` }
        })
          .then(res => res.json())
          .then(data => setUser(data.user || null))
          .catch(() => setUser(null));
      } else {
        setUser(null);
      }
    };
    window.addEventListener('storage', handleLogin);
    window.addEventListener('login', handleLogin);
    return () => {
      window.removeEventListener('storage', handleLogin);
      window.removeEventListener('login', handleLogin);
    };
  }, [API_URL]);

  const handleLogout = () => {
    setUser(null);
    localStorage.removeItem('jwt');
    window.location.href = '/';
  };

  const isUserVip = user && String(user.modalidad).toLowerCase() === 'vip';

  return (
    <div className="px-6 py-10 max-w-4xl mx-auto relative">
      {user && isUserVip && (
        <div className="absolute top-4 right-4 bg-yellow-100 border border-yellow-300 rounded-xl px-5 py-2 shadow text-yellow-800 font-bold text-lg flex items-center gap-2 z-20">
          <svg className="w-6 h-6 text-yellow-500" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12 1C5.925 1 1 5.925 1 12s4.925 11 11 11 11-4.925 11-11S18.075 1 12 1zm0 20c-4.963 0-9-4.037-9-9s4.037-9 9-9 9 4.037 9 9-4.037 9-9 9zm.5-13h-1v1.09c-1.71.14-3 1.52-3 3.16 0 1.66 1.34 3 3 3 .55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1H9c0 1.3.84 2.4 2 2.82V17h1v-1.18c1.16-.42 2-1.52 2-2.82 0-1.66-1.34-3-3-3-.55 0-1-.45-1-1s.45-1 1-1 1 .45 1 1h1c0-1.3-.84-2.4-2-2.82V7z"/>
          </svg>
          <span>Ganancias: ${earnings.toFixed(2)} USD</span>
        </div>
      )}
      <div className="flex flex-col md:flex-row md:justify-between md:items-center mb-6 gap-4">
        <h1 className="text-3xl font-bold text-gray-900">Mi Perfil</h1>
        <button
          className={`bg-gradient-to-r from-blue-500 to-purple-500 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-blue-700 transition ${!user ? 'opacity-60 cursor-not-allowed' : ''}`}
          onClick={handleUploadClick}
          disabled={false}
        >
          Subir archivos
        </button>
      </div>
      {alert && (
        <div className="mb-4 text-red-600 font-semibold text-center">{alert}</div>
      )}
      {showUpload && (
        <FileUpload
          onUpload={() => {
            setShowUpload(false);
            refreshFiles();
          }}
          onClose={() => setShowUpload(false)}
          currentUser={user}
          forceVip={false}
        />
      )}
      {user && (
        <div className="flex flex-col md:flex-row items-center gap-8 mb-10 bg-gray-50 rounded-xl p-6 shadow">
          <img src={user.photo} alt={user.name} className="w-24 h-24 rounded-full border-4 border-gray-200 shadow" />
          <div>
            <div className="text-xl font-semibold text-gray-800">{user.name}</div>
            <div className="text-gray-500">{user.email}</div>
            <div className="mt-2 text-sm text-gray-600">
              <span className="bg-gray-200 px-2 py-0.5 rounded">{user.rol || 'miembro'}</span>
            </div>
            <div className="mt-4 text-sm text-gray-700">
              <strong>Archivos subidos:</strong> {files.length}
            </div>
          </div>
        </div>
      )}
      <h2 className="text-2xl font-semibold text-gray-800 mb-4">Mis archivos HTML</h2>
      <FileList
        files={files}
        currentUser={user}
        loading={loading}
        onLikeChange={refreshFiles}
        page={page}
        setPage={setPage}
        hasMore={hasMore}
        searchTerm={searchTerm}
        setSearchTerm={setSearchTerm}
        filterPago={filterPago}
        setFilterPago={setFilterPago}
        onEdit={() => refreshFiles(page)}
      />
      {showLoginModal && <LoginModal />}
    </div>
  );
};

export default PerfilPage;
    