import React, { useEffect, useState } from 'react';
import FileList from '../components/FileList';
import FileUpload from '../components/FileUpload';
import { motion, AnimatePresence } from 'framer-motion';
import PayPalButton from '../components/PayPalButton';

const VipPage = () => {
  const [vipFiles, setVipFiles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showUpload, setShowUpload] = useState(false);
  const [user, setUser] = useState(null);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [earnings, setEarnings] = useState(0);
  const [pendingUpload, setPendingUpload] = useState(false);
  const [showSubscribeModal, setShowSubscribeModal] = useState(false);
  const API_URL = process.env.REACT_APP_API_URL;

  // Cargar archivos VIP
  const fetchVipFiles = () => {
    setLoading(true);
    fetch(`${API_URL}/files?epago=vip&limit=1000`)
      .then(res => res.ok ? res.json() : { files: [] })
      .then(data => {
        setVipFiles((data.files || []).filter(f => String(f.epago).toLowerCase() === 'vip'));
      })
      .finally(() => setLoading(false));
  };

  useEffect(() => {
    fetchVipFiles();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [API_URL]);

  // Cargar usuario y ganancias
  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetch(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` }
      })
        .then(res => res.json())
        .then(data => {
          // Asegúrate de que modalidad se reciba correctamente
          setUser(data.user || null);
          // Debug: muestra el objeto user en consola
          console.log('Usuario cargado:', data.user);
        })
        .catch(() => setUser(null));
    } else {
      setUser(null);
    }
  }, [API_URL]);

  // Calcular ganancias del usuario (90% de descargas de sus archivos VIP)
  useEffect(() => {
    if (!user) {
      setEarnings(0);
      return;
    }
    const fetchEarnings = async () => {
      try {
        const res = await fetch(`${API_URL}/files?user=${encodeURIComponent(user.name)}&vip=true&limit=1000`);
        const data = await res.json();
        let total = 0;
        (data.files || []).forEach(f => {
          if (f.vip) {
            total += (Number(f.downloads) || 0) * 0.9; // 0.9 USD por descarga
          }
        });
        setEarnings(total);
      } catch {
        setEarnings(0);
      }
    };
    fetchEarnings();
    const interval = setInterval(fetchEarnings, 5000); // Actualiza cada 5s
    return () => clearInterval(interval);
  }, [user, API_URL]);

  const handleUploadSuccess = () => {
    setShowUpload(false);
    setPendingUpload(false);
    fetchVipFiles(); // <-- Refresca la lista tras subir
  };

  // Maneja edición de archivo (desde FileCard)
  const handleFileEdited = () => {
    fetchVipFiles(); // <-- Refresca la lista tras editar
  };

  const handleUploadClick = () => {
    if (user) {
      // Si el usuario ya pagó, abre el FileUpload directamente
      if (pendingUpload) {
        setShowUpload(true);
        setPendingUpload(false);
        return;
      }
      setShowUpload(show => !show);
    } else {
      setShowLoginModal(true);
    }
  };

  // Modal de advertencia para login Google
  const LoginModal = () => (
    <AnimatePresence>
      <motion.div
        className="fixed inset-0 bg-black bg-opacity-40 z-50 flex items-center justify-center"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          className="bg-white rounded-xl shadow-2xl p-8 max-w-xs w-full flex flex-col items-center relative border border-yellow-200"
          initial={{ scale: 0.95, y: -20, opacity: 0 }}
          animate={{ scale: 1, y: 0, opacity: 1 }}
          exit={{ scale: 0.95, y: -20, opacity: 0 }}
        >
          <button
            className="absolute top-3 right-3 text-gray-400 hover:text-yellow-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
            onClick={() => setShowLoginModal(false)}
            aria-label="Cerrar"
            type="button"
          >
            ×
          </button>
          <svg
            className="w-16 h-16 text-yellow-400 mb-3"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            viewBox="0 0 24 24"
          >
            <path d="M12 9v2m0 4h.01M21 12c0 4.97-4.03 9-9 9s-9-4.03-9-9 4.03-9 9-9 9 4.03 9 9z" />
          </svg>
          <div className="text-xl font-bold text-yellow-700 mb-2 text-center">
            Debes iniciar sesión
          </div>
          <div className="text-gray-600 text-center mb-5">
            Para subir archivos VIP necesitas iniciar sesión con Google.
          </div>
          <button
            className="gsi-material-button w-full"
            onClick={() => {
              setShowLoginModal(false);
              window.location.href = `${process.env.REACT_APP_API_URL}/auth/google`;
            }}
            type="button"
          >
            <div className="gsi-material-button-state"></div>
            <div className="gsi-material-button-content-wrapper">
              <div className="gsi-material-button-icon">
                <svg
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 48 48"
                  style={{ display: 'block' }}
                >
                  <path
                    fill="#EA4335"
                    d="M24 9.5c3.54 0 6.71 1.22 9.21 3.6l6.85-6.85C35.9 2.38 30.47 0 24 0 14.62 0 6.51 5.38 2.56 13.22l7.98 6.19C12.43 13.72 17.74 9.5 24 9.5z"
                  ></path>
                  <path
                    fill="#4285F4"
                    d="M46.98 24.55c0-1.57-.15-3.09-.38-4.55H24v9.02h12.94c-.58 2.96-2.26 5.48-4.78 7.18l7.73 6c4.51-4.18 7.09-10.36 7.09-17.65z"
                  ></path>
                  <path
                    fill="#FBBC05"
                    d="M10.53 28.59c-.48-1.45-.76-2.99-.76-4.59s.27-3.14.76-4.59l-7.98-6.19C.92 16.46 0 20.12 0 24c0 3.88.92 7.54 2.56 10.78l7.97-6.19z"
                  ></path>
                  <path
                    fill="#34A853"
                    d="M24 48c6.48 0 11.93-2.13 15.89-5.81l-7.73-6c-2.15 1.45-4.92 2.3-8.16 2.3-6.26 0-11.57-4.22-13.47-9.91l-7.98 6.19C6.51 42.62 14.62 48 24 48z"
                  ></path>
                  <path fill="none" d="M0 0h48v48H0z"></path>
                </svg>
              </div>
              <span className="gsi-material-button-contents">
                Iniciar sesión con Google
              </span>
              <span style={{ display: 'none' }}>Iniciar sesión con Google</span>
            </div>
          </button>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );

  // Al cargar la página, si el usuario está logueado y tiene pendiente subir, muestra el FileUpload automáticamente
  useEffect(() => {
    if (user && pendingUpload) {
      setShowUpload(true);
      setPendingUpload(false);
    }
  }, [user, pendingUpload]);

  // Solo muestra el botón de subir VIP si el usuario es VIP
  const isUserVip = user && String(user.modalidad).toLowerCase() === 'vip';

  // Nuevo: IDs de archivos VIP comprados por el usuario (solo para no VIP)
  const [purchasedIds, setPurchasedIds] = useState([]);

  // Cargar IDs de archivos VIP comprados por el usuario (solo si no es VIP)
  useEffect(() => {
    if (!user || isUserVip) {
      setPurchasedIds([]);
      return;
    }
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      fetch(`${API_URL}/files?epago=vip&limit=1000`, {
        headers: { Authorization: `Bearer ${jwt}` }
      })
        .then(res => res.ok ? res.json() : { files: [] })
        .then(async data => {
          const files = data.files || [];
          // Consulta para cada archivo si está comprado
          const checks = await Promise.all(
            files.map(f =>
              fetch(`${API_URL}/files/${f.id}/purchased`, {
                headers: { Authorization: `Bearer ${jwt}` }
              })
                .then(res => res.ok ? res.json() : { purchased: false })
                .then(d => d.purchased ? f.id : null)
            )
          );
          setPurchasedIds(checks.filter(Boolean));
        });
    }
  }, [user, API_URL, isUserVip]);

  // Suscripción: función para actualizar modalidad tras pago
  const handleSubscriptionSuccess = async () => {
    try {
      const jwt = localStorage.getItem('jwt');
      await fetch(`${API_URL}/users/subscribe-vip`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${jwt}` },
      });
      // Refresca usuario
      fetch(`${API_URL}/auth/me`, {
        headers: { Authorization: `Bearer ${jwt}` },
      })
        .then((res) => res.json())
        .then((data) => setUser(data.user || null));
      setShowSubscribeModal(false);
      alert('¡Ahora eres usuario VIP por 1 mes!');
    } catch {
      alert('Error al activar la suscripción VIP.');
    }
  };

  // Maneja el historial para el modal de suscripción
  useEffect(() => {
    if (showSubscribeModal) {
      window.history.pushState({ modal: true }, '');
      const handlePopState = () => setShowSubscribeModal(false);
      window.addEventListener('popstate', handlePopState);
      return () => {
        window.removeEventListener('popstate', handlePopState);
        if (window.history.state && window.history.state.modal) {
          window.history.back();
        }
      };
    }
  }, [showSubscribeModal]);

  return (
    <div className="flex flex-col items-center justify-center min-h-[60vh] relative">
      {/* Dinero ganado arriba a la derecha */}
      {user && isUserVip && (
        <div className="fixed md:absolute top-4 right-4 z-20">
          <div className="bg-yellow-100 border border-yellow-300 rounded-xl px-4 py-2 shadow text-yellow-800 font-bold text-base md:text-lg flex items-center gap-2">
            <svg className="w-5 h-5 md:w-6 md:h-6 text-yellow-500" fill="currentColor" viewBox="0 0 24 24">
              <path d="M12 1C5.925 1 1 5.925 1 12s4.925 11 11 11 11-4.925 11-11S18.075 1 12 1zm0 20c-4.963 0-9-4.037-9-9s4.037-9 9-9 9 4.037 9 9-4.037 9-9 9zm.5-13h-1v1.09c-1.71.14-3 1.52-3 3.16 0 1.66 1.34 3 3 3 .55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1H9c0 1.3.84 2.4 2 2.82V17h1v-1.18c1.16-.42 2-1.52 2-2.82 0-1.66-1.34-3-3-3-.55 0-1-.45-1-1s.45-1 1-1 1 .45 1 1h1c0-1.3-.84-2.4-2-2.82V7z"/>
            </svg>
            <span className="whitespace-nowrap">Ganancias: ${earnings.toFixed(2)} USD</span>
          </div>
        </div>
      )}
      <h1 className="text-4xl font-extrabold text-yellow-500 mb-4 text-center drop-shadow-lg">
        Archivos VIP
      </h1>
      <p className="text-lg text-gray-600 text-center max-w-xl mb-4">
        Solo los archivos VIP aparecen aquí. <br />
        <span className="font-semibold text-yellow-700"></span>
      </p>
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-6 max-w-2xl text-yellow-800 text-center shadow">
        <strong>¿Cómo funciona?</strong>
        <ul className="list-disc list-inside text-left mt-2 text-sm text-yellow-900">
          <li>Solo los usuarios VIP pueden subir archivos VIP.</li>
          <li>Los archivos VIP solo se obtienen pagando individualmente cada uno por 2$ dolar o Suscribiendote mensualmente por 4$ dolares.</li>
          <li>Con la suscripción puedes pedirle exclusivamente al propietario de la web que te edite cualquier archivo a tu gusto.</li>
        </ul>
      </div>
      {/* Solo muestra el botón si el usuario es VIP */}
      {isUserVip && (
        <div className="mb-6 flex justify-center">
          <button
            className={`bg-gradient-to-r from-yellow-400 to-yellow-600 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-yellow-700 transition`}
            onClick={handleUploadClick}
            disabled={!user}
          >
            {showUpload ? 'Cancelar' : 'Subir archivo VIP'}
          </button>
        </div>
      )}
      {/* Si el usuario es gratuito, muestra el botón Suscribirse */}
      {!isUserVip && user && (
        <div className="mb-6 flex justify-center">
          <button
            className="bg-gradient-to-r from-yellow-400 to-yellow-600 text-white px-6 py-2 rounded-lg font-semibold shadow hover:bg-yellow-700 transition"
            onClick={() => setShowSubscribeModal(true)}
          >
            Suscribirse
          </button>
        </div>
      )}
      {/* Modal de suscripción VIP SOLO con PayPalButton */}
      <AnimatePresence>
        {showSubscribeModal && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-40">
            <div className="relative p-4 w-full max-w-2xl max-h-full">
              <div className="relative bg-white rounded-lg shadow-sm">
                <div className="flex items-center justify-between p-4 border-b rounded-t border-gray-200">
                  <h3 className="text-xl font-semibold text-gray-900">
                    Suscripción VIP mensual
                  </h3>
                  <button
                    type="button"
                    className="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ms-auto inline-flex justify-center items-center"
                    onClick={() => setShowSubscribeModal(false)}
                    aria-label="Cerrar"
                  >
                    <svg className="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 14">
                      <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"/>
                    </svg>
                    <span className="sr-only">Cerrar modal</span>
                  </button>
                </div>
                <div className="p-4 space-y-4">
                  <PayPalButton
                    amount={4.00}
                    description={'Suscripción VIP mensual'}
                    fileId={null}
                    userId={user?.id}
                    clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
                    onSuccess={handleSubscriptionSuccess}
                  />
                </div>
                <div className="flex items-center p-4 border-t border-gray-200 rounded-b">
                  {/* Botón de cerrar o acción extra si lo necesitas */}
                </div>
              </div>
            </div>
          </div>
        )}
      </AnimatePresence>
      {showUpload && user && isUserVip && (
        <div className="mb-8 w-full max-w-md mx-auto">
          <FileUpload
            onUpload={handleUploadSuccess}
            onClose={() => setShowUpload(false)}
            currentUser={user}
            forceVip={true}
          />
        </div>
      )}
      {showLoginModal && <LoginModal />}
      {/* Contacto WhatsApp para usuarios con VIP */}
      {user && (
        <div className="mb-4 flex flex-col items-center">
          <div className="bg-green-50 border border-green-200 rounded-lg px-4 py-3 text-green-800 text-center max-w-lg shadow">
            <div className="font-semibold mb-1 flex items-center justify-center gap-2">
              <svg className="w-5 h-5 text-green-500" fill="currentColor" viewBox="0 0 24 24">
                <path d="M20.52 3.48A11.94 11.94 0 0012 0C5.37 0 0 5.37 0 12c0 2.12.55 4.13 1.61 5.93L0 24l6.18-1.62A11.94 11.94 0 0012 24c6.63 0 12-5.37 12-12 0-3.19-1.24-6.19-3.48-8.52zM12 22c-1.85 0-3.63-.5-5.18-1.44l-.37-.22-3.67.96.98-3.58-.24-.37A9.93 9.93 0 012 12c0-5.52 4.48-10 10-10s10 4.48 10 10-4.48 10-10 10zm5.2-7.6c-.28-.14-1.65-.81-1.9-.9-.25-.09-.43-.14-.61.14-.18.28-.7.9-.86 1.08-.16.18-.32.2-.6.07-.28-.14-1.18-.44-2.25-1.4-.83-.74-1.39-1.65-1.55-1.93-.16-.28-.02-.43.12-.57.13-.13.28-.34.42-.51.14-.17.18-.29.28-.48.09-.19.05-.36-.02-.5-.07-.14-.61-1.47-.84-2.01-.22-.54-.45-.47-.61-.48-.16-.01-.35-.01-.54-.01-.19 0-.5.07-.77.36-.27.29-1.04 1.02-1.04 2.49 0 1.47 1.07 2.89 1.22 3.09.15.2 2.11 3.23 5.13 4.53.72.31 1.28.5 1.72.64.72.23 1.38.2 1.9.12.58-.09 1.65-.67 1.89-1.32.23-.65.23-1.21.16-1.32-.07-.11-.25-.18-.53-.32z"/>
              </svg>
              ¿Necesitas personalizar tu archivo VIP?
            </div>
            <div className="text-sm mb-2">
              Si eres VIP y quieres que personalice algun archivo para ti, contáctame directamente por WhatsApp.
            </div>
            <a
              href="https://wa.me/593999562442?text=Hola%20Steven%2C%20quiero%20personalizar%20mi%20archivo%20VIP%20en%20SysteCode"
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-2 bg-green-500 hover:bg-green-600 text-white font-semibold px-4 py-2 rounded-lg shadow transition mt-1"
            >
              <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                <path d="M20.52 3.48A11.94 11.94 0 0012 0C5.37 0 0 5.37 0 12c0 2.12.55 4.13 1.61 5.93L0 24l6.18-1.62A11.94 11.94 0 0012 24c6.63 0 12-5.37 12-12 0-3.19-1.24-6.19-3.48-8.52zM12 22c-1.85 0-3.63-.5-5.18-1.44l-.37-.22-3.67.96.98-3.58-.24-.37A9.93 9.93 0 012 12c0-5.52 4.48-10 10-10s10 4.48 10 10-4.48 10-10 10zm5.2-7.6c-.28-.14-1.65-.81-1.9-.9-.25-.09-.43-.14-.61.14-.18.28-.7.9-.86 1.08-.16.18-.32.2-.6.07-.28-.14-1.18-.44-2.25-1.4-.83-.74-1.39-1.65-1.55-1.93-.16-.28-.02-.43.12-.57.13-.13.28-.34.42-.51.14-.17.18-.29.28-.48.09-.19.05-.36-.02-.5-.07-.14-.61-1.47-.84-2.01-.22-.54-.45-.47-.61-.48-.16-.01-.35-.01-.54-.01-.19 0-.5.07-.77.36-.27.29-1.04 1.02-1.04 2.49 0 1.47 1.07 2.89 1.22 3.09.15.2 2.11 3.23 5.13 4.53.72.31 1.28.5 1.72.64.72.23 1.38.2 1.9.12.58-.09 1.65-.67 1.89-1.32.23-.65.23-1.21.16-1.32-.07-.11-.25-.18-.53-.32z"/>
              </svg>
              Contactar por WhatsApp
            </a>
            <div className="text-xs text-green-700 mt-2">
              Número: <b>0999562442</b> (Steven Baque)
            </div>
          </div>
        </div>
      )}
      <div className="w-full max-w-5xl">
        <FileList
          files={vipFiles}
          loading={loading}
          title="Lista de todos los archivos VIP"
          onEdit={handleFileEdited}
          currentUser={user}
          purchasedIds={isUserVip ? vipFiles.map(f => f.id) : purchasedIds}
        />
      </div>
    </div>
  );
};

export default VipPage;



