import React, { useEffect } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import DashboardNavbar from './components/DashboardNavbar';
import LandingPage from './routes/landingpage';
import Home from './routes/home';
import FileListPage from './routes/FileListPage';
import FileDetailPage from './routes/FileDetailPage';
import UsersPage from './routes/UsersPage';
import PerfilPage from './routes/PerfilPage';
import VipPage from './routes/VipPage';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

function App() {
  useEffect(() => {
    const interval = setInterval(() => {
      fetch(`${API_URL}/users/ping`, {
        method: 'POST',
        credentials: 'include',
      }).catch(() => {}); // evita warning si falla
    }, 15000); // cada 15s

    return () => clearInterval(interval);
  }, [API_URL]);

  return (
    <BrowserRouter>
      {/* Bloque de anuncios Google AdSense global, fijo abajo y poco intrusivo */}
      <div style={{
        position: 'fixed',
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: 50,
        background: 'transparent',
        pointerEvents: 'none'
      }}>
        <div style={{
          maxWidth: 700,
          margin: '0 auto',
          pointerEvents: 'auto'
        }}>
          {/* Bloque de anuncios */}
          <div style={{ display: 'flex', justifyContent: 'center', minHeight: 60 }}>
            <ins className="adsbygoogle"
              style={{ display: 'block' }}
              data-ad-client="ca-pub-6887015448861752"
              data-ad-slot="1410980475"
              data-ad-format="auto"
              data-full-width-responsive="true"></ins>
          </div>
        </div>
      </div>
      {/* Script de AdSense solo una vez */}
      <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-6887015448861752" crossOrigin="anonymous"></script>
      <Routes>
        <Route path="/" element={<LandingPage />} />
        <Route
          path="/*"
          element={
            <DashboardNavbar>
              <Routes>
                <Route path="/home" element={<Home />} />
                <Route path="/archivos" element={<FileListPage />} />
                <Route path="/archivos/:filename" element={<FileDetailPage />} />
                <Route path="/usuarios" element={<UsersPage />} />
                <Route path="/perfil" element={<PerfilPage />} />
                <Route path="/vip" element={<VipPage />} />
                {/* Puedes agregar más rutas aquí */}
              </Routes>
            </DashboardNavbar>
          }
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
