// --- Panel interactivo de frases con botón corazón y fondo animado ---
function LovePanel() {
  // Frases originales
  const phrasesOriginal = [
    "Eres hermosa sin esfuerzo.",
    "Tienes una mirada que destaca.",
    "Tu sonrisa tiene algo especial.",
    "Inteligente y segura, así eres.",
    "Bonita por dentro y por fuera.",
    "Me encanta tu forma de pensar.",
    "Tienes estilo y mucha personalidad.",
    "Eres fuerte, pero también muy dulce.",
    "Brillas sin hacer ruido.",
    "Tu vibra es de las que se quedan.",
    "Eres real, y eso vale mucho.",
    "Encantas sin intentarlo.",
    "Naturalmente linda.",
    "Siempre de buen gusto.",
    "Sabes estar sin querer llamar la atención.",
    "Eres increíble.",
    "BAÑATE"
  ];
  const [phrases, setPhrases] = React.useState([...phrasesOriginal]);
  const panelRef = React.useRef();

  // Animación de fondo degradado
  React.useEffect(() => {
    if (!panelRef.current) return;
    panelRef.current.animate(
      [
        { backgroundPosition: "0% 50%" },
        { backgroundPosition: "100% 50%" },
        { backgroundPosition: "0% 50%" }
      ],
      {
        duration: 20000,
        iterations: Infinity,
        easing: "ease-in-out"
      }
    );
  }, []);

  // Manejar click en el botón corazón
  const handleLoveClick = (e) => {
    e.stopPropagation();
    let arr = [...phrases];
    if (arr.length === 0) arr = [...phrasesOriginal];
    const idx = Math.floor(Math.random() * arr.length);
    const phraseText = arr.splice(idx, 1)[0];
    setPhrases(arr);

    // Crear el span de la frase
    const phraseEl = document.createElement('span');
    phraseEl.className = 'phrase';
    phraseEl.textContent = phraseText;

    // Posición inicial: centro del botón
    const btn = e.currentTarget;
    const btnRect = btn.getBoundingClientRect();
    const panelRect = panelRef.current.getBoundingClientRect();
    // Coordenadas relativas al panel
    const left = btnRect.left - panelRect.left + btnRect.width / 2;
    const top = btnRect.top - panelRect.top + btnRect.height / 2;
    phraseEl.style.left = `${left}px`;
    phraseEl.style.top = `${top}px`;
    phraseEl.style.transform = 'translate(-50%, -50%)';

    // Direcciones posibles
    const directions = [
      { x: 0, y: -120 },
      { x: -80, y: -100 },
      { x: 80, y: -100 },
      { x: -120, y: 0 },
      { x: 120, y: 0 },
      { x: -80, y: -40 },
      { x: 80, y: -40 },
      { x: 0, y: -150 }
    ];
    const dir = directions[Math.floor(Math.random() * directions.length)];
    const duration = 7000;

    // Animación con Web Animations API
    phraseEl.animate([
      { transform: 'translate(-50%, -50%) translate(0, 0)', opacity: 1 },
      { transform: `translate(-50%, -50%) translate(${dir.x}px, ${dir.y}px) scale(1.2)`, opacity: 0 }
    ], {
      duration,
      easing: 'ease-out',
      fill: 'forwards'
    });

    panelRef.current.appendChild(phraseEl);
    setTimeout(() => {
      phraseEl.remove();
    }, duration);
  };

  // Estilos en línea para fondo y animación
  return (
    <section
      ref={panelRef}
      className="relative flex flex-col items-center justify-center py-20 px-4 md:px-0 overflow-hidden"
      style={{
        minHeight: 340,
        background: "linear-gradient(270deg, #ff9a9e, #fad0c4, #fad0c4, #ff9a9e)",
        backgroundSize: "800% 800%",
        borderRadius: 32,
        margin: "32px 0",
        position: "relative",
        boxShadow: "0 8px 32px rgba(255,105,180,0.13), 0 1.5px 8px #fbc2eb",
        animation: "gradientAnimation 20s ease infinite"
      }}
    >
      <style>{`
        @keyframes gradientAnimation {
          0% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
          100% { background-position: 0% 50%; }
        }
        .phrase {
          position: absolute;
          z-index: 20;
          color: #fff;
          background: linear-gradient(135deg, #ff6a9e, #ff3d7f);
          padding: 6px 14px;
          border-radius: 20px;
          font-weight: bold;
          white-space: nowrap;
          user-select: none;
          pointer-events: none;
          box-shadow: 0 4px 12px rgba(255, 61, 127, 0.8);
          text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
          opacity: 1;
          animation-fill-mode: forwards;
          animation-timing-function: ease-out;
        }
      `}</style>
      <button
        id="loveBtn"
        aria-label="Botón corazón"
        onClick={handleLoveClick}
        className="relative z-10 text-6xl md:text-7xl bg-pink-400 hover:bg-pink-500 border-none rounded-full w-32 h-32 md:w-36 md:h-36 flex items-center justify-center shadow-lg transition-all duration-200 cursor-pointer select-none"
        style={{
          color: "white",
          boxShadow: "0 6px 15px rgba(255, 105, 180, 0.5)",
          fontSize: "4rem",
          outline: "none",
          border: "none",
          userSelect: "none"
        }}
      >
        ❤️
      </button>
      <div className="mt-8 text-white text-lg font-semibold drop-shadow-lg text-center select-none">
        Haz clic en el corazón para ver frases especiales ✨
      </div>
    </section>
  );
}

