// fetchWithAuth para renovar el token si expira
async function fetchWithAuth(url, options = {}) {
  let token = localStorage.getItem('jwt');
  const refreshToken = localStorage.getItem('refreshToken'); // refreshtoken en localStorage

  const fetchWithToken = async (tokenToUse) => {
    return fetch(url, {
      ...options,
      headers: {
        ...(options.headers || {}),
        Authorization: `Bearer ${tokenToUse}`,
        'Content-Type': 'application/json',
      },
      // no uses credentials si no usas cookies
    });
  };

  let response = await fetchWithToken(token);

  if (response.status === 401 && refreshToken) {
    // Intenta renovar access token enviando el refresh token en body
    const refreshRes = await fetch(`${process.env.REACT_APP_API_URL}/auth/refresh`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ refreshToken }),
    });

    if (refreshRes.ok) {
      const data = await refreshRes.json();
      token = data.accessToken;
      localStorage.setItem('jwt', token);

      // Reintenta la petición original con nuevo token
      response = await fetchWithToken(token);
    } else {
      // Si falla renovación, limpia tokens y redirige login
      localStorage.removeItem('jwt');
      localStorage.removeItem('refreshToken');
      window.location.href = '/login';
    }
  } else if (response.status === 401) {
    // Sin refresh token o acceso no autorizado
    localStorage.removeItem('jwt');
    localStorage.removeItem('refreshToken');
    window.location.href = '/login';
  }

  return response;
}

