import React, { useRef } from 'react';
import { motion } from 'framer-motion';

const SearchBar = ({ searchTerm, setSearchTerm, tipo, setTipo, categoria, setCategoria }) => {
  const nameRef = useRef();

  return (
    <motion.form
      initial={{ opacity: 0, y: -10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.4 }}
      className="mb-4 max-w-2xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-4 bg-white rounded-xl shadow-md p-4 border border-gray-100"
      onSubmit={(e) => e.preventDefault()}
    >
      {/* Buscar por nombre */}
      <div className="relative">
        <label
          className="block text-sm font-semibold text-gray-700 mb-1"
          htmlFor="searchName"
        >
          Nombre del archivo
        </label>
        <span className="absolute left-3 top-1/2 transform -translate-y-1/2 text-purple-400">
          <svg
            className="w-5 h-5"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            viewBox="0 0 24 24"
          >
            <circle cx="11" cy="11" r="8" />
            <line x1="21" y1="21" x2="16.65" y2="16.65" />
          </svg>
        </span>
        <input
          ref={nameRef}
          id="searchName"
          type="text"
          placeholder="Buscar por nombre de archivo..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full pl-10 pr-10 border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500 transition shadow bg-gradient-to-r from-blue-50 via-white to-purple-50"
          autoComplete="off"
        />
        {searchTerm && (
          <button
            type="button"
            className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-red-500"
            onClick={() => {
              setSearchTerm('');
              nameRef.current.focus();
            }}
            aria-label="Limpiar búsqueda"
          >
            <svg
              className="w-5 h-5"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              viewBox="0 0 24 24"
            >
              <line x1="6" y1="6" x2="18" y2="18" />
              <line x1="6" y1="18" x2="18" y2="6" />
            </svg>
          </button>
        )}
      </div>

      {/* Filtro por tipo */}
      <div className="relative">
        <label className="block text-sm font-semibold text-gray-700 mb-1" htmlFor="searchTipo">
          Tipo de HTML
        </label>
        <select
          id="searchTipo"
          value={tipo}
          onChange={e => setTipo(e.target.value)}
          className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-400 transition bg-white"
        >
          <option value="">Todos</option>
          <option value="carta">Carta</option>
          <option value="flores">Flores</option>
          <option value="decorado">Decorado</option>
          <option value="dedicatoria">Dedicatoria</option>
          <option value="globos">Globos</option>
          <option value="otro">Otro</option>
        </select>
      </div>

      {/* Filtro por categoría */}
      <div className="relative">
        <label className="block text-sm font-semibold text-gray-700 mb-1" htmlFor="searchCategoria">
          Categoría
        </label>
        <select
          id="searchCategoria"
          value={categoria}
          onChange={e => setCategoria(e.target.value)}
          className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400 transition bg-white"
        >
          <option value="">Todas</option>
          <option value="amor">Amor</option>
          <option value="amistad">Amistad</option>
          <option value="romance">Romance</option>
          <option value="otro">Otro</option>
        </select>
      </div>
    </motion.form>
  );
};

export default SearchBar;
