import React, { useEffect, useState, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';

import PayPalButton from '../components/PayPalButton';
import { AnimatePresence } from 'framer-motion';
import BannerAd from '../components/BannerAd';
import FileCard from '../components/FileCard';

const FileDetailPage = () => {
  // Banner Adsterra: cargar script externo y mostrar responsivo
  useEffect(() => {
    const bannerDiv = document.getElementById('adsterra-banner');
    if (!bannerDiv) return;
    bannerDiv.innerHTML = '';
    window.atOptions = {
      'key' : '59100bc8ee7a09132f72ceeec85b6cf2',
      'format' : 'iframe',
      'height' : 250,
      'width' : 300,
      'params' : {}
    };
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.src = '//felllilyforegoing.com/59100bc8ee7a09132f72ceeec85b6cf2/invoke.js';
    script.id = 'adsterra-script';
    bannerDiv.appendChild(script);
    return () => {
      bannerDiv.innerHTML = '';
      Array.from(document.querySelectorAll('#adsterra-script')).forEach(s => s.remove());
    };
  }, []);
  const { filename } = useParams();
  const navigate = useNavigate();
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(true);
  const [currentUser, setCurrentUser] = useState(null);
  const [likes, setLikes] = useState(0);
  const [downloads, setDownloads] = useState(0);
  const [liked, setLiked] = useState(false);
  const [copied, setCopied] = useState(false);
  const [hasPurchased, setHasPurchased] = useState(false);
  const [showVipModal, setShowVipModal] = useState(false);
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [adModalOpen, setAdModalOpen] = useState(false);
  const [adContinueEnabled, setAdContinueEnabled] = useState(false);
  const [adAction, setAdAction] = useState(null);
  const [adCountdown, setAdCountdown] = useState(5);
  const [showCopyToast, setShowCopyToast] = useState(false);

  const API_URL = process.env.REACT_APP_API_URL;

  // fetchWithAuth para renovar el token si expira
  const fetchWithAuth = useCallback(async (url, options = {}) => {
    let token = localStorage.getItem('jwt');
    let refreshToken = localStorage.getItem('refreshToken');

    const fetchWithToken = async (tokenToUse) => {
      return fetch(url, {
        ...options,
        headers: {
          ...(options.headers || {}),
          Authorization: `Bearer ${tokenToUse}`,
          'Content-Type': 'application/json',
        },
      });
    };

    let response = await fetchWithToken(token);

    if (response.status === 401 && refreshToken) {
      const refreshRes = await fetch(`${API_URL}/auth/refresh`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-refresh-token': refreshToken,
        },
        body: JSON.stringify({ refreshToken }),
      });

      if (refreshRes.ok) {
        const data = await refreshRes.json();
        token = data.accessToken;
        localStorage.setItem('jwt', token);
        response = await fetchWithToken(token);
      } else {
        localStorage.removeItem('jwt');
        localStorage.removeItem('refreshToken');
        window.location.href = '/login';
      }
    } else if (response.status === 401) {
      localStorage.removeItem('jwt');
      localStorage.removeItem('refreshToken');
      window.location.href = '/login';
    }

    return response;
  }, [API_URL]);

  // Cargar datos del archivo y usuario
  useEffect(() => {
    const loadFileData = async () => {
      try {
        // Buscar archivo por filename
        const response = await fetchWithAuth(`${API_URL}/files?search=${encodeURIComponent(filename)}`);
        const data = await response.json();
        
        if (data.files && data.files.length > 0) {
          const foundFile = data.files.find(f => f.filename === filename);
          if (foundFile) {
            setFile(foundFile);
            setLikes(Number(foundFile.likes) || 0);
            setDownloads(Number(foundFile.downloads) || 0);
          } else {
            // Si no se encuentra por filename exacto, usar el primero
            setFile(data.files[0]);
            setLikes(Number(data.files[0].likes) || 0);
            setDownloads(Number(data.files[0].downloads) || 0);
          }
        }
      } catch (error) {
        console.error('Error loading file:', error);
        navigate('/archivos'); // Redirigir si no se encuentra
      } finally {
        setLoading(false);
      }
    };

    // Cargar usuario actual
    const loadUser = async () => {
      const jwt = localStorage.getItem('jwt');
      if (jwt) {
        try {
          const response = await fetchWithAuth(`${API_URL}/auth/me`);
          const data = await response.json();
          setCurrentUser(data.user || null);
        } catch (error) {
          setCurrentUser(null);
        }
      }
    };

    loadFileData();
    loadUser();
    // Scroll arriba al cargar archivo (móvil y desktop)
    setTimeout(() => {
      window.scrollTo({ top: 0, behavior: 'auto' });
    }, 100);
  }, [filename, API_URL, navigate, fetchWithAuth]);

  // Verificar si el usuario ya compró el archivo VIP
  useEffect(() => {
    if (file && file.id && currentUser && String(file.epago).toLowerCase() === 'vip') {
      fetchWithAuth(`${API_URL}/files/${file.id}/purchased`)
        .then(res => res.json())
        .then(data => setHasPurchased(!!data.purchased))
        .catch(() => setHasPurchased(false));
    }
  }, [file, currentUser, API_URL, fetchWithAuth]);

  // Efectos para el modal de anuncios
  useEffect(() => {
    if (adModalOpen) {
      const timer = setInterval(() => {
        setAdCountdown(prev => {
          if (prev <= 1) {
            setAdContinueEnabled(true);
            clearInterval(timer);
            return 0;
          }
          return prev - 1;
        });
      }, 1000);

      return () => clearInterval(timer);
    }
  }, [adModalOpen]);

  // Función para validar fecha
  const isValidDate = (dateStr) => {
    const date = new Date(dateStr);
    return date instanceof Date && !isNaN(date.getTime());
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-purple-500"></div>
      </div>
    );
  }

  if (!file) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-700 mb-4">Archivo no encontrado</h2>
          <button
            onClick={() => navigate('/archivos')}
            className="bg-purple-500 text-white px-6 py-2 rounded-lg hover:bg-purple-600 transition"
          >
            Volver a archivos
          </button>
        </div>
      </div>
    );
  }

  // Variables del archivo
  const isVip = String(file.epago).toLowerCase() === "vip";
  const isFreeVipUser = currentUser && String(currentUser.modalidad).toLowerCase() === 'vip';
  const isOwner = currentUser && file.user_id && String(file.user_id) === String(currentUser?.id);
  const hasJwt = Boolean(localStorage.getItem('jwt'));
  const shouldShowAdModal = false;
  
  const publicUrl = file.publicUrl || file.file_url || '';
  const fileData = file.file_data || '';
  let downloadUrl = '';
  // Lógica robusta para evitar [object Object] en la URL
  if (typeof fileData === 'string' && fileData.includes('/')) {
    // fileData es tipo "2025/07/uuid.html"
    const [year, month, ...rest] = fileData.split('/');
    const filename = rest.join('/');
    downloadUrl = `${API_URL}/files/download/${encodeURIComponent(year)}/${encodeURIComponent(month)}/${encodeURIComponent(filename)}`;
  } else {
    downloadUrl = '';
  }

  const previewImageUrl = file.previewImageUrl || file.preview_image_url || file.image_url || file.image || null;
  const previewVideoUrl = file.previewVideoUrl || file.preview_video_url || file.video_url || file.video || null;

  // Funciones de manejo
  const handleDownload = async () => {
    if (isVip && !isFreeVipUser && !hasPurchased && !isOwner) {
      setShowVipModal(true);
      return;
    }

    if (shouldShowAdModal) {
      showAdModal(() => downloadFile());
      return;
    }

    downloadFile();
  };

  // WhatsApp y TikTok
  const whatsappUrl = "https://wa.me/593999562442?text=Hola%20Steven%2C%20quiero%20personalizar%20mi%20archivo%20en%20SysteCode";

  const downloadFile = async () => {
    try {
      const jwt = localStorage.getItem('jwt');
      const headers = jwt ? { Authorization: `Bearer ${jwt}` } : {};
      
      const response = await fetch(downloadUrl, { headers });
      if (!response.ok) throw new Error('Download failed');
      
      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = file.filename || 'archivo.html';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
      
      setDownloads(prev => prev + 1);
    } catch (error) {
      console.error('Error downloading file:', error);
    }
  };

  const handleView = () => {
    if (isVip && !isFreeVipUser && !hasPurchased && !isOwner) {
      setShowVipModal(true);
      return;
    }

    window.open(publicUrl, '_blank');
  };

  const handleLike = async () => {
    if (!hasJwt) return;

    try {
      const response = await fetchWithAuth(`${API_URL}/files/${file.id}/like`, {
        method: 'POST',
      });

      if (response.ok) {
        const data = await response.json();
        setLikes(data.likes);
        setLiked(data.liked);
      }
    } catch (error) {
      console.error('Error liking file:', error);
    }
  };

  const handleCopyLink = (url) => {
    if (
      isVip &&
      !isFreeVipUser &&
      !hasPurchased &&
      !isOwner
    ) {
      setShowVipModal(true);
      return;
    }
    navigator.clipboard.writeText(url).then(() => {
      setCopied(true);
      setShowCopyToast(true);
      setTimeout(() => {
        setCopied(false);
        setShowCopyToast(false);
      }, 2000);
    });
  };

  const showAdModal = (action) => {
    setAdAction(() => action);
    setAdModalOpen(true);
    setAdContinueEnabled(false);
    setAdCountdown(5);
  };

  const handleAdContinue = () => {
    setAdModalOpen(false);
    if (adAction) {
      adAction();
      setAdAction(null);
    }
  };

  const VIP_EMAILS = [
    "pastranagonzalezbrayanstiven@gmail.com",
    "baqueperez89@gmail.com"
  ];

  return (
  <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Breadcrumb */}
      <nav className="flex mb-6" aria-label="Breadcrumb">
        <button
          onClick={() => navigate('/archivos')}
          className="text-purple-600 hover:text-purple-800 transition text-sm sm:text-base"
        >
          ← Volver a archivos
        </button>
      </nav>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 lg:gap-8">
        {/* Columna Izquierda - Banner Adsterra y datos */}
        <div className="space-y-4 sm:space-y-6 order-2 lg:order-1">
          <div>
            <div className="flex flex-col sm:flex-row sm:items-center gap-2 mb-2">
              <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 break-words">{file.filename}</h1>
              {isVip && (
                <span className="bg-yellow-100 text-yellow-800 text-xs font-medium px-2.5 py-0.5 rounded-full self-start">
                  VIP
                </span>
              )}
            </div>
            <p className="text-gray-600 text-base sm:text-lg break-words">{file.descripcion || 'Sin descripción'}</p>
          </div>
          {/* Banner Adsterra: abajo de la información */}
          <div
            id="adsterra-banner"
            className="w-full flex justify-center items-center mt-4 lg:mt-6"
            style={{ minHeight: 250, maxWidth: 300, margin: '0 auto' }}
          />
          {/* FileCard de prueba solo en desarrollo */}
          {process.env.NODE_ENV === 'development' && (
            <div className="mt-4">
              <h3 className="text-sm text-gray-500 mb-2">Prueba rápida (solo desarrollo):</h3>
              <FileCard file={{
                id: 'test',
                filename: 'Archivo de Prueba',
                descripcion: 'Este es un archivo de prueba para desarrollo.',
                tipo: 'HTML',
                categoria: 'Test',
                likes: 99,
                downloads: 42,
                user_name: 'Dev Tester',
                user_email: 'dev@test.com',
                epago: 'gratuito',
                created_at: new Date().toISOString(),
                user_photo: '',
                publicUrl: '#',
                file_data: '',
              }} currentUser={currentUser} />
            </div>
          )}

          <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
            <div className="bg-gray-50 p-3 sm:p-4 rounded-lg">
              <h3 className="font-semibold text-gray-700 text-sm sm:text-base">Tipo</h3>
              <p className="text-gray-600 text-sm sm:text-base break-words">{file.tipo || 'No especificado'}</p>
            </div>
            <div className="bg-gray-50 p-3 sm:p-4 rounded-lg">
              <h3 className="font-semibold text-gray-700 text-sm sm:text-base">Categoría</h3>
              <p className="text-gray-600 text-sm sm:text-base break-words">{file.categoria || 'No especificada'}</p>
            </div>
          </div>

          <div className="flex flex-col items-center justify-center gap-2 mb-2">
            <div className="flex flex-row items-center justify-center gap-12">
              <div className="text-center">
                <div className="text-2xl sm:text-3xl font-bold text-purple-600">{likes}</div>
                <div className="text-xs sm:text-sm text-gray-500">Gustos</div>
              </div>
              <div className="text-center">
                <div className="text-2xl sm:text-3xl font-bold text-blue-600">{downloads}</div>
                <div className="text-xs sm:text-sm text-gray-500">Descargas</div>
              </div>
            </div>
          </div>

          {/* Solo muestra la fecha si es válida */}
          {isValidDate(file.created_at || file.fecha_subida) && (
            <div className="text-center mb-4">
              <div className="text-lg sm:text-2xl font-bold text-green-600">
                {new Date(file.created_at || file.fecha_subida).toLocaleDateString()}
              </div>
              <div className="text-xs sm:text-sm text-gray-500">Subido</div>
            </div>
          )}

          <div className="bg-gray-50 p-3 sm:p-4 rounded-lg">
            <h3 className="font-semibold text-gray-700 mb-2 text-sm sm:text-base">Autor</h3>
            <div className="flex items-center gap-3">
              {/* Avatar con fallback a SVG local si la imagen falla */}
              <img
                src={file.user_photo || ''}
                alt="Avatar"
                className="w-8 h-8 sm:w-10 sm:h-10 rounded-full flex-shrink-0"
                onError={e => {
                  e.target.onerror = null;
                  e.target.style.display = 'none';
                  const svg = document.createElement('span');
                  svg.innerHTML = `
                    <svg width="40" height="40" viewBox="0 0 40 40" fill="none">
                      <circle cx="20" cy="20" r="20" fill="#E5E7EB"/>
                      <path d="M20 21c3.314 0 6-2.686 6-6s-2.686-6-6-6-6 2.686-6 6 2.686 6 6 6zm0 2c-4.418 0-8 2.239-8 5v2h16v-2c0-2.761-3.582-5-8-5z" fill="#9CA3AF"/>
                    </svg>
                  `;
                  e.target.parentNode.insertBefore(svg, e.target.nextSibling);
                }}
              />
              <div className="min-w-0 flex-1">
                <p className="font-medium text-gray-900 text-sm sm:text-base truncate">{file.user_name || 'Usuario'}</p>
                <p className="text-xs sm:text-sm text-gray-500 truncate">{file.user_email || ''}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Columna Derecha - Preview y botones */}
        <div className="space-y-4 sm:space-y-6 order-1 lg:order-2">
          {/* Preview */}
          <div className="bg-white border border-gray-200 rounded-lg overflow-hidden shadow-sm">
            <div className="aspect-video bg-gray-100 flex items-center justify-center">
              {previewVideoUrl ? (
                <video
                  src={previewVideoUrl}
                  controls
                  className="w-full h-full object-cover"
                  poster={previewImageUrl}
                />
              ) : previewImageUrl ? (
                <img
                  src={previewImageUrl}
                  alt="Preview"
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="text-center text-gray-500 p-4">
                  <svg className="w-12 h-12 sm:w-16 sm:h-16 mx-auto mb-2" fill="currentColor" viewBox="0 0 20 20">
                    <path d="M4 3a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V5a2 2 0 00-2-2H4zm12 12H4l4-8 3 6 2-4 3 6z" />
                  </svg>
                  <p className="text-sm sm:text-base">Sin vista previa</p>
                </div>
              )}
            </div>
          </div>

          {/* Botones de acción */}
          <div className="space-y-3">
            {/* Botón Ver */}
            <button
              onClick={handleView}
              className="w-full bg-gradient-to-r from-purple-500 to-pink-500 text-white py-2.5 sm:py-3 px-4 sm:px-6 rounded-lg font-semibold hover:from-purple-600 hover:to-pink-600 transition flex items-center justify-center gap-2 text-sm sm:text-base"
            >
              <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
              </svg>
              Ver archivo
            </button>

            {/* Botón Descargar */}
            <button
              onClick={handleDownload}
              className="w-full bg-gradient-to-r from-blue-500 to-cyan-500 text-white py-2.5 sm:py-3 px-4 sm:px-6 rounded-lg font-semibold hover:from-blue-600 hover:to-cyan-600 transition flex items-center justify-center gap-2 text-sm sm:text-base"
            >
              <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
              </svg>
              Descargar
            </button>

            {/* Botones secundarios y Eliminar */}
            <div className="grid grid-cols-2 gap-2 sm:gap-3">
              {/* Like */}
              <button
                onClick={handleLike}
                disabled={!hasJwt}
                className={`py-2 px-3 sm:px-4 rounded-lg font-medium transition flex items-center justify-center gap-1 sm:gap-2 text-xs sm:text-sm border
                  ${liked
                    ? 'bg-red-100 text-red-600 border-red-400 hover:bg-red-200'
                    : 'bg-gray-100 text-gray-600 border-gray-300 hover:bg-gray-200'
                  }
                  ${!hasJwt ? 'opacity-50 cursor-not-allowed' : ''}`}
              >
                <svg className="w-3 h-3 sm:w-4 sm:h-4" fill={liked ? "currentColor" : "none"} stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                </svg>
                <span className="hidden sm:inline">{likes}</span>
                <span className="sm:hidden">{likes > 999 ? '999+' : likes}</span>
              </button>

              {/* Copiar enlace */}
              <button
                onClick={() => handleCopyLink(publicUrl)}
                className="py-2 px-3 sm:px-4 bg-gray-100 text-gray-600 rounded-lg font-medium hover:bg-gray-200 transition flex items-center justify-center gap-1 sm:gap-2 text-xs sm:text-sm"
              >
                <svg className="w-3 h-3 sm:w-4 sm:h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
                <span className="hidden sm:inline">{copied ? 'Copiado!' : 'Copiar enlace'}</span>
                <span className="sm:hidden">{copied ? '✓' : 'Copiar enlace'}</span>
              </button>
            </div>
            {/* Botón Eliminar solo para dueño o admin */}
            {(isOwner || (currentUser && String(currentUser.rol).toLowerCase() === 'admin')) && (
              <button
                onClick={async () => {
                  if (!window.confirm('¿Seguro que deseas eliminar este archivo? Esta acción no se puede deshacer.')) return;
                  try {
                    const jwt = localStorage.getItem('jwt');
                    const response = await fetch(`${API_URL}/files/delete/${file.id}`, {
                      method: 'DELETE',
                      headers: { Authorization: `Bearer ${jwt}` },
                    });
                    if (response.ok) {
                      alert('Archivo eliminado correctamente');
                      navigate('/archivos');
                    } else {
                      alert('Error al eliminar el archivo');
                    }
                  } catch (err) {
                    alert('Error al eliminar el archivo');
                  }
                }}
                className="w-full mt-2 bg-red-500 text-white py-2.5 sm:py-3 px-4 sm:px-6 rounded-lg font-semibold hover:bg-red-600 transition flex items-center justify-center gap-2 text-sm sm:text-base"
              >
                <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
                Eliminar archivo
              </button>
            )}
            {/* Toast de copiado */}
            {showCopyToast && (
              <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 bg-green-500 text-white px-4 py-2 rounded-lg shadow-lg z-[9999] animate-fade-in">
                ¡Enlace copiado!
              </div>
            )}
            {/* Botón TikTok fijo */}
            <div className="mt-4 flex flex-col items-center">
              <span className="text-sm font-semibold text-pink-600 mb-2 text-center">¡Ayúdame a crecer! Sígueme en TikTok:</span>
              <a
                href="https://www.tiktok.com/@systecode"
                target="_blank"
                rel="noopener noreferrer"
                className="bg-black text-white px-4 py-2 rounded-lg font-bold flex items-center gap-2 text-base shadow hover:bg-gray-800 transition"
                style={{ textDecoration: 'none' }}
              >
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 48 48" fill="none">
                  <rect width="48" height="48" rx="12" fill="#FEE2E2"/>
                  <path d="M32.5 14.5c-1.1 0-2-.9-2-2V12h-4v16c0 2.2-1.8 4-4 4s-4-1.8-4-4 1.8-4 4-4c.7 0 1.3.2 1.9.5V20.5c-2.6-.3-5.1 1.7-5.4 4.3-.3 2.6 1.7 5.1 4.3 5.4 2.6.3 5.1-1.7 5.4-4.3V16.5c0-.6.4-1 1-1s1 .4 1 1v2c0 1.1.9 2 2 2h2v-4h-2z" fill="#EC4899"/>
                </svg>
                TikTok @systecode
              </a>
            </div>
            {/* Apartado de personalización */}
            <div className="mt-6 p-4 bg-yellow-50 border border-yellow-200 rounded-lg flex flex-col items-center">
              <span className="text-base font-semibold text-yellow-700 mb-2 text-center">¿Desea editar o personalizar su archivo preferido a su gusto?</span>
              <span className="text-sm text-gray-700 mb-2 text-center">Contáctame <span className="font-bold">(Ojo tiene costo)</span>:</span>
              <a
                href={whatsappUrl}
                target="_blank"
                rel="noopener noreferrer"
                className="bg-green-500 hover:bg-green-600 text-white font-bold px-4 py-2 rounded-lg flex items-center gap-2 text-base shadow transition"
                style={{ textDecoration: 'none' }}
              >
                <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" viewBox="0 0 32 32" fill="none">
                  <rect width="32" height="32" rx="8" fill="#ECFDF5"/>
                  <path d="M16 6C10.477 6 6 10.477 6 16c0 2.137.672 4.13 1.83 5.81L6 26l4.19-1.83A9.96 9.96 0 0016 26c5.523 0 10-4.477 10-10S21.523 6 16 6zm0 18c-1.85 0-3.63-.5-5.18-1.44l-.37-.22-2.47.96.66-2.41-.24-.37A7.96 7.96 0 018 16c0-4.418 3.582-8 8-8s8 3.582 8 8-3.582 8-8 8zm4.13-5.47c-.23-.12-1.36-.67-1.57-.75-.21-.08-.36-.12-.51.12-.15.23-.58.75-.71.91-.13.15-.26.17-.49.06-.23-.12-.97-.36-1.85-1.13-.68-.6-1.14-1.34-1.27-1.57-.13-.23-.01-.35.11-.47.11-.11.23-.29.34-.43.11-.15.15-.25.23-.41.08-.16.04-.31-.02-.43-.06-.12-.51-1.23-.7-1.68-.18-.44-.37-.38-.51-.39-.13-.01-.28-.01-.43-.01-.15 0-.39.06-.6.28-.21.22-.8.78-.8 1.9s.82 2.21.93 2.37c.11.15 1.62 2.48 3.94 3.38.55.19.98.3 1.32.39.55.14 1.05.12 1.45.07.44-.05 1.36-.56 1.55-1.1.19-.54.19-1 .13-1.1-.06-.1-.21-.16-.44-.28z" fill="#10B981"/>
                </svg>
                WhatsApp: +593 0999562442
              </a>
            </div>
          </div>

          {/* Banner Publicitario Responsivo */}
          <div className="mt-6 flex flex-col sm:flex-row justify-center items-center gap-4">
            <div className="w-full sm:w-[320px] min-h-[50px] bg-gray-50 border border-gray-200 rounded-lg overflow-hidden flex items-center justify-center">
              <BannerAd />
            </div>
          </div>
        </div>
      </div>

      {/* Modales con mejor responsividad */}
      <AnimatePresence>
        {/* Modal VIP */}
        {showVipModal && (
          <div className="fixed inset-0 z-[999] flex items-center justify-center bg-black bg-opacity-60 p-4">
            <div className="bg-white rounded-2xl shadow-2xl border p-4 sm:p-8 w-full max-w-lg relative flex flex-col items-center">
              <button
                className="absolute top-3 right-3 text-gray-400 hover:text-yellow-600 text-lg font-bold rounded-full w-7 h-7 flex items-center justify-center focus:outline-none transition"
                onClick={() => setShowVipModal(false)}
                aria-label="Cerrar"
                type="button"
              >
                ×
              </button>
              <h2 className="text-3xl font-bold text-yellow-500 mb-2 text-center">Archivo VIP</h2>
              <p className="text-gray-700 text-center mb-4 text-lg">
                Este archivo es <span className="font-bold text-yellow-600">VIP</span>.<br />
                Para acceder, realiza el pago seguro con tarjeta o PayPal.<br />
                <span className="text-yellow-700 font-bold text-lg">Precio: $2 USD</span>
              </p>
              <div className="w-full flex justify-center my-4">
                <PayPalButton
                  amount={2.00}
                  description={file.filename || 'Archivo HTML VIP'}
                  receiverEmail={
                    VIP_EMAILS.includes(file.user_email || file.owner_email || file.email)
                      ? "baqueperez89@gmail.com"
                      : file.user_email || file.owner_email || file.email
                  }
                  fileId={file.id}
                  userId={currentUser?.id}
                  clientId={process.env.REACT_APP_PAYPAL_CLIENT_ID}
                  onSuccess={() => {
                    setHasPurchased(true);
                    setShowVipModal(false);
                    setShowSuccessModal(true);
                  }}
                />
              </div>
              <div className="mt-4 text-xs text-gray-400 text-center">
                Pago seguro con todas las tarjetas y PayPal.<br />
                Recibirás acceso inmediato tras el pago.
              </div>
            </div>
          </div>
        )}

        {/* Modal de éxito */}
        {showSuccessModal && (
          <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-black bg-opacity-50 p-4">
            <div className="bg-white rounded-2xl shadow-2xl border p-4 sm:p-8 w-full max-w-sm relative flex flex-col items-center">
              <div className="mb-4 flex items-center justify-center">
                <svg className="w-20 h-20 text-green-500 animate-bounce" viewBox="0 0 64 64" fill="none">
                  <circle cx="32" cy="32" r="30" fill="#d1fae5" />
                  <path d="M18 34l10 10 18-18" stroke="#10b981" strokeWidth="5" fill="none" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
              </div>
              <h2 className="text-2xl font-bold text-green-600 mb-2 text-center">¡Compra exitosa!</h2>
              <p className="text-gray-700 text-center mb-4 text-lg">
                Tu archivo VIP ya está disponible para descargar y ver siempre que lo desees.<br />
                <span className="font-semibold text-green-700">Gracias por tu compra en SysteCode.</span>
              </p>
              <button
                className="mt-4 px-6 py-2 rounded bg-green-600 text-white font-semibold hover:bg-green-700 transition"
                onClick={() => setShowSuccessModal(false)}
              >
                Cerrar
              </button>
            </div>
          </div>
        )}

      </AnimatePresence>
    </div>
  );
};

export default FileDetailPage;

// Banner publicitario como componente
