// DashboardNavbar.jsx
import React, { useState, useEffect, useRef } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import OnlineUsers from './OnlineUsers';
import GlobalChatBubble from './GlobalChatBubble';

const ICONO = process.env.PUBLIC_URL + '/iconoImagen.ico';

// Iconos SVG para cada sección
const icons = {
  dashboard: (
    <svg className="w-5 h-5 text-gray-500" fill="currentColor" viewBox="0 0 22 21">
      <path d="M16.975 11H10V4.025a1 1 0 0 0-1.066-.998 8.5 8.5 0 1 0 9.039 9.039.999.999 0 0 0-1-1.066h.002Z"/>
      <path d="M12.5 0c-.157 0-.311.01-.565.027A1 1 0 0 0 11 1.02V10h8.975a1 1 0 0 0 1-.935c.013-.188.028-.374.028-.565A8.51 8.51 0 0 0 12.5 0Z"/>
    </svg>
  ),
  archivos: (
    <svg className="w-5 h-5 text-gray-500" fill="currentColor" viewBox="0 0 18 18">
      <path d="M6.143 0H1.857A1.857 1.857 0 0 0 0 1.857v4.286C0 7.169.831 8 1.857 8h4.286A1.857 1.857 0 0 0 8 6.143V1.857A1.857 1.857 0 0 0 6.143 0Zm10 0h-4.286A1.857 1.857 0 0 0 10 1.857v4.286C10 7.169 10.831 8 11.857 8h4.286A1.857 1.857 0 0 0 18 6.143V1.857A1.857 1.857 0 0 0 16.143 0Zm-10 10H1.857A1.857 1.857 0 0 0 0 11.857v4.286C0 17.169.831 18 1.857 18h4.286A1.857 1.857 0 0 0 8 16.143v-4.286A1.857 1.857 0 0 0 6.143 10Zm10 0h-4.286A1.857 1.857 0 0 0 10 11.857v4.286c0 1.026.831 1.857 1.857 1.857h4.286A1.857 1.857 0 0 0 18 16.143v-4.286A1.857 1.857 0 0 0 16.143 10Z"/>
    </svg>
  ),
  perfil: (
    <svg className="w-5 h-5 text-gray-500" fill="currentColor" viewBox="0 0 20 20">
      <path d="M14 2a3.963 3.963 0 0 0-1.4.267 6.439 6.439 0 0 1-1.331 6.638A4 4 0 1 0 14 2Zm1 9h-1.264A6.957 6.957 0 0 1 15 15v2a2.97 2.97 0 0 1-.184 1H19a1 1 0 0 0 1-1v-1a5.006 5.006 0 0 0-5-5ZM6.5 9a4.5 4.5 0 1 0 0-9 4.5 4.5 0 0 0 0 9ZM8 10H5a5.006 5.006 0 0 0-5 5v2a1 1 0 0 0 1 1h11a1 1 0 0 0 1-1v-2a5.006 5.006 0 0 0-5-5Z"/>
    </svg>
  ),
  vip: (
    <svg className="w-5 h-5 text-yellow-500" fill="currentColor" viewBox="0 0 24 24">
      <path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/>
    </svg>
  ),
  usuarios: (
    <svg className="w-5 h-5 text-gray-500" fill="currentColor" viewBox="0 0 20 18">
      <path d="M14 2a3.963 3.963 0 0 0-1.4.267 6.439 6.439 0 0 1-1.331 6.638A4 4 0 1 0 14 2Zm1 9h-1.264A6.957 6.957 0 0 1 15 15v2a2.97 2.97 0 0 1-.184 1H19a1 1 0 0 0 1-1v-1a5.006 5.006 0 0 0-5-5ZM6.5 9a4.5 4.5 0 1 0 0-9 4.5 4.5 0 0 0 0 9ZM8 10H5a5.006 5.006 0 0 0-5 5v2a1 1 0 0 0 1 1h11a1 1 0 0 0 1-1v-2a5.006 5.006 0 0 0-5-5Z"/>
    </svg>
  ),
  logout: (
    <svg className="w-5 h-5 text-gray-500" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 18 16">
      <path strokeLinecap="round" strokeLinejoin="round" d="M1 8h11m0 0L8 4m4 4-4 4m4-11h3a2 2 0 0 1 2 2v10a2 2 0 0 1-2 2h-3"/>
    </svg>
  ),
  login: (
    <svg className="w-5 h-5 text-gray-500" fill="currentColor" viewBox="0 0 18 20">
      <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.96 2.96 0 0 0 .13 5H5Z"/>
      <path d="M6.737 11.061a2.961 2.961 0 0 1 .81-1.515l6.117-6.116A4.839 4.839 0 0 1 16 2.141V2a1.97 1.97 0 0 0-1.933-2H7v5a2 2 0 0 1-2 2H0v11a1.969 1.969 0 0 0 1.933 2h12.134A1.97 1.97 0 0 0 16 18v-3.093l-1.546 1.546c-.413.413-.94.695-1.513.81l-3.4.679a2.947 2.947 0 0 1-1.85-.227 2.96 2.96 0 0 1-1.635-3.257l.681-3.397Z"/>
      <path d="M8.961 16a.93.93 0 0 0 .189-.019l3.4-.679a.961.961 0 0 0 .49-.263l6.118-6.117a2.884 2.884 0 0 0-4.079-4.078l-6.117 6.117a.96.96 0 0 0-.263.491l-.679 3.4A.961.961 0 0 0 8.961 16Zm7.477-9.8a.958.958 0 0 1 .68-.281.961.961 0 0 1 .682 1.644l-.315.315-1.36-1.36.313-.318Zm-5.911 5.911 4.236-4.236 1.359 1.359-4.236 4.237-1.7.339.341-1.699Z"/>
    </svg>
  ),
};

// Contenido informativo del sidebar (no botones)
const SidebarSummary = ({ user, stats, earnings }) => {
  // Detecta si el usuario es VIP
  const isUserVip = user && String(user.modalidad).toLowerCase() === 'vip';

  return (
    <div className="px-6 py-4 space-y-6">
      {/* Card usuario */}
      <div className="rounded-xl border border-gray-200 p-4 bg-white/60 backdrop-blur-sm">
        {user ? (
          <div className="flex items-center gap-3">
            <img src={user.photo} alt="perfil" className="w-10 h-10 rounded-full border" />
            <div className="leading-tight">
              <p className="font-semibold text-gray-900">{user.name}</p>
              <p className="text-xs text-gray-500 capitalize">{user.rol}</p>
            </div>
          </div>
        ) : (
          <p className="text-sm text-gray-600">No has iniciado sesión</p>
        )}
      </div>

      {/* Resumen */}
      <div className="rounded-xl border border-gray-200 p-4 bg-white/60">
        <p className="text-sm font-semibold text-gray-900 mb-3">Resumen</p>
        <ul className="space-y-2">
          <li className="flex items-center justify-between text-sm">
            <span className="text-gray-600">Archivos subidos</span>
            <span className="font-semibold text-gray-900">{stats.archivos}</span>
          </li>
          <li className="flex items-center justify-between text-sm">
            <span className="text-gray-600">Descargas</span>
            <span className="font-semibold text-gray-900">{stats.descargas}</span>
          </li>
          <li className="flex items-center justify-between text-sm">
            <span className="text-gray-600">Likes</span>
            <span className="font-semibold text-gray-900">{stats.likes}</span>
          </li>
          {/* Solo muestra ganancias si el usuario es VIP */}
          {isUserVip && (
            <li className="flex items-center justify-between text-sm">
              <span className="text-yellow-600 font-semibold">Ganancias</span>
              <span className="font-bold text-yellow-700">
                ${earnings && earnings > 0 ? earnings.toFixed(2) : '0.00'} USD
              </span>
            </li>
          )}
        </ul>
      </div>

      {/* Usuarios en línea centrado y responsivo */}
      <div className="flex justify-center items-center">
        <div className="w-full flex justify-center">
          <OnlineUsers API_URL={process.env.REACT_APP_API_URL} />
        </div>
      </div>
    </div>
  );
};

const DashboardNavbar = ({ children }) => {
  const [open, setOpen] = useState(false);
  const [user, setUser] = useState(null);
  const [stats, setStats] = useState({ archivos: 0, descargas: 0, likes: 0 });
  const [earnings, setEarnings] = useState(0);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [isDesktop, setIsDesktop] = useState(typeof window !== 'undefined' ? window.innerWidth >= 768 : true);

  const navRef = useRef();
  const navigate = useNavigate();
  const location = useLocation();
  const API_URL = process.env.REACT_APP_API_URL;

  // Detecta si es desktop
  useEffect(() => {
    const handleResize = () => {
      setIsDesktop(window.innerWidth >= 768);
    };
    window.addEventListener('resize', handleResize);
    handleResize();
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Drawer siempre abierto en desktop
  useEffect(() => {
    if (isDesktop) setDrawerOpen(true);
    else setDrawerOpen(false);
  }, [isDesktop]);

  // fetchWithAuth para renovar el token si expira
async function fetchWithAuth(url, options = {}) {
  let token = localStorage.getItem('jwt');
  let response = await fetch(url, {
    ...options,
    headers: {
      ...(options.headers || {}),
      Authorization: `Bearer ${token}`,
      'Content-Type': 'application/json',
    },
  });

  if (response.status === 401) {
    // Intentar refrescar token
    const refreshToken = localStorage.getItem('refreshToken');
    if (!refreshToken) {
      window.location.href = '/login';
      return;
    }

    const refreshRes = await fetch(`${process.env.REACT_APP_API_URL}/auth/refresh`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ refreshToken }),
    });

    if (refreshRes.ok) {
      const data = await refreshRes.json();
      token = data.accessToken;
      localStorage.setItem('jwt', token);

      // Reintentar la petición original con el nuevo token
      response = await fetch(url, {
        ...options,
        headers: {
          ...(options.headers || {}),
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });
    } else {
      // Refresh token inválido o expirado, redirigir a login
      localStorage.removeItem('jwt');
      localStorage.removeItem('refreshToken');
      window.location.href = '/login';
      return;
    }
  }

  return response;
}

  // Cargar usuario y estadísticas
  useEffect(() => {
    let interval;
    let lastStats = null;
    const fetchStats = async (u) => {
      try {
        // Trae todos los archivos subidos por el usuario
        const res = await fetchWithAuth(`${API_URL}/files?user=${encodeURIComponent(u.name)}&limit=1000`);
        const df = await res.json();
        const archivos = Array.isArray(df.files) ? df.files.length : 0;
        let descargas = 0;
        let likes = 0;
        if (Array.isArray(df.files)) {
          df.files.forEach(f => {
            // Suma los likes de cada archivo subido por el usuario
            likes += Number(f.likes) || 0;
            descargas += Number(f.downloads) || 0;
          });
        }
        // Solo actualiza si hay cambios reales
        const newStats = { archivos, descargas, likes };
        if (
          !lastStats ||
          lastStats.archivos !== archivos ||
          lastStats.descargas !== descargas ||
          lastStats.likes !== likes
        ) {
          setStats(newStats);
          lastStats = newStats;
        }
      } catch {
        setStats(s => ({ ...s, archivos: 0, descargas: 0, likes: 0 }));
      }
    };

    const fetchUserAndStats = () => {
      const jwt = localStorage.getItem('jwt');
      if (!API_URL) return;
      if (jwt) {
        fetchWithAuth(`${API_URL}/auth/me`, {
          headers: { Authorization: `Bearer ${jwt}` }
        })
          .then(res => res.ok ? res.json() : Promise.reject())
          .then(data => {
            const u = data?.user || null;
            setUser(u);
            if (u) fetchStats(u);
            else setStats({ archivos: 0, descargas: 0, likes: 0 });
          })
          .catch(() => {
            setUser(null);
            setStats({ archivos: 0, descargas: 0, likes: 0 });
          });
      } else {
        setUser(null);
        setStats({ archivos: 0, descargas: 0, likes: 0 });
      }
    };

    fetchUserAndStats();
    // Actualización en tiempo real cada 2s para reflejar likes/descargas instantáneos
    interval = setInterval(() => {
      if (user) fetchStats(user);
    }, 2000);

    return () => clearInterval(interval);
  }, [API_URL, user]);

  // Cerrar si clic fuera (solo móvil)
  useEffect(() => {
    if (!open || isDesktop) return;
    const handleClick = (e) => {
      if (navRef.current && !navRef.current.contains(e.target)) setOpen(false);
    };
    document.addEventListener('mousedown', handleClick);
    return () => document.removeEventListener('mousedown', handleClick);
  }, [open, isDesktop]);

  // Cerrar al cambiar ruta (solo móvil)
  useEffect(() => {
    if (!isDesktop) setOpen(false);
  }, [location.pathname, isDesktop]);

  // Calcular ganancias reales desde el backend
  useEffect(() => {
    if (!user) {
      setEarnings(0);
      return;
    }

    const isUserVip = String(user.modalidad).toLowerCase() === 'vip';
    if (!isUserVip) {
      setEarnings(0);
      return;
    }

    const fetchEarnings = async () => {
      try {
        const res = await fetchWithAuth(`${API_URL}/earnings`);
        if (res.ok) {
          const data = await res.json();
          setEarnings(Number(data.earnings) || 0);
        } else {
          setEarnings(0);
        }
      } catch {
        setEarnings(0);
      }
    };

    fetchEarnings();
    const interval = setInterval(fetchEarnings, 5000);
    return () => clearInterval(interval);
  }, [user, API_URL]);

  const handleLogout = () => {
    if (!API_URL) {
      localStorage.removeItem('jwt');
      setUser(null);
      navigate('/');
      return;
    }
    fetchWithAuth(`${API_URL}/auth/logout`, {
      credentials: 'include',
      method: 'GET',
      headers: { Accept: 'application/json' }
    }).finally(() => {
      localStorage.removeItem('jwt');
      setUser(null);
      navigate('/');
    });
  };

  const handleLogin = () => {
    if (API_URL) {
      // Redirige a Google Auth y luego a /archivos
      window.location.href = `${API_URL}/auth/google?redirect_uri=https://codigossyste.onrender.com/archivos`;
    }
  };

  // Fuerza modo claro SOLO en el sidebar del dashboard
  useEffect(() => {
    const sidebar = document.getElementById('drawer-navigation');
    if (sidebar) {
      sidebar.classList.remove('dark');
      sidebar.style.backgroundColor = '#fff';
      sidebar.style.color = '#222';
    }
  }, []);

  return (
    <div className="relative flex min-h-screen bg-white w-full overflow-x-hidden">
      {/* Botón hamburguesa solo en móvil y solo si el drawer está cerrado */}
      {!isDesktop && !drawerOpen && (
        <button
          className="fixed top-4 left-4 z-50 bg-white border border-gray-300 rounded-full shadow p-2 flex items-center justify-center"
          type="button"
          onClick={() => setDrawerOpen(true)}
          aria-label="Abrir menú"
        >
          {/* Icono hamburguesa */}
          <svg className="w-7 h-7 text-gray-800" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
            <path d="M4 6h16M4 12h16M4 18h16" />
          </svg>
        </button>
      )}

      {/* Drawer lateral: fijo en desktop, flotante en móvil */}
      <div
        id="drawer-navigation"
        className={`top-0 left-0 z-40 h-screen p-4 overflow-y-auto bg-white w-64 border-r border-gray-200 shadow-2xl
          ${isDesktop
            ? 'fixed transition-none translate-x-0'
            : `fixed transition-transform ${drawerOpen ? 'translate-x-0' : '-translate-x-full'}`}`}
        tabIndex={-1}
        aria-labelledby="drawer-navigation-label"
        style={{
          transition: isDesktop ? 'none' : 'transform 0.3s',
          backgroundColor: '#fff',
          color: '#222',
          overflowY: 'auto', // fuerza scroll solo si es necesario
        }}
      >
        {/* Encabezado con logo y texto, NO es botón */}
        <div
          className="flex items-center gap-2 mb-4 px-2 select-none"
          style={{ transition: 'box-shadow 0.2s' }}
        >
          <img src={ICONO} alt="SysteCode Logo" className="w-9 h-9 rounded-md shadow" />
          <span className="text-2xl font-bold tracking-wide text-purple-700">SysteCode</span>
        </div>
        <h5 id="drawer-navigation-label" className="text-base font-semibold text-gray-500 uppercase mb-4">
          Menu
        </h5>
        {/* Menú con animaciones de hover/click, sin scroll extra al pasar el mouse */}
        <div className="py-4">
          <ul className="space-y-2 font-medium">
            <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
              <Link
                to="/"
                className="flex items-center p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                onClick={() => setDrawerOpen(false)}
              >
                {icons.dashboard}
                <span className="ms-3">Inicio</span>
              </Link>
            </motion.li>
            <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
              <Link
                to="/archivos"
                className="flex items-center p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                onClick={() => setDrawerOpen(false)}
              >
                {icons.archivos}
                <span className="ms-3">Ver archivos</span>
              </Link>
            </motion.li>
            <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
              <Link
                to="/perfil"
                className="flex items-center p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                onClick={() => setDrawerOpen(false)}
              >
                {icons.perfil}
                <span className="ms-3">Mis archivos</span>
              </Link>
            </motion.li>
            <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
              <Link
                to="/vip"
                className="flex items-center p-2 text-yellow-700 rounded-lg transition-all duration-150 hover:bg-yellow-100 active:bg-yellow-200 group"
                onClick={() => setDrawerOpen(false)}
              >
                {icons.vip}
                <span className="ms-3">Archivos VIP</span>
              </Link>
            </motion.li>
            {user && user.rol === 'admin' && (
              <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
                <Link
                  to="/usuarios"
                  className="flex items-center p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                  onClick={() => setDrawerOpen(false)}
                >
                  {icons.usuarios}
                  <span className="ms-3">Ver usuarios</span>
                </Link>
              </motion.li>
            )}
            <motion.li whileHover={{ scale: 1.04 }} whileTap={{ scale: 0.97 }}>
              {user ? (
                <button
                  className="flex items-center w-full p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                  onClick={() => {
                    setDrawerOpen(false);
                    handleLogout();
                  }}
                >
                  {icons.logout}
                  <span className="ms-3">Cerrar sesión</span>
                </button>
              ) : (
                <button
                  className="flex items-center w-full p-2 text-gray-900 rounded-lg transition-all duration-150 hover:bg-purple-100 active:bg-purple-200 group"
                  onClick={() => {
                    setDrawerOpen(false);
                    handleLogin();
                  }}
                >
                  {icons.login}
                  <span className="ms-3">Iniciar sesión</span>
                </button>
              )}
            </motion.li>
          </ul>
          {/* Panel funcional original: perfil, administración, resumen, estadísticas, ganancias, usuarios en línea */}
          <div className="mt-6">
            <SidebarSummary user={user} stats={stats} earnings={earnings} />
          </div>
        </div>
      </div>

      {/* Overlay para cerrar el drawer al hacer clic fuera (solo móvil y solo si abierto) */}
      {!isDesktop && drawerOpen && (
        <div
          className="fixed inset-0 z-30 bg-black/30"
          onClick={() => setDrawerOpen(false)}
        />
      )}

      {/* Contenido principal con margen dinámico en desktop */}
      <main
        className="flex-1 min-h-screen transition-all duration-300 bg-white"
        style={{
          marginLeft: isDesktop ? 256 : 0, // ancho del drawer en desktop
          overflowX: 'hidden',
          width: '100vw',
          maxWidth: '100vw',
          position: 'relative',
          zIndex: 10,
        }}
      >
        <div className="max-w-6xl mx-auto px-4 md:px-6 py-8 w-full">
          <div className="mt-2 w-full">
            {children}
          </div>
        </div>
        {/* Créditos al pie */}
        <footer className="w-full bg-white border-t py-6 px-4 md:px-16 text-center text-sm text-gray-500 shadow-inner">
          <div className="flex flex-col md:flex-row items-center justify-center gap-2">
            <span className="font-bold text-purple-600 animate-pulse">SysteCode</span>
            <span>© {new Date().getFullYear()} Desarrollado por</span>
            <span className="font-semibold text-blue-600">Steven Baque</span>
            <svg className="w-5 h-5 text-blue-400 animate-bounce inline-block" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
              <path d="M12 19v-6m0 0V5m0 8l-4-4m4 4l4-4" />
            </svg>
            <a
              href="https://whatsapp.com/channel/0029Vb64QobFy728nFRgGD0W"
              target="_blank"
              rel="noopener noreferrer"
              className="ml-2 flex items-center gap-1 text-green-600 hover:underline"
              title="Grupo de WhatsApp"
            >
              <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                <path d="M20.52 3.48A11.94 11.94 0 0012 0C5.37 0 0 5.37 0 12c0 2.12.55 4.13 1.61 5.93L0 24l6.18-1.62A11.94 11.94 0 0012 24c6.63 0 12-5.37 12-12 0-3.19-1.24-6.19-3.48-8.52zM12 22c-1.85 0-3.63-.5-5.18-1.44l-.37-.22-3.67.96.98-3.58-.24-.37A9.93 9.93 0 012 12c0-5.52 4.48-10 10-10s10 4.48 10 10-4.48 10-10 10zm5.2-7.6c-.28-.14-1.65-.81-1.9-.9-.25-.09-.43-.14-.61.14-.18.28-.7.9-.86 1.08-.16.18-.32.2-.6.07-.28-.14-1.18-.44-2.25-1.4-.83-.74-1.39-1.65-1.55-1.93-.16-.28-.02-.43.12-.57.13-.13.28-.34.42-.51.14-.17.18-.29.28-.48.09-.19.05-.36-.02-.5-.07-.14-.61-1.47-.84-2.01-.22-.54-.45-.47-.61-.48-.16-.01-.35-.01-.54-.01-.19 0-.5.07-.77.36-.27.29-1.04 1.02-1.04 2.49 0 1.47 1.07 2.89 1.22 3.09.15.2 2.11 3.23 5.13 4.53.72.31 1.28.5 1.72.64.72.23 1.38.2 1.9.12.58-.09 1.65-.67 1.89-1.32.23-.65.23-1.21.16-1.32-.07-.11-.25-.18-.53-.32z"/>
              </svg>
              Canal de WhatsApp
            </a>
          </div>
        </footer>
      </main>
      {/* Chat global burbuja */}
      <GlobalChatBubble />
    </div>
  );
};

export default DashboardNavbar;
